<template>
  <div class="equipment">
    <head-top :title="$t('addPage.pairDevice')">
      <div class="back" slot="setLeft" @click="goBack">
        <img src="../../image/global/back.png" />
      </div>
    </head-top>
    <div class="equipment_container">
      <div class="column_List" v-if="pageType===4">
        <li v-for="(item, index) in equipmentData"  @click.stop.prevent="chooseItem(item, index)" :key="index">
          <div class="li_left">
            <!-- <div class="icon">
              <img src="../../image/equipment/equipmenticon.png">     
            </div> -->
            <div class="message">
              <!-- {{item.signal}} -->
              <div class="message_top" :style="{color:item.state===1?'#4fa2ff': ''}">{{item.name}}</div>
              <div class="message_mac"> MAC: {{item.mac}}</div>
              <div class="message_bottom">
                <span v-if="item.state===2">
                  <img src="../../image/equipment/Blueetoothicon.png">
                </span>
                <span v-if="item.state===1">
                  <img src="../../image/equipment/Blueetoothicon.png">
                </span>
                <span v-if="item.state===0">
                  <img src="../../image/equipment/Blueetoothlogofreeicon.png">
                </span>
                <span v-if="item.state===2" :style="{color:item.state===2?'#4fa2ff': ''}">{{$t('equimentpage.connecting')}}</span>                
                <span v-if="item.state===1" >{{$t('equimentpage.connect')}}</span>                
                <span v-if="item.state===0">{{$t('equimentpage.unconnect')}}</span>
              </div>
            </div>
          </div>
          <div class="li_right">
            <!-- 阻止默认行为，阻止checkbox选中 -->
            <!-- <input class="mui-switch mui-switch-anim time_swicth" v-model="item.state" type="checkbox"> -->
            <img src="../../image/equipment/ON@3x.png"  v-if="item.state !== 0" alt="">
            <img src="../../image/equipment/OFF@3x.png"  v-if="item.state ===0" alt="">
          </div>
        </li>
      </div>

      <div class="bluetooth" v-if="pageType===1">
        <div class="column_top">
          <span>{{$t('addPage.noInitiateBluetooth')}}</span>
          <em>{{$t('addPage.clickInitiateBluetooth')}}</em>
        </div>
        <div class="column_bottom">
          <div class="arrow">
            <img src="../../image/add/Resize.png">
          </div>
          <div class="icon">
            <img @click="turnOnBlueTooth" src="../../image/add/bluetoothicon.png">
          </div>
        </div>
      </div>
      
      <div class="bluetooth" v-if="pageType===2">
        <div class="column_top">
          <span>{{$t('addPage.scanningPlug')}}</span>
          <em>{{$t('addPage.scanningPlugRange')}}</em>
        </div>
        <div class="column_bottom">
          <div class="icon">
            <span>
              <div class="pulse"></div>
              <div class="pulse2"></div>
              <div class="pulse3"></div>
              <img src="../../image/add/bluetoothicon.png">
            </span>            
          </div>
        </div>
      </div>

      <div class="bluetooth" v-if="pageType===3">
        <div class="column_top">
          <span></span>
          <em>{{$t('addPage.findPlugFailed')}}</em>
        </div>
        <div class="column_bottom">
          <div class="noFind">
            <img src="../../image/add/pair.png">
          </div>
          <div class="button button_position" @click="retry">
            <span>{{$t('messageBox.Retry')}}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 刷新 -->
    <div class="themeButton bom_position refresh_button" @click="startotation" v-if="pageType===4" :style="{backgroundColor:disRefsh? '' : 'grey'}">
      <img src="../../image/equipment/refresh.png" alt="" :class="isRefresh?'refresh_animation': ''">
      <span>{{$t('equimentpage.refresh')}}</span>
    </div>
    <!-- 弹框 confirm pormpt -->
    <!-- 断开设备 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='equipmentOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.disconnetDevice')}}</div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 断开当前 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='cutEquipmentOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.disconnetCurrent')}}</div>
        <div class="alert_operation">
          <div class="determine_one"  @click="alertChoose(2)">{{$t('messageBox.confirm')}}</div>
        </div>
      </div>
    </messageBox>

  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import { addDeviceRequest, turnOnBlueToothRequest, equipmentSwitchRequest, closeSearchRequest, deviceReconnectRequest } from '../../service/method'
import { addDeviceResponse, equipmentSwitchResponse } from '../../service/getData'
import headTop from '../../components/header/header'
import toast from '../../components/common/toast'
import messageBox from '../../components/common/messageBox'

export default {
  data () {
    return {
      Timer: null, // 提示框显示计时器
      pageType: 2, // 1、蓝牙未开启 2、搜索 3、未找到 4、显示设备列表
      itemIndex: null, // 点击按钮的下标
      disRefsh: true // 连接过程禁止刷新
    }
  },

  mounted () {
    // 清空设备列表数据
    this.$store.state.equipmentData = []

    // 添加设备请求
    addDeviceRequest(this.NAVIGATOR, this)

    // 添加设备返回
    addDeviceResponse(this.NAVIGATOR, this, 'connect')

    // 连接、断开设备返回
    equipmentSwitchResponse(this.NAVIGATOR, this, 'connect')

    // 重连接口调用时，显示重连设备
    if (this.$route.params.connect) {
      let obj = this.$route.params.data
      window.addDeviceResponse(4, obj)
      this.$store.state.ToastType = 'addEquipment'
      this.disRefsh = false
      // 设备重连
      deviceReconnectRequest(this.NAVIGATOR, this, JSON.parse(obj).mac)
    }

    // 获取设备列表
    this.$root.getMockData('/equipment', function (res) {
      window.addDeviceResponse(4, JSON.stringify(res.data.data))
    })
    // this.$root.getMockData('/equipment', function (res) {
    //   window.addDeviceResponse(4, JSON.stringify(res.data.data))
    // })
    // this.$root.getMockData('/equipment', function (res) {
    //   window.addDeviceResponse(4, JSON.stringify(res.data.data))
    // })

    // setTimeout(() => {
    //   window.equipmentSwitchResponse(this.currentDevice.mac, 1, true)
    // }, 3000)
  },

  components: {
    headTop,
    toast,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBox', 'messageBoxType', 'ToastType', 'currentDevice', 'equipmentData'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    goBack () {
      // 正在连接状态返回
      if (this.$store.state.ToastType === 'addEquipment') {
        // 断开连接
        equipmentSwitchRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, 0)
        this.$store.state.ToastType = null
      } else {
        // if (this.currentDevice.state) {
          // self.$router.push({
          //   path: '/add/state'
          // })
        // }
        this.$router.go(-1)
        // 结束搜索
        closeSearchRequest(this.NAVIGATOR, this)
      }
    },

    sort (array) {
      if (array.length <= 1) {
        return array
      }
      array.sort((obj1, obj2) => {
        let val1 = obj1.signal
        let val2 = obj2.signal
        if (val1 > val2) {
          return 1
        } else if (val1 < val2) {
          return -1
        } else {
          return 0
        }
      })
      return array
    },

    turnOnBlueTooth () {
      // 开启蓝牙
      turnOnBlueToothRequest(this.NAVIGATOR, this)
    },

    retry () {
      // 添加设备请求
      addDeviceRequest(this.NAVIGATOR, this)
    },

    // 选择设备
    chooseItem (item, index) {
      if (this.ToastType !== 'addEquipment') {
        let flags = false
        this.$store.state.equipmentData.forEach((element, index) => {
          if (element.state && item !== element) {
            flags = true // 当前有已连接设备
          }
        })

        if (flags) {
          this.MESSAGEBOX_SWITCH({type: 'cutEquipmentOpreation'}) // 断开当前
        } else {
          if (item.state) {
            this.MESSAGEBOX_SWITCH({type: 'equipmentOpreation'}) // 断开设备
          } else {
            // this.$store.state.equipmentData[index].state = !this.$store.state.equipmentData[index].state

            this.$store.state.ToastType = 'addEquipment'
            this.disRefsh = false
            item.state = 2

            // 连接设备
            equipmentSwitchRequest(this.NAVIGATOR, this, item.mac, 1)
            this.$store.state.currentDevice = item

            // 测试
            if (this.$root.ISDEBUG) {
              setTimeout(() => {
                this.$store.state.ToastType = null
                item.state = 1
                this.$router.push({path: '/main/state'})
                window.equipmentSwitchResponse(this.currentDevice.mac, 1, 1)
              }, 2000)
            }
          }
        }
        this.itemIndex = index
      }
    },

    // 弹框
    alertChoose (num) {
      if (num === 0) {
        this.MESSAGEBOX_SWITCH({type: 'equipmentOpreation'})
      } else if (num === 1) {
        // 断开设备
        equipmentSwitchRequest(this.NAVIGATOR, this, this.$store.state.equipmentData[this.itemIndex].mac, 0)

        // 测试
        if (this.$root.ISDEBUG) {
          this.$store.state.equipmentData[this.itemIndex].state = 0
        }

        this.MESSAGEBOX_SWITCH({type: 'equipmentOpreation'})
        // 断开设备
        this.$store.state.ToastType = 'disconnectEquipment'
        this.disRefsh = false
      } else if (num === 2) {
        this.MESSAGEBOX_SWITCH({type: 'cutEquipmentOpreation'})
      }
    },
    startotation () { // 刷新设备列表
      if (this.ToastType === 'addEquipment' || this.ToastType === 'disconnectEquipment') {
        return false
      } else {
        this.isRefresh = true
        closeSearchRequest(this.NAVIGATOR, this)
        this.$store.state.equipmentData = []
        let timer = setTimeout(() => {
          addDeviceRequest(this.NAVIGATOR, this)
          clearInterval(timer)
          this.$root.getMockData('/equipment', function (res) {
            window.addDeviceResponse(4, JSON.stringify(res.data.data))
          })
        }, 300)
      }
    }
  },

  destroyed () {
    this.$store.state.ToastType = null
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

@keyframes pulse{
  from {
    transform: scale(1);
    opacity: 1.0;
  } to {
    transform: scale(2.5);
    opacity: 0.0;
  }
}

.equipment{
  .fs;
  z-index: 10;
  background-color: #FFF;
  color: #525252;
  .back{
    .wh(1.066667rem, 100%);
    margin-left: .2rem;
    img{
      height: .533333rem;
      vertical-align: middle;
    }
  }
  .equipment_container{
    .fs-noTop;
    .column_List{
      .wh(100%, 100%);
      position: relative;
      padding: .4rem .213333rem 0 .213333rem;
      overflow: auto;
      padding-bottom: 150px;
      li{
        .wh(100%, 2.4rem);
        border: 1px solid #D4E4E5;
        border-radius: .133333rem;
        overflow: hidden;
        position: relative;
        margin-bottom: .4rem;
        .li_left{
          .wh-f(83%, 100%);
          padding-left: 16px;
          // .icon{
          //   .wh-f(26%, 100%);
          //   position: relative;
          //   img{
          //     height: 1.333333rem;
          //     .f-center;
          //   }          
          // }
          .message{
            .wh-f(90%, 100%);
            font-size: 14px;
            text-align: left;
            padding-left: 5px;
            .message_top{
              .wh(100%, 30%);
              line-height: 1.28rem;
            }
            .message_mac{
              font-size: 10px;
              color: #cfcfcf;
              margin-top: 8px;
              .text-over;
            }
            .message_bottom{
              .wh(100%, 50%);
              line-height: 1rem;
              span:nth-child(1){
                img{
                  height: 18px;
                  position: relative;
                  top: 4px;
                  margin-right: 5px;
                }
              }
              span:nth-child(2){
                font-size: 13px;
              }
            }
          }
        }
        .li_right{
          .wh-f(17%, 100%);
          position: absolute;
          right: 0;
          .time_swicth{
            position: absolute;
            top: 50%;
            right: .3rem;
            transform: translateY(-50%);
          }
          // .input{
          //   .wh(20px, 10px);
          //   background: red;
          //   position: absolute;
          //   top: 50%;
          //   margin-top: -5px;
          //   margin-left: 10px;
          // }
          img{
            .wh(45px,22.5px);
            margin-top:33px;
          }
        }
      }
    }
    .bluetooth{
      .wh(100%, 100%);
      .column_top{
        .wh(100%, 30%);
        padding-top: 10%;
        span{
          .wh(100%, 30px);
          line-height: 34px;
          display: block;
          font-size: 18px;
          color: @themeColor;
        }
        em{
          .wh(100%, 30px);
          display: block;
          font-size: 12px;
          line-height: 28px;
          color: #808080;
        }
      }
      .column_bottom{
        .wh(100%, 70%);    
        padding-top: 4%;
        position: relative;
        .arrow{
          width: 100%;
          margin-bottom: 44px;
          img{
            .wh(30px, 30px);
            margin: 0 auto;
          }
        }
        .icon{
          width: 100%;
          position: absolute;
          top: 92px;
          left: 0;
          img{
           .wh(200px, 200px);
            margin: 0 auto;
          }
          span{
            .wh(200px, 178px);
            display: block;
            margin: 0 auto;
            position: relative;
            .pulse, .pulse2, .pulse3{
              position: absolute;
              left: 17px;
              top: 7px;
              z-index: -1;
              .wh(165px, 165px);
              border: 2px solid #C1E0FF;
              border-radius: 50%;
              animation: pulse 2s infinite;
            }
            .pulse2{
              animation: pulse 2s .35s infinite;
            }
            .pulse3{
              animation: pulse 2s 0.7s infinite;
            }
          }
        }
        .noFind{
          width: 100%;
          margin-top: -17%;
          img{
            height: 240px;
            margin: 0 auto;
          }
        }
        .button{
          .wh-l(6.666667rem, 1.066667rem);
          border-radius: 1.066667rem;
          position: absolute;
          left: 50%;
          bottom: 0;
          margin-left: -3.333333rem;
          background: url(../../image/timing/button.png) no-repeat;
          background-size: cover;
          box-shadow: 0 5px 10px #bbd8f5;
          span{
            .wh(40%, 100%);
            margin: 0 auto;
            display: block;
            font-size: .453333rem;
            color: #FFF;
          }
        }
        .button_position{
          position: absolute;
          bottom: 92px;
        }
      }
    }
  }
  .refresh_button{
    display: flex;
    justify-content: center;
    span{
      margin: 0 0 0 5px
    }
    img{
      .wh(20px, 20px);
      display: inline-block;
      margin-top: 10px;
    }
    .refresh_animation{
      animation: rotation  1s infinite
    }
    @keyframes rotation {
      0%{
        transform: rotate(0deg)
      }
      // 50%{
      //   transform: rotate(90deg)
      // }
      // 75%{
      //   transform: rotate(90deg)
      // }
      100%{
        transform: rotate(360deg)
      }
    }
  }
}

</style>



