/**
 * better-scroll滚动封装
 * @method pickerSave
 * @param  {object}      self             this对象，必选
 * @param  {array}       wheels           轮子数组，必选
 * @param  {array}       pickerData       轮子数组对应数据，必选
 */

export const pickerSave = (self) => {
  let array = []
  for (let i = 0; i < self.pickerData.length; i++) {
    let index = self.wheels[i].getSelectedIndex()
    // array.push(self.pickerData[i][index])
    let n = parseInt(self.wheels[i].items[index].innerHTML)
    array.push(n)
  }
  return array
}

/**
 * better-scroll滚动封装
 * @method pickerCancel
 * @param  {object}      self             this对象，必选
 * @param  {array}       wheels           轮子数组，必选
 * @param  {array}       pickerData       轮子数组对应数据，必选
 */

export const pickerCancel = (self) => {
  for (let i = 0; i < self.pickerData.length; i++) {
    self.wheels[i].disable()
  }
}

/**
 * 星期数组转化为10进制
 * @method getWeekday
 * @param  val        初始字符串
 */

export const getWeekday = (val, self) => {
  switch (val) {
    case self.$t('timerPage.weekends'):
      return 96
    case self.$t('timerPage.workingDay'):
      return 31
    case self.$t('timerPage.everyDay'):
      return 127
    default:
      let array = [0, 0, 0, 0, 0, 0, 0, 0]
      let newarr = val.split('、')
      console.log(newarr)
      let week = [self.$t('timerPage.sun'), self.$t('timerPage.sat'), self.$t('timerPage.fri'), self.$t('timerPage.thur'), self.$t('timerPage.wed'), self.$t('timerPage.tues'), self.$t('timerPage.mon')]
      for (var i in newarr) {
        if (week.indexOf(newarr[i]) !== -1) {
          array[week.indexOf(newarr[i]) + 1] = 1
        }
      }
      console.log(array)
      return parseInt(array.join(''), 2)
  }
}

/**
 * 十进制转星期
 * @method weekFormat
 * @param  val        初始字符串
 */
export const weekFormat = (str, self) => {
  console.log(str)
  let binary = (parseInt(str)).toString(2)
  console.log(binary)
  self.$store.state.weekarr = (binary.split('')).reverse()
  // console.log(arr)
  let week = [self.$t('timerPage.mon'), self.$t('timerPage.tues'), self.$t('timerPage.wed'), self.$t('timerPage.thur'), self.$t('timerPage.fri'), self.$t('timerPage.sat'), self.$t('timerPage.sun')]
  let newstr = ''
  console.log(self.$store.state.weekarr)
  try {
    for (var i = 0; i < self.$store.state.weekarr.length; i++) {
      console.log(i)
      console.log(self.$store.state.weekarr[i])
      if (parseInt(self.$store.state.weekarr[i]) === 1) {
        newstr += '、' + week[i]
      }
    }
    // self.$store.state.weekarr.forEach((element, index) => {
    //   if (element === 1) {
    //     console.log('111')
    //     newstr += '、' + week[index]
    //   }
    // })
  } catch (err) {
  }
  console.log(newstr)
  switch (newstr) {
    case '、' + self.$t('timerPage.mon') + '、' + self.$t('timerPage.tues') + '、' + self.$t('timerPage.wed') + '、' + self.$t('timerPage.thur') + '、' + self.$t('timerPage.fri'):
      newstr = '、' + self.$t('timerPage.workingDay')
      break
    case '、' + self.$t('timerPage.sat') + '、' + self.$t('timerPage.sun'):
      newstr = '、' + self.$t('timerPage.weekends')
      break
    case '、' + self.$t('timerPage.mon') + '、' + self.$t('timerPage.tues') + '、' + self.$t('timerPage.wed') + '、' + self.$t('timerPage.thur') + '、' + self.$t('timerPage.fri') + '、' + self.$t('timerPage.sat') + '、' + self.$t('timerPage.sun'):
      newstr = '、' + self.$t('timerPage.everyDay')
      break
    default:
      break
  }
  console.log(newstr)
  return newstr.slice(1)
}

/**
 * 星期格式化（十六进制转数组）
 * @method weekToArrayFormat
 * @param  {string}    val      初始字符串, 必选
 */

export const weekToArrayFormat = (val) => {
  val = parseInt(val, 10).toString(2)
  // console.log(val)
  let array = []
  let weekArray = []
  let index = val.indexOf('1')
  while (index > -1) {
    array.push(index)
    index = val.indexOf('1', index + 1)
  }
  weekArray = array.map(i => val.length - i)
  return weekArray
}

/**
 * 位数格式化
 * @method digitFormat
 * @param  {string}    val      初始字符串, 必选
 */

export const digitFormat = (val) => {
  if (val < 10) {
    val = '0' + val
  }
  return val
}

/**
 * 大小排序
 * @method sortFormat
 * @param  {array}      arr      数组, 必选
 */

export const sortFormat = (arr) => {
  for (var i = 0; i < arr.length - 1; i++) {
    for (var j = 0; j < arr.length - i - 1; j++) {
      if (arr[j] > arr[j + 1]) {
        var temp = arr[j]
        arr[j] = arr[j + 1]
        arr[j + 1] = temp
      }
    }
  }
  return arr
}

/**
 * 字符串时间转数字
 * @method timeToNumFormat
 * @param  {string}      val      字符串时间, 必选
 */

export const timeToNumFormat = (val) => {
  let arr = val.split(':')
  val = parseInt(arr[0] * 60) + parseInt(arr[1])
  return val
}

/**
 * 数字转字符串时间
 * @method numToTimeFormat
 * @param  {number}      val      数字, 必选
 */

export const numToTimeFormat = (val) => {
  let hour = digitFormat(parseInt(val / 60))
  let minute = digitFormat(val - parseInt(val / 60) * 60)
  return `${hour}:${minute}`
}

/**
 * 判断数组和对象
 * @method isType
 * @param  {All}      val      数字, 必选
 */

export const isType = (val) => {
  let type = Object.prototype.toString.call(val)
  if (type === '[object Array]') {
    return 'Array'
  } else if (type === '[object Object]') {
    return 'Object'
  } else {
    return 'param is no object type'
  }
}



