<template>
  <div class="main" :style="{marginTop: -offsetTop + 'px'}">
    <head-top :title="$t('homePage.renameDevice')">
      <!-- <div slot="setLeft" class="bluetooth" @click="navjump">
        <img src="../../image/global/back.png">
      </div> -->
    </head-top>
    <div class="content">
      <div class="content_top">
        <img src="../../image/adddirver/socket-pic.png">
        <span>{{$t('homePage.renameTips')}}</span>
      </div>
      <div class="content_bottom">
        <input type="text" v-on:input ="inputHander" v-model="deviceName" :placeholder="$t('homePage.renameTips')">
        <span>
          <b :class="{error: !validate.nameActive}">{{$t('homePage.renameTips_1')}}</b><br/>
          <b>{{$t('homePage.renameTips_2')}}</b><br/>          
          <b class="error" v-show="nameFlag">{{$t('homePage.renameTips_3')}}</b>
        </span>
      </div>
      <div class="nextbtn" :class="{disableBtn: !validate.nameActive}" @click="next" v-show="!keyboard">
        <span>{{$t('homePage.chooseNext')}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { reNameResponse } from '../../service/getData.js'
import { reNameRequest } from '../../service/method.js'
import headTop from '../../components/header/header'

export default {
  data () {
    return {
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      deviceName: '', // 设备名
      nameFlag: false // 是否重名
    }
  },

  mounted () {
    let h = 0
    this.fullHeight = document.documentElement.clientHeight
    window.onresize = () => {
      h = document.documentElement.clientHeight
      // 软键盘是否弹起
      if (h < this.fullHeight) {
        this.keyboard = true
      } else {
        this.keyboard = false
      }
      // 解决小屏幕遮挡输入框
      if (h < 370) {
        this.offsetTop = 370 - h
      } else {
        this.offsetTop = 0
      }
    }

    // 重命名
    reNameResponse(this.NAVIGATOR, this, 'rename')

    // 设备名字
    this.deviceName = this.$route.params.item.name
  },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'deviceListData'
    ]),

    validate () {
      return {
        nameActive: /^[\u4e00-\u9fa5a-zA-Z0-9]{2,16}$/.test(this.deviceName)
      }
    }
  },

  methods: {
    navjump () {
      this.$store.state.transitiondre = true
      this.$router.go(-1)
    },

    inputHander () {
      if (this.nameFlag) {
        this.nameFlag = false
        this.deviceListData.forEach(ele => {
          if (ele.name === this.deviceName) {
            this.nameFlag = true
          }
        })
      }
    },

    next () {
      // 验证
      if (this.validate.nameActive) {
        this.$store.state.transitiondre = false
        console.log(this.$route.params.item)

        this.nameFlag = false
        this.deviceListData.forEach(ele => {
          if (ele.name === this.deviceName && ele.mac !== this.$route.params.item.mac) {
            this.nameFlag = true
          }
        })

        // 没有重名，执行
        if (!this.nameFlag) {
          if (this.$root.ISDEBUG) {
            window.reNameResponse(this.$route.params.item.mac, true)
            // this.$router.push({path: '/home/adddirver/addfailed'})
            // this.$router.push({path: '/home/adddirver/addsuccess'})
          }
          reNameRequest(this.NAVIGATOR, this, this.$route.params.item.mac, this.deviceName)

          // 硬件有时回不了数据，暂时跳过
          this.$store.state.transitiondre = true
          this.$router.push({path: '/home/adddirver/addsuccess'})
        }
      }
    }
  },

  watch: {}
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.main{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .bluetooth{
    .wh(1.066667rem, 100%);
    margin-left: .2rem;
    img{
      height: .533333rem;
      vertical-align: middle;
    }
  }
  .content{
    .fs-noTop;
    padding: 0 30px;
    .content_top{
      .wh(100%, 190px);
      img{
        height: 115px;
        margin: 30px auto 0 auto;
      }
      span{
        .wh-l(100%, 40px);
        font-size: 12px;
        display: block;
        color: #888;
      }
    }
    .content_bottom{
      input{
        .wh(100%, 40px);
        padding-top: 8px;
        font-size: 15px;
        border-bottom: 1px solid @themeColor;
      }
      span{
        .wh(100%, 100px);
        margin-top: 12px;
        line-height: 30px;
        display: block;
        color: #cccccc;
        font-size: 12px;
        text-align: left;
        .error{
          color: #E04F61;
        }
      }
    }
    .disableBtn{
      background: #95C8FF !important;
    }
    .nextbtn{
      .wh(240px,40px);
      line-height: 36px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      position: fixed;
      left: 50%;
      bottom: 50px;
      margin-left: -120px;
      span{
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
      }
    }
  }
}

</style>



