<template>
  <div class="main">
    <head-top :title="$t('homePage.addDevices')">
      <div slot="setLeft" class="bluetooth" @click="navjump">
        <img src="../../image/global/back.png">
      </div>
    </head-top>
    <div class="scanning_content">
      <span>{{$t('homePage.failedToFind')}}</span>
      <img src="../../image/adddirver/link-pic.png">
      <div class="nextbtn" @click="next">
        <span>{{$t('homePage.Rerty')}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { } from 'vuex'
import headTop from '../../components/header/header'

export default {
  data () {
    return {}
  },

  mounted () {
    if (this.$root.ISDEBUG) {}
  },

  components: {
    headTop
  },

  computed: {},

  methods: {
    navjump () {
      this.$store.state.transitiondre = true
      this.$router.go(-1)
    },

    next () {
      this.$store.state.transitiondre = true
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';
@keyframes pulse{
  from {
    transform: scale(1);
    opacity: 1.0;
  } to {
    transform: scale(2.5);
    opacity: 0.0;
  }
}
.main{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .bluetooth{
    .wh(1.066667rem, 100%);
    margin-left: .2rem;
    img{
      height: .533333rem;
      vertical-align: middle;
    }
  }
  .scanning_content{
    .fs-noTop;
    span{
      .wh-l(100%, 30px);
      display: block;
      font-size: 12px;  
      margin: 62px 0 30px 0;
      color: #7F7F7F;
    }
    img{
      height: 200px;
      margin-top: 12px;
    }
    .nextbtn{
      .wh(240px,40px);
      line-height: 36px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      position: absolute;
      left: 50%;
      bottom: 70px;
      margin-left: -120px;
      span{
        .wh(100%, 100%);
        line-height: 40px;
        display: block;
        margin-top: 0;
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
      }
    }
  }
}

</style>



