<template>
  <div class="main">
    <head-top :title="$t('homePage.addfailed')">
      <div slot="setLeft" class="bluetooth" @click="navjump">
        {{$t('homePage.finished')}}
      </div>
    </head-top>
    <div class="content">
      <img src="../../image/adddirver/network-pic.png">
      <span>{{$t('homePage.addfailedTips')}}</span>
      <div class="nextbtn" @click="next">
        <span>{{$t('homePage.Rerty')}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../components/header/header'

export default {
  data () {
    return {}
  },

  mounted () {},

  components: {
    headTop
  },

  computed: {},

  methods: {
    navjump () {
      this.$store.state.transitiondre = true
      this.$router.go(-1)
    },

    next () {
      this.$store.state.transitiondre = true
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.main{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .bluetooth{
    .wh(1.066667rem, 100%);
    margin-left: .2rem;
    font-size: 16px;
  }
  .content{
    .fs-noTop;
    img{
      height: 100px;
      margin: 50px 0;
    }
    span{
      font-size: 12px;
      color: #575757;
      display: block;
    }
  }
  .nextbtn{
      .wh(240px,40px);
      line-height: 36px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -120px;
      span{
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
      }
    }
}

</style>



