<template>
  <div class="success">
    <div class="header">
      <div class="header-left">
        <div class="back" @click="back">
          <img src="../../image/global/back.png" alt="">
        </div>
      </div>
      <div class="header-center">
        Sent Email Successfully
      </div>
    </div>
    <div class="content">
        <div class="imgBox">
          <img src="../../image/global/send.png" alt="">
        </div>
        <div class="tip_1">
          <p>We have sent a confirmation email to <span class="emial">{{this.$route.query.email}}</span>, please click on the link in the email to complete the account activation.</p>
        </div>
        <div class="tip_2">
          <p>Not received? </p>
          <p>1.Pease check if it is classified as trash </p>
          <p><span class="send" @click="back">2.Resend mail</span></p>
        </div>
      </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapState([
      'messageBox', 'messageBoxType'
    ])
  },
  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),
    back () {
      this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../style/mixin.less";
.success{
  .fs;
  background: #ffffff;
  z-index: 999;
  .header{
    .wh-l(100%, 1.333333rem);
    background: @themeColor;
    font-size: .48rem;
    color: #F5FAFF;
    .header-left{
      .wh-f(10%, 100%);
      .back{
        .wh(1.066667rem, 100%);
        margin-left: .2rem;
        img{
          height: .533333rem;
          vertical-align: middle;
        }
      }
    }
    .header-center{
      .wh-f(90%, 100%);
      text-align: center;
    }
  }
  .content{
    .imgBox{
      padding: 50px 0;
    }
    .tip_1,.tip_2{
      text-align:left;
      padding: 10px;
      font-size: 16px;
      .emial{
        font-weight: 600;
      }
      .send{
        color: #0000FF;
      }
    }
  }
}
</style>




