<template>
  <div class="setTime">
    <head-top title="位置" goBack="true"></head-top>
    <div class="setTime_container"></div>
  </div>
</template>

<script>
import { } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../../components/header/header'

export default {
  data () {
    return {}
  },

  mounted () {},

  components: {
    headTop
  },

  computed: {},

  methods: {}
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../../style/mixin.less';

.setTime{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .setTime_container{
    .fs-noTop;
  }
}

</style>



