<template>
  <div id="app">
    <transition :name="transitionName" mode="in-out">
      <router-view></router-view>
    </transition>

    <!-- 返回测试按钮 -->
    <!-- <div class="phone_back" @click="back">←</div> -->
    <!-- 等待加载 -->

    <!-- 捕获错误 -->
    <messageBox setHeight="270" setType="confirm" v-show="messageBoxType==='errorOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe alert_error">{{errorMsg}}</div>
        <div class="alert_operation">
          <div class="determine_one"  @click="alertChoose(1)">OK</div>
        </div>
      </div>
    </messageBox>

    <!-- 吐司 -->
    <toast describe="网络已切换" v-if="ToastType==='networkSwitch'" position="bottom"></toast>
    <toast describe="连接失败，认证失败" v-if="ToastType==='authenticationError'" position="bottom"></toast>
    <toast describe="连接失败，密钥无效" v-if="ToastType==='secretKeyError'" position="bottom"></toast>
    <toast describe="连接失败，超时" v-if="ToastType==='connectFailTimeOutError'" position="bottom"></toast>
    <toast describe="连接失败，服务器异常" v-if="ToastType==='serverExceptionError'" position="bottom"></toast>
    <toast describe="发送失败，未连接" v-if="ToastType==='noConnectError'" position="bottom"></toast>
    <toast describe="发送失败，超时" v-if="ToastType==='overtimeError'" position="bottom"></toast>
    <toast describe="发送失败，网络异常" v-if="ToastType==='networkAnomalyError'" position="bottom"></toast>
    <toast describe="发送失败，未登录" v-if="ToastType==='notLoggedInError'" position="bottom"></toast>
    <toast describe="连接断开，账号在别处登录" v-if="ToastType==='beloggedInError'" position="bottom"></toast>
    <toast describe="连接断开，mqtt断开连接" v-if="ToastType==='mqttError'" position="bottom"></toast>
    <toast describe="连接断开，超时" v-if="ToastType==='connectTimeOutError'" position="bottom"></toast>
    <toast describe="连接断开，心跳超时" v-if="ToastType==='heartbeatTimeoutError'" position="bottom"></toast>
    <toast describe="连接断开，主动断开" v-if="ToastType==='toBreakOffError'" position="bottom"></toast>
    <toast describe="未知异常" v-if="ToastType==='sendOutError'" position="bottom"></toast>
    <toast describe="等待来自服务器的响应时超时" v-if="ToastType==='serverTimeoutError'" position="bottom"></toast>
    <toast describe="无法连接至服务器" v-if="ToastType==='unableToConnectServerError'" position="bottom"></toast>
    <toast describe="已断开连接" v-if="ToastType==='disconnectedError'" position="bottom"></toast>
    <toast describe="正在进行过多的发布" v-if="ToastType==='excessiveReleaseError'" position="bottom"></toast>

    <toast describe="设备已解绑" v-if="ToastType==='Unbind'" position="bottom"></toast>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { errorHandlerRequest, disableGoBackRequest } from './service/method'
import { goBackResponse, switchNetworkResponse, errorCodeResponse } from './service/getData'
import messageBox from './components/common/messageBox'
import toast from './components/common/toast'

export default {
  name: 'app',
  data () {
    return {}
  },

  mounted () {
    this.$store.state.currentRouter = this.$route.path

    // 禁用原生返回
    disableGoBackRequest(this.NAVIGATOR, this, true)

    // 返回操作
    goBackResponse(this.NAVIGATOR, this)
    // this.$i18n.locale = 'en'

    if (this.VERSION === 1) {
      this.$router.push('/add')
    } else if (this.VERSION === 2) {
      this.$router.push('/home')
      // 手机连接网络切换返回
      switchNetworkResponse(this.NAVIGATOR, this)

      // 后台返回错误码
      errorCodeResponse(this.NAVIGATOR, this)
    }
    // 禁用原生返回按键
    disableGoBackRequest(this.NAVIGATOR, this, true)
    // if (this.$root.ISDEBUG) {
    //   setTimeout(() => {
    //     console.log('error')
    //     window.errorCodeResponse('32103')
    //   }, 2000)
    // }
  },

  components: {
    messageBox,
    toast
  },

  computed: {
    ...mapState([
      'currentRouter', 'actionSheet', 'actionSheetType', 'messageBox', 'messageBoxType', 'ToastType', 'currentDevice', 'errorMsg', 'equipmentData'
    ])
  },

  methods: {
    ...mapMutations([
      'TABLE_SWITCH', 'MESSAGEBOX_SWITCH'
    ]),

    // 返回操作
    back () {
      window.goBackResponse()
      // 切换中英文测试
      // if (this.$i18n.locale === 'zh') {
      //   this.$i18n.locale = 'en'
      // } else {
      //   this.$i18n.locale = 'zh'
      // }
    },

    alertChoose (num) {
      this.MESSAGEBOX_SWITCH({ type: 'errorOpreation' })
    }
  },

  watch: {
    '$route' (to, from, next) {
      this.$store.state.currentRouter = to.path
      // let isBack = this.$router.isBack
      // if (isBack) {
      //   this.$store.state.transitionName = 'slide-right'
      // } else {
      //   this.$store.state.transitionName = 'slide-left'
      // }
      // console.log(this.transitionName)
      // this.$router.isBack = false
    },

    ToastType: function (currentValue, oldValue) {
      if (currentValue !== 'addEquipment' && currentValue !== null && currentValue !== 'disconnectEquipment') {
        clearTimeout(this.Timer)
        this.Timer = setTimeout(() => {
          this.$store.state.ToastType = null
        }, 2500)
      }
    },

    errorMsg: function (currentValue, oldValue) {
      this.MESSAGEBOX_SWITCH({ type: 'errorOpreation' })
      errorHandlerRequest(this.NAVIGATOR, this, currentValue)
    }
  }
}
</script>

<style lang="less">
@import './style/common.less';

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width: 100%;
  height: 100%;
}

.phone_back{
  position: absolute;
  top: 300px;
  z-index: 999;
  .wh-l(50px, 50px);
  font-size: 30px;
  background: @themeColor;
  color: #FFF;
}

.loading{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  color: #FFF;
  z-index: 9999;
}

.alert_error{
  overflow: hidden;
  height: 54% !important;
  width: 90% !important;
  margin: 0 auto;
  padding: 0 !important;
  position: relative;
  top: -8%;
  word-wrap: break-word;
  word-break: normal;
}

//enter开始  active结束
.slide-left-enter-active, .slide-left-leave-active{
  transition: all .3s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-left-leave-to{
  transform: translateX(100%);
  opacity: 0;
}
</style>



