<template>
  <div class="main">
    <head-top :title="deviceList.length!==0?$t('homePage.chooseDevices'):$t('homePage.addDevices')">
      <div slot="setLeft" class="bluetooth" @click="navjump">
        <img src="../../image/global/back.png">
      </div>
    </head-top>
    <div class="content" v-show="deviceList.length!==0">
      <div class="describe">{{$t('homePage.chooseTips')}}</div>
      <ul class="list">
        <li v-show="!item.isBinding" v-for="(item, index) in deviceList" @click="chooseItem(item, index)">
          <span :class="{selected: activeIndex === index}">{{item.name}}</span>
          <strong>{{item.mac}}</strong>
          <img v-show="activeIndex !== index" src="../../image/adddirver/default.png">
          <img v-show="activeIndex === index" src="../../image/adddirver/click.png">
        </li>
      </ul>
      <div class="nextbtn" @click="next" :class="{disableBtn: validate.itemActive}">
         <span v-show="!adding">{{$t('homePage.chooseNext')}}</span>
         <span v-show="adding">
           <img src="../../image/adddirver/Loadingcirclesfreeicon.png">
           {{$t('homePage.chooseAdding')}}
         </span>
       </div>
    </div>

    <div class="scanning_content" v-show="deviceList.length===0">
      <span>{{$t('homePage.scanningForPlug')}}</span>
      <div class="warpper">
        <div class="pulse"></div>
        <div class="pulse2"></div>
        <div class="pulse3"></div>
        <img src="../../image/adddirver/Group.png">
      </div>
    </div>
    <!-- 弹窗 -->
    <messageBox setHeight="220" setType="confirm" v-show="messageBoxType==='inputPasswordOpreation'">
    <div slot="confirmColumn" class="common_alert">
      <div class="alert_icon">
        <span class="password"></span>
      </div>
      <div class="alert_describe lang-fix-top input_warpper ">
        {{$t('homePage.encryptedTips')}}
        <ul class="lang-fix-top inputList">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <input type="text" v-model="password" maxlength="4" ref="inputValue">
        </ul>
      </div>
      <div class="alert_operation">
        <div class="cancle">
          <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
        </div>
        <div class="determine">
          <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
        </div>
      </div>
    </div>
  </messageBox>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { openDeviceScanningRequest, closeDeviceScanningRequest, addDeviceConnectedByRouterRequest } from '../../service/method.js'
import { deviceConnectedByRouterResponse, addDeviceConnectedByRouterResponse } from '../../service/getData.js'
import headTop from '../../components/header/header'
import messageBox from '../../components/common/messageBox'

export default {
  data () {
    return {
      timer: null, // 计时器
      deviceList: [],
      activeIndex: -1, // 选择序号
      deviceItem: null, // 选择项
      password: '', // 密码
      adding: false // 正在添加
    }
  },

  mounted () {
    // 开启路由器扫描设备
    openDeviceScanningRequest(this.NAVIGATOR, this)

    // 获取路由器连接的设备列表返回
    deviceConnectedByRouterResponse(this.NAVIGATOR, this)

    // 添加路由器连接的设备返回
    addDeviceConnectedByRouterResponse(this.NAVIGATOR, this)

    // 超时处理
    this.timer = setTimeout(() => {
      // 关闭路由器扫描设备
      closeDeviceScanningRequest(this.NAVIGATOR, this)
      this.$store.state.transitiondre = false
      this.$router.push({path: '/home/adddirver/scanningfailed'})
      clearTimeout(this.timer)
    }, 30000)

    if (this.$root.ISDEBUG) {
      // window.deviceConnectedByRouterResponse(true, '[{"name":"卧室空调的插座","state":1,"mac":"00:00:00:00:01","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')

      // setInterval(() => {
      //   window.deviceConnectedByRouterResponse(true, '[{"name":"卧室空调的插座01","state":1,"mac":"00:00:00:00:01","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"卧室空调的插座02","state":1,"mac":"00:00:00:00:00","encrypted":false,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"卧室空调的插座03","state":1,"mac":"00:00:00:00:02","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      // }, 3000)
    }
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBoxType'
    ]),

    validate () {
      return {
        itemActive: this.deviceItem === null
      }
    }
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    navjump () {
      if (!this.adding) {
        // 关闭路由器扫描设备
        closeDeviceScanningRequest(this.NAVIGATOR, this)
        this.$store.state.transitiondre = false

        if (this.$store.state.findPage) {
          this.$router.go(-1)
        } else {
          this.$router.go(-2)
        }
      }
    },

    chooseItem (item, index) {
      if (index === this.activeIndex) {
        this.activeIndex = -1
        this.deviceItem = null
      } else {
        this.activeIndex = index
        this.deviceItem = item
      }
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          this.adding = true
          // 关闭路由器扫描设备
          closeDeviceScanningRequest(this.NAVIGATOR, this)
          // 添加路由器连接的设备
          addDeviceConnectedByRouterRequest(this.NAVIGATOR, this, this.deviceItem.mac, this.password)
          break
      }
    },

    next () {
      // 是否有选择项
      if (!this.validate.itemActive) {
        // 界面处理
        this.$store.state.transitiondre = false

        // 是否加密
        if (this.deviceItem.encrypted) {
          this.MESSAGEBOX_SWITCH({type: 'inputPasswordOpreation'})
        } else {
          this.adding = true
          // 关闭路由器扫描设备
          closeDeviceScanningRequest(this.NAVIGATOR, this)
          // 添加路由器连接的设备
          addDeviceConnectedByRouterRequest(this.NAVIGATOR, this, this.deviceItem.mac, '')
          if (this.$root.ISDEBUG) {
            setTimeout(() => {
              // this.$router.push({name: 'rename', params: {item: this.deviceItem}})
              window.addDeviceConnectedByRouterResponse(true)
            }, 2000)
          }
        }
      }
    },

    inputHandle () {
      if (this.password.length >= 4) {
        this.$refs.inputValue.blur()
        console.log(123)
      }
    }
  },

  destroyed () {
    clearTimeout(this.timer)
  },

  watch: {
    password: 'inputHandle'
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes pulse{
  from {
    transform: scale(1);
    opacity: 1.0;
  } to {
    transform: scale(2.5);
    opacity: 0.0;
  }
}
.main{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .bluetooth{
    .wh(1.066667rem, 100%);
    margin-left: .2rem;
    img{
      height: .533333rem;
      vertical-align: middle;
    }
  }
  .lang-fix-top{
    position: relative;
    top: -5px;
  }
  .input_warpper{
    height: 40%;
    font-size: 15px;
    .inputList{
      .wh(100%, 40px);
      position: relative;
      li{
        .wh(19%, 100%);
        margin: 0 3%;
        float: left;
        border-bottom: 2px solid #A5CFFF;
      }
      input{
        .wh(130%, 90%);
        .fs;
        background: transparent;
        padding: 14px 24px 0 24px;
        color: @themeColor;
        letter-spacing: 40px;
        font-size: 16px;
      }
    }
  }
  .content{
    .fs-noTop;
    padding: 0 15px;
    .describe{
      .wh-l(100%, 45px);
      font-size: 12px;
      text-align: left;
      color: #585858;
    }
    .list{
      .wh(100%, auto);
      li{
        .wh(100%, 70px);
        border: 1px solid #D4E4E5;
        border-radius: .133333rem;
        overflow: hidden;
        position: relative;
        margin-bottom: .4rem;
        text-align: left;
        font-size: 15px;
        padding-left: 10px;
        .selected{
          color: @themeColor;
        }
        span{
          .wh(70%, 50%);
          line-height: 38px;
          display: block;
        }
        strong{
          .wh(70%, 50%);
          line-height: 30px;
          display: block;
          color: #A2A2A2;
        }
        img{
          height: 15px;
          position: absolute;
          top: 50%;
          right: 10px;
          margin-top: -7.5px;
        }
      }
    }
    .disableBtn{
      background: #95C8FF !important;
    }
    .nextbtn{
      .wh(240px,40px);
      line-height: 36px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -120px;
      span{
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
        img{
          height: 17px;
          position: relative;
          top: 2px;
          left: -5px;
          transform-origin: center;
          animation: rotate 1s infinite;
        }
      }
    }
  }
  .scanning_content{
    .fs-noTop;
    span{
      .wh-l(100%, 30px);
      color: @themeColor;
      display: block;
      font-size: 18px;  
      margin: 35px 0;
    }
    .warpper{
      .wh(175px, 175px);
      margin: 40% auto 0 auto;
      position: relative;
      background: #D9EBFF;
      border-radius: 50%;
      position: relative;
      img{
        height: 85px;
        .f-center;
      }
      .pulse, .pulse2, .pulse3{
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        .wh(100%, 100%);
        border: 2px solid #C1E0FF;
        border-radius: 50%;
        animation: pulse 2s infinite;
      }
      .pulse2{
        animation: pulse 2s .35s infinite;
      }
      .pulse3{
        animation: pulse 2s 0.7s infinite;
      }
    }
  }
}

</style>



