<template>
  <div class="connectwifi">
       <head-top :title="$t('homePage.configureWiFi')">
          <div slot="setLeft" class="bluetooth" @click="navjump">
            <img src="../../image/global/back.png">
          </div>
       </head-top>
       <div class="circle_progress">
         <div class="progress_bar">
          <svg xmlns="http://www.w3.org/200/svg" height="150px" width="150px">
            <circle class="circle" cx="75px" cy="75px" :r="radius + 'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round" />
          </svg>
          <svg xmlns="http://www.w3.org/200/svg" height="150px" width="150px" class="circle_top">
            <circle class="circle" cx="75px" cy="75px" :r="radius + 'px'" fill="none" stroke="#4FA2FE" stroke-width="5px" :stroke-dasharray="strokeDasharray" :stroke-dashoffset="strokeDashoffset" stroke-linecap="round" />
          </svg>
          <div class="icon" :style="{transform: 'rotate('+ percentage/100*360 +'deg)'}">
            <img src="../../image/adddirver/icon.png">
          </div>
          <div class="number">
            <span>{{percentage}}%</span>
          </div>
         </div>
       </div>  
       <div class="prompt">
           <p ref="eltime">{{timeText}}</p>
           <p>{{$t('homePage.connecting')}}</p>
           <p>{{$t('homePage.routers')}}</p>
       </div>
       <!-- 退出配网 -->
       <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='exitConnect'">
        <div slot="confirmColumn" class="common_alert unconnectalert">
          <div class="alert_icon">
            <span></span>
          </div>
          <div class="alert_describe">{{$t('homePage.distributionNetwork')}}</div>
         <div class="alert_operation">
            <div class="cancle">
              <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
            </div>
            <div class="determine">
              <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
            </div>
          </div>
        </div>
      </messageBox>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
import { stopConnectionNetworkRequest } from '../../service/method.js'
import { connectionNetworkResponse } from '../../service/getData.js'
import headTop from '../../components/header/header'
import messageBox from '../../components/common/messageBox'
export default {
  data () {
    return {
      time: '03:00',
      timeText: `3 ${this.$t('homePage.minute')} 0 ${this.$t('homePage.second')}`,
      radius: 70, // svg进度条半径
      percentage: 0, // 百分比
      timer: null, // 定时器
      barTimer: null // 定时器
    }
  },
  created () {},

  mounted () {
    // 设备连接路由器网络返回
    connectionNetworkResponse(this.NAVIGATOR, this)

    if (this.$root.ISDEBUG) {
      setTimeout(() => {
        window.connectionNetworkResponse(true)
        // window.switchNetworkResponse('MOBILE', 1)
      }, 2000)
    }

    this.timedown()
    this.barTimedown()
  },

  destroyed () {
    clearInterval(this.timer) // 清除定时器
    clearInterval(this.barTimer) // 清除定时器
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBox', 'messageBoxType', 'transitiondre'
    ]),

    strokeDasharray () {
      return (Math.floor(2 * Math.PI * this.radius) + ',' + Math.floor(2 * Math.PI * this.radius))
    },

    strokeDashoffset () {
      return Math.floor(2 * Math.PI * this.radius) - Math.floor(2 * Math.PI * this.radius) / 100 * this.percentage
    }
  },

  methods: {
    ...mapMutations(['MESSAGEBOX_SWITCH']),
    timedown () {
      let m = parseInt(this.time.split(':')[0])
      let s = parseInt(this.time.split(':')[1])
      this.timer = setInterval(() => {
        if (--s < 0) {
          s = 59
          m--
        }
        // this.time = (m < 10 ? `0${m}` : m) + ':' + (s < 10 ? `0${s}` : s)
        this.timeText = `${m} ${this.$t('homePage.minute')} ${s} ${this.$t('homePage.second')}`
        if (m === 0 && s === 0) {
          clearInterval(this.timer)
          this.$router.push({path: '/home/adddirver/configfailed'})
          // 停止配置网络
          stopConnectionNetworkRequest(this.NAVIGATOR, this)
        }
      }, 1000)
    },

    barTimedown () {
      this.barTimer = setInterval(() => {
        if (++this.percentage === 100) {
          clearInterval(this.barTimer) // 清除定时器
        }
      }, 1000 * 180 / 100)
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: 'exitConnect'})
          break
        case 1:
          // 停止配置网络
          stopConnectionNetworkRequest(this.NAVIGATOR, this)

          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          this.$store.state.transitiondre = true
          this.$router.go(-1)
          break
        default:
          break
      }
    },
    navjump () {
      this.MESSAGEBOX_SWITCH({type: 'exitConnect'})
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .connectwifi{
   .fs;
   background: #ffffff;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
    .circle_progress{
      .wh(100%, 220px);
      padding-top: 40px;
      .progress_bar{
        .wh(150px, 150px);
        margin: 0 auto;
        position: relative;
        .circle_top{
          position: absolute;
          top: 0;
          left: 0;
          transform-origin: center;
          transform: rotate(-90deg);
          .circle{
            transition: all 1.8s ease-in-out;
          }
        }
        .icon{
          width: 142px; /* no */
          height: 142px; /* no */
          border-radius: 50%;
          position: absolute;
          top: 4px; /* no */
          left: 4px; /* no */
          transform-origin: center;
          transition: all 1.8s ease-in;
          // border: 1px solid red;
          img{
            height: 20px; /* no */
            position: absolute;
            top: -8px; /* no */
            left: 64px; /* no */
          }
        }
        .number{
          .wh(150px, 150px);
          position: absolute;
          left: 0;
          top: 0;
          padding-top: 100px;
          span{
            .wh(60px, 30px);
            display: block;
            margin: 0 auto;
            color: @themeColor;
            font-size: 24px;
          }
        }
      }
    }
   .prompt{
        width:100%;
        text-align: center;
        font-size: 15px;
        p:nth-child(1){
          color: #2ec7ff;
          font-size: 20px;
        }
        p:nth-child(2){
            color: #000;
            margin-top: 8px;
            font-weight: bold;
            font-size: 18px;
        }
        p:nth-child(3){
            color: #b7b7b7;
            margin-top:23px;
        }
     
    }
  }
</style>


