<template>
  <div class="configwifi" :style="{marginTop: -offsetTop + 'px'}">
       <head-top :title="$t('homePage.configureWiFi')" >
         <div slot="setLeft" class="bluetooth" @click="navjump">
           <img src="../../image/global/back.png">
        </div>
       </head-top>
       <div class="outletwifi">
         <img src="../../image/adddirver/wifi@3x.png" />
       </div>  
       <div class="getwifi">
         <div class="wifiname">
            <input type="text" v-model="WIFIValue" :placeholder="$t('homePage.wifiName')"/>
         </div>
         <div class="wifipassword">
            <input v-model="WIFIPassword" :type="passwordHide?'password':'text'" :placeholder="$t('homePage.password')" ref="password">
            <img src="../../image/adddirver/Closed@3x.png" v-if="passwordHide" @click="hide"/>
            <img src="../../image/adddirver/first@3x.png" v-if="!passwordHide" @click="hide" />
         </div>
       </div>
       <div class="nextbtn" @click="next" v-show="!keyboard">
         <span>{{$t('homePage.chooseNext')}}</span>
       </div>
       <div class="prompt" v-show="!keyboard">
        <p>{{$t('homePage.Tips')}}<br/>
          <span>{{$t('homePage.Tips1')}}</span><br/>
          <span>{{$t('homePage.Tips2')}}</span>
        </p>
       </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { WiFiNameRequest, WiFiConfigRequest } from '../../service/method.js'
import { WiFiNameResponse, WiFiConfigResponse } from '../../service/getData.js'
import headTop from '../../components/header/header'

export default {
  data () {
    return {
      timer: null, // 定时器
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      WIFIValue: '', // wifi名
      WIFIPassword: '', // wifi密码
      passwordHide: true  // 默认隐藏密码
    }
  },

  mounted () {
    let h = 0
    this.fullHeight = document.documentElement.clientHeight
    window.onresize = () => {
      h = document.documentElement.clientHeight
      // 软键盘是否弹起
      if (h < this.fullHeight) {
        this.keyboard = true
      } else {
        this.keyboard = false
      }
      // 解决小屏幕遮挡输入框
      if (h < 370) {
        this.offsetTop = 370 - h
      } else {
        this.offsetTop = 0
      }
    }

    // 获取wifi名称返回
    WiFiNameResponse(this.NAVIGATOR, this)

    // 配置WiFi网络返回
    WiFiConfigResponse(this.NAVIGATOR, this)

    // 获取wifi名称
    WiFiNameRequest(this.NAVIGATOR, this)
  },

  components: {
    headTop
  },
  computed: {
    ...mapState([
      'transitiondre'
    ])
  },

  methods: {
    next () {
      if (this.$root.ISDEBUG) {
        this.$router.push({path: '/home/adddirver/connectwifi'})
      }
      // 配置wifi网络
      WiFiConfigRequest(this.NAVIGATOR, this, this.WIFIValue, this.WIFIPassword)
      this.$store.state.transitiondre = true
    },
    hide () {
      this.passwordHide = !this.passwordHide
      this.timer = setTimeout(() => {
        this.$refs.password.focus()
      }, 10)
    },
    navjump () {
      this.$store.state.transitiondre = false
      this.$router.go(-1)
    }
  },

  destroyed () {
    clearTimeout(this.timer)
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .configwifi{
   .fs;
   z-index: 1;
   background: #fff;
   transition: all .1s ease-in;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
   .outletwifi{
     margin: 28px auto 0 auto;
      img{
        height: 130px;
      }
    }
    .getwifi{
      width: 311px;
      margin: 0 auto;
      margin-top: 48px;
      input{
        flex: 1;
        outline: none;
        height: 33px;
      }
      .wifiname{
        display: flex;
        border-bottom: 1px solid @themeColor;
        padding-top: 17px;
        height: 51px;
      }
      .wifipassword{
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid @themeColor;
        height: 51px;
        padding-top: 17px;
        img{
          height: 24px;
        }
      }

    }
   .prompt{
      width: 265px;
      margin: 0 auto;
      margin-top: 21px;
      line-height: 15px;
      color: #ACACAC;
      font-size: 12px;
      text-align: left;
    }
    .nextbtn{
      .wh-l(240px,40px);
      margin: 0 auto;
      margin-top: 107px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      span{
        font-size: 17px;
        color: #fff;
        vertical-align: middle
      }
    }
  }
 </style>


