<template>
  <div class="adddirver">
    <head-top :title="$t('homePage.addDevices')" go-back="true"></head-top>
    <div class="outleticon">
      <img src="../../image/adddirver/outleticon@3x.png" >
    </div>
    <div class="prompt">
      <span>{{$t('homePage.turnOnThePower')}}</span>
    </div>
    <div class="light" @click="clickOut=!clickOut">
      <img v-show="!clickOut" src="../../image/adddirver/yuan@3x.png" />
      <img v-show="clickOut" src="../../image/adddirver/Tickmark.png" />
      <span>{{$t('homePage.indicator')}}</span>
    </div>
    <div class="nextbtn" :class="{nextDisable: !clickOut}" @click="next">
      <span>{{$t('homePage.chooseNext')}}</span>
    </div>
    <transition :name="transitiondre?'slide-right':'slide-left'" mode="in-out">
      <router-view></router-view>
    </transition>

  </div>
</template>
<script>
import { mapState } from 'vuex'
import { isConnectToWiFiRequest } from '../../service/method.js'
import { isConnectToWiFiResponse } from '../../service/getData.js'
import headTop from '../../components/header/header'

export default {
  data () {
    return {
      clickOut: false // 是否勾选
    }
  },

  mounted () {
    // 判断手机是否连接wifi返回
    isConnectToWiFiResponse(this.NAVIGATOR, this)
  },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'transitiondre'
    ])
  },

  methods: {
    next () {
      // 勾选后
      if (this.clickOut) {
        if (this.$root.ISDEBUG) {
          window.isConnectToWiFiResponse(true)
        }
        // 判断手机是否连接wifi
        isConnectToWiFiRequest(this.NAVIGATOR, this)
        this.$store.state.transitiondre = false
      }
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
 
 .adddirver{
   .fs;
   background: #ffffff;
   .outleticon{
     .wh(225px,225px);
     margin: 0 auto;
     margin-top: 68px;
     img{
      .wh(225px,225px)
     }
    }
   .prompt{
     width: 323px;
     margin: 0 auto;
     margin-top: 21px;
     text-align: left;
     line-height: 19px;
     span{
       font-size: 15px;
       color: #585858;
     } 
    }
    .nextbtn{
      .wh(240px,40px);
      line-height: 37px;
      margin: 0 auto;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      span{
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
      }
    }
    .nextDisable{
      background: #D6D6D6;
    }
    .light{
      .wh-l(200px, 30px);
      font-size: 12px;
      margin: 30px auto 0 auto;
      img{
        height: 13px;
        position: relative;
        top: 3px;
        margin-right: 5px;
      }
    }
    .slide-right-enter-active, .slide-right-leave-active{
      transition: all .3s cubic-bezier(.55,0,.1,1);
    }
    .slide-right-enter, .slide-right-leave-to{
      transform: translateX(-100%);
      opacity: 0;
    }
    .slide-left-enter-active, .slide-left-leave-active{
      transition: all .3s cubic-bezier(.55,0,.1,1);
    }
    .slide-left-enter, .slide-left-leave-to{
      transform: translateX(100%);
      opacity: 0;
    }
  }
</style>





