                  if (x < 0) {
                    x += 10
                    console.log(x)
                  } else {
                    x = 0
                    this.delterflag = false
                    clearInterval(timer)
                  }
                  Slistli[i].style.marginLeft = x + 'px'
                }, 30)
              }
            }
          }
          // }
          // console.log(e.changedTouches["0"].pageX)
