              let timer = setInterval(() => {
                if (x > -75) {
                  x += -10
                } else {
                  x = -75
                  this.delterflag = true // 删除显示
                  clearInterval(timer)
                }
                Listli.style.marginLeft = x + 'px'
              }, 30)
