          console.log(Math.abs(this.endX - this.startX))
          // 判断左滑还是点击
          // Slistli = document.getElementsByClassName('list_li')
          if (Math.abs(this.endX - this.startX) < 1) {
            // for (let i = 0; i < Slistli.length; i++) {
            //   console.log(Slistli[i].style.marginLeft)
            //   if (parseInt(Slistli[i].style.marginLeft) < 0) {
