              console.log('state--->' + item.state)
            } else if (num === 2) {
              commonPatternEditTimingRequest(this.NAVIGATOR, this, this.currentDevice.mac, item.id, item.switch, item.time, week, item.state, 2, '0:1', '0:2', item.time2)
              console.log(item.id, item.switch, item.time, week, item.state, 2, '0:1', '0:2', item.time2)
            }
            let timerout = setTimeout(() => {
              this.timerFlag = true
              clearTimeout(timerout)
            }, 300)
          }
        }
      } else {
        this.MESSAGEBOX_SWITCH({ type: 'Undevices' })
      }
    },
    // 新建定时
    newTime (num) {
      if (this.currentDevice.state) {
        if (num === 1) {
          // this.$store.state.editorNEw = 2
          this.$router.push({
            name: 'timingSet',
            params: {
              editorNEw: 2
            }
          })
        } else if (num === 2) {
          this.$router.push({
            name: 'seniorSet',
            params: {
              editorNEw: 2
            }})
        }
      } else {
        this.MESSAGEBOX_SWITCH({ type: 'Undevices' })
      }
    },
    alertChoose () {
      this.MESSAGEBOX_SWITCH({ type: this.messageBox })
    },
    cancelDelter (e, type) { // 检测有删除按钮就让删除隐藏
      console.log(e.target.tagName)
      let Slistli = document.getElementsByClassName('list_li')
      // e.stopImmediatePropagation()
      // e.stopPropagation()
      let startX
      let moveX
      if (type === 'click') {
        for (let i = 0; i < Slistli.length; i++) {
          if (parseInt(Slistli[i].style.marginLeft) < 0) {
            if (e.target.parentNode.className !== 'delate') {
              this.delterflag = true
              let x = parseInt(Slistli[i].style.marginLeft)
              let timer = setInterval(() => {
                if (x < 0) {
                  x += 10
                  console.log(x)
                } else {
                  x = 0
                  this.delterflag = false
                  clearInterval(timer)
                }
                Slistli[i].style.marginLeft = x + 'px'
              }, 30)
            }
            return false
          }
        }
        this.delterflag = false
      }
      if (type === 'start') {
        startX = e.changedTouches['0'].pageX
      }
      if (type === 'move') {
        moveX = e.changedTouches['0'].pageX - startX
      }
      if (Math.abs(moveX) > 10) {
        for (let i = 0; i < Slistli.length; i++) {
          if (parseInt(Slistli[i].style.marginLeft) < 0) {
            let x = parseInt(Slistli[i].style.marginLeft)
            let timer = setInterval(() => {
              if (x < 0) {
                x += 10
                console.log(x)
              } else {
                x = 0
                this.delterflag = false
                clearInterval(timer)
              }
              Slistli[i].style.marginLeft = x + 'px'
            }, 30)
          }
        }
      }
    }
    // runTask (ordinaryData) {
    //   // 显示最近的定时任务
    //   if (this.ordinaryData.length !== 0) {
    //     this.ordinaryData.forEach((value) => {
    //       weekToArrayFormat(value.week).forEach((value2) => {
    //         if (this.taskData[value2] === undefined) {
    //           this.$store.state.taskData[value2] = []
    //         }
    //         this.$store.state.taskData[value2].push(timeToNumFormat(value.time))
    //       })
    //     })
    //     // 排序
    //     this.taskData.forEach((val, index, array) => {
    //       array[index] = sortFormat(val)
    //     })
    //     // 是否有设置定时
    //     this.$store.state.hasTask = true
    //     // 转换时间
    //     // this.taskData.forEach((val, index, array) => {
    //     //   val.forEach((val2, index2, array2) => {
    //     //     array2[index2] = numToTimeFormat(val2)
    //     //   })
    //     // })
    //     console.log(this.taskData)
    //   } else if (this.advancedData.length !== 0) {
    //     // 是否有设置定时
    //     this.$store.state.hasTask = true
    //   } else {
    //     this.$store.state.hasTask = false
    //   }
    // }
  },
  destroyed () {
    powerSwitchStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac)
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import "../../style/mixin.less";

