  z-index: 11;
  background-color: #fff;
  .timing_container {
    .fs-noTop;
    .timing_nav {
      .wh-l(100%, 1.333333rem);
      .navActive {
        color: @themeColor;
      }
      li {
        .wh-f(50%, 100%);
        .nav_button {
          .wh(140px, 100%);
          margin: 0 auto;
          position: relative;
          span {
            font-size: 0.48rem;
          }
          strong {
            .wh(0.746667rem, 0.08rem);
            border-radius: 0.08rem;
            position: absolute;
            bottom: 0.08rem;
            left: 50%;
            margin-left: -0.373333rem;
            margin-top: -0.04rem;
            background: @themeColor;
          }
