      }
    }
    .main_column {
      // .wh(100%, calc(~"100% - 1.333333rem"));
      .wh(100%, 100%);
      position: relative;
      .column_contarner {
        .fs;
        .wrapper {
          .wh(100%, 100%);
        }
        .timing_null {
          .wh(213px, 210px);
          .f-center;
          top: 37%;
          img {
            height: 170px;
            margin: 0 auto;
          }
          span {
            .wh(100%, 30px);
            text-align: center;
            position: absolute;
            left: 0;
            bottom: 0;
            font-size: 18px;
          }
        }
        #righticon .icon{
          .wh(30px,100%);
          img{
            .wh(30px,20px);
          }
        }
        .column_List {
          .wh(100%, auto);
          position: relative;
          padding: 0.4rem 0.213333rem 0 0.213333rem;
          //overflow: auto;
          padding-bottom: 90px;
          .swipe_wrap {
            margin-bottom: 10px;
            position: relative;
            .cell_swipe {
              .wh(100%, 95px);
              position: relative;
              .swipe_container {
                .fs;
                padding-bottom: 5px;
                .list_li {
                  .wh(100%, 100%);
                  position: relative;
                  border: 1px solid #d4e4e5;
                  border-radius: 0.133333rem;
                  .li_left {
                    .wh-f(85%, 100%);
                    padding-left: 0.4rem;
                    a {
                      .wh(100%, 100%);
                      display: block;
                    }
                    .time {
                      .wh(100%, 50%);
                      line-height: 1.333333rem;
                      font-size: 0.8rem;
                      color: @themeColor;
                      text-align: left;
                      span {
                        margin: 0 5px;
                      }
                    }
                    .senior-margintop{
                      margin-top: 7%
                    }
                    .describe {
                      .wh(100%, 50%);
                      line-height: 0.96rem;
                      position: relative;
                      span {
                        float: left;
                        font-size: 0.32rem;
                        color: #6a6a6a;
                      }
                      span:nth-child(1) {
                        margin-right: 0.213333rem;
                        img {
                          height: 0.4rem;
                          position: relative;
                        }
                      }
                      span:nth-child(3) {
                        margin: 0 0.373333rem;
                        img {
                          height: 0.32rem;
                          position: relative;
                          top: 0.053333rem;
                        }
                      }
                      span:nth-child(4) {
                        width:70%;
                        text-align: left;
                        line-height: 0.453333rem;
                        position: relative;
                        top: 0.266667rem;
                      }
                      .timeSlot {
                        width: 80%;
                        text-align: left;
                        line-height: 0.453333rem;
                        position: relative;
                        top: 0.266667rem;
                      }
                    }
                  }
                  .li_right {
                    .wh-f(17%, 100%);
                    position: absolute;
                    right: 0;
                    // .time_swicth {
                    //   position: absolute;
                    //   top: 50%;
                    //   right: 0.3rem;
                    //   transform: translateY(-50%);
                    // }
                    img{
                      .wh(45px,22.5px);
                      margin-top:33px;
                    }
                  }
                  .delate{
                    position: absolute;
                    right:-75px;
                    top: 20px;
                    .wh(50px,100%);
