                }
              }
              &:active {
                background: transparent;
              }
            }
          }
        }
        .button {
          .wh-l(6.666667rem, 1.066667rem);
          border-radius: 1.066667rem;
          position: absolute;
          bottom: 1.066667rem;
          left: 50%;
          margin-left: -3.333333rem;
          background: url(../../image/timing/button.png) no-repeat;
          background-size: cover;
          box-shadow: 0 5px 10px #bbd8f5;
          span {
            .wh(auto, 100%);
            margin: 0 auto;
            display: block;
            font-size: 0.453333rem;
            color: #fff;
            strong {
              .wh(auto, 100%);
              img {
                height: 0.533333rem;
                vertical-align: middle;
                margin-top: -3px;
              }
            }
            em {
              .wh(auto, 100%);
              line-height: normal;
              padding-top: 0.213333rem;
            }
          }
        }
      }
    }
  }
  .diSabledclick {
    .wh(100%,100%);
    z-index: 999;
    .fs;
    margin-top: 50px;
    background: grey;
    opacity: 0.2;
  }
}
</style>



