import Vue from 'vue'
import store from '../store/index'
// 系统错误捕获
const errorHandler = (error, vm, info) => {
  console.error('抛出全局异常')
  console.error(error)
  let {
    // message, // 异常信息
    // name, // 异常名称
    // script,  // 异常脚本url
    // line,  // 异常行号
    // column,  // 异常列号
    stack  // 异常堆栈信息
  } = error
  store.state.errorMsg = stack
}
Vue.config.errorHandler = errorHandler
Vue.prototype.$throw = (error) => errorHandler(error, this)



