<template>
  <transition name="toast">
    <div class="toast" :class="position==='bottom'?'positionBottom':''">
      <div class="icon" v-if="icon">
        <img v-if="icon==='addSuccess'" src="../../image/global/right.png">
        <img v-if="icon==='resetSuccess'" src="../../image/global/right.png">
        <img v-if="icon==='setCountdown'" src="../../image/countdown/setcountdown.png">
        <img v-if="icon==='setTemperature'" src="../../image/humidity/File.png">
        <img v-if="icon==='setHumidity'" src="../../image/humidity/Humidity.png">
        <img v-if="icon==='setPower'" src="../../image/powerSaving/Light.png">
        <img v-if="icon==='setMonitor'" src="../../image/powerSaving/Clock-icon.png">
        <img v-if="icon==='setMonitorAndPower'" src="../../image/powerSaving/Clock-icon.png">
        <img v-if="icon==='setElectricity'" src="../../image/cost/Danger.png">
        <img v-if="icon==='setElectricityAndTime'" src="../../image/cost/Notepad.png">
        <img v-if="icon==='setSpend'" src="../../image/cost/yuan.png">
        <img v-if="icon==='setSpendAndTime'" src="../../image/cost/Notepad.png">
        <img v-if="icon==='setCostTime'" src="../../image/cost/yuan.png">
        <img class="waiting" v-if="icon==='addEquipment'" src="../../image/add/Hourglass.png">
        <img class="waiting" v-if="icon==='disconnectEquipment'" src="../../image/add/Hourglass.png">
        <img v-if="icon==='connectSuccess'" src="../../image/global/right.png">
        <img v-if="icon==='addFail'" src="../../image/global/Close.png">
      </div>
      <div class="describe" >{{describe}}</div>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {}
  },

  mounted () {},

  props: [ 'describe', 'icon', 'position' ],

  components: {},

  computed: {
    ...mapState([])
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

@keyframes waiting{
  0% {
    transform: rotate(0deg)
  }
  20% {
    transform: rotate(90deg)
  }
  40% {
    transform: rotate(175deg)
  }
  60% {
    transform: rotate(178deg)
  }
  80% {
    transform: rotate(179deg)
  }
  100% {
    transform: rotate(180deg)
  }
}
.toast{
  .wh(auto, auto);
  max-width: 250px;
  background: rgba(0, 0, 0, .7);
  color: #EEEEEE;
  padding: 5px 0px 0 0px;
  border-radius: 5px;
  .f-center;
  z-index: 998;
  .icon{
    .wh(auto, 22px);
    margin: 5px auto 5px auto;
    position: relative;
    img{
      height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -15px;
      margin-top: -11px;
    }
    .waiting{
      margin-left: -10px;
      transform-origin: center;
      animation: waiting .9s ease-out infinite;
    }
  }
  .describe{
    font-size: 14px;
    padding: 10px 10px;
    min-width: 180px;
  }
}
.positionBottom{
  top: 88%;
}
//提示框动画
.toast-enter-active,.toast-leave-active{
  transition: opacity .3s linear;
}
/* 进入开始 */
.toast-enter{
  opacity: 0;
}
/* 出去终点 */
.toast-leave-active{
  opacity: 0;
}

</style>



