<template>
  <div class="addwizard">
       <head-top title="添加向导" >
          <div slot="setLeft" class="bluetooth" @click="navjump">
            <img src="../../image/global/back.png">
          </div>
       </head-top>
       <div class="dristribution">
         <img src="../../image/adddirver/dristribution@3x.png" >
       </div>  
       <div class="prompt">
         <span>设备、手机、路由器互相靠近，并使设备进入待配网的状态，且手机已成功链接2.4G可联网的WIFI(暂不支持5G频段的WIFI）   </span>
       </div>
       <div class="nextbtn"  @click="next1">
         <span>下一步</span>
       </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import headTop from '../../components/header/header'

export default {
  data () {
    return {

    }
  },
  created () {

  },

  mounted () { },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'transitiondre'
    ])
  },
  methods: {
    next1 () {
      this.$store.state.transitiondre = false
      this.$router.push({path: '/home/adddirver/configwifi'})
    },
    navjump () {
      this.$store.state.transitiondre = false
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .addwizard{
   .fs;
   background: #ffffff;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
   .dristribution{
     .wh(207.5px,155px);
     margin: 0 auto;
     margin-top:20px;
     img{
      .wh(207.5px,155px)
     }
    }
   .prompt{
     width: 323px;
     margin: 0 auto;
     margin-top: 22px;
     text-align: left;
     line-height: 19px;
     span{
       font-size: 15px;
       color: #585858;
     
     } 
    }
    .nextbtn{
      .wh-l(240px,40px);
      margin: 0 auto;
      margin-top: 160px;
        border-radius: 40px;
        background:  @themeColor;
        text-align: center;
        span{
          font-size: 17px;
          color: #fff;
          vertical-align: middle;

        }
    }
  }
 </style>


