<template>
  <div class="setTask">
    <head-top :title="$t('setTaskPage.tittle')" goBack="true">
      <!-- 删除任务 -->
      <!-- <div slot="setRight" class="delete" @click="deleteTask">
        <img src="../../image/task/Delete.png">
      </div> -->
    </head-top>
    <div class="setTask_container">
      <div class="task_column" >
        <div class="full" v-if="!taskShow">
          <img src="../../image/task/File.png" alt="">
          <div>{{$t('setTaskPage.noTask')}}</div>
        </div>
        <div class="task_column_li" v-if="taskShow">
          <div class="column" v-if="timingOff || temperatureOFF || spendingCountdownOff ">
            <div class="column_center">{{$t('setTaskPage.current')}}</div>
            <ul class="column_right">
              <!-- v-if="timingOff" -->
              <li v-if="timingOff && item.state" v-for="(item, index) in this.timeData" :key=index.id>
                <img src="../../image/task/timing-icon.png">
                <span>{{$t('setTaskPage.timerSRunning')}}{{item.week }} {{ item.time }} {{ item.switch?$t('setTaskPage.turnONplug'):$t('setTaskPage.turnOFFplug')}}</span>
                <div style="clear:both"></div>
              </li>
              <!-- <li v-if="countDownOFF">
                <img src="../../image/task/countdown-icon.png">
                <span>倒计时设定已启动</span>
                <div style="clear:both"></div>
              </li> -->
              <li v-if="temperatureOFF">
                <img src="../../image/task/Temperature-icon.png">
                <span>{{temperatureMode}}</span>
                <div style="clear:both"></div>
              </li>
              <li v-if="spendingCountdownOff">
                <img src="../../image/task/Temperature-icon.png">
                <span>{{spendingCountdownMode}}</span>
                <div style="clear:both"></div>
              </li>
              
            </ul>
            <div class="line_left">
              <div class="line" v-if="countDownOFF"></div>
              <div class="icon"></div>
            </div>
            <!-- <div class="bottom_line"></div> -->
          </div>
        </div>
        
        <div class="task_column_li" v-if="taskShow">
          <div class="column" v-if="countDownOFF">
            <div class="column_center column-countdown">{{this.timeformatext}}</div>
            <ul class="column_right">
              <li>
                <img src="../../image/task/countdown-icon.png">
                <span>{{this.taskText}}</span>
                <div style="clear:both"></div>
              </li>
              <div class="line_left no_line_left">
                <div class="icon icon_off"></div>
              </div>
            </ul>
            <!-- <div class="bottom_line"></div> -->
          </div>
          <!-- <div class="column">
            <div class="column_center">{{$t('setTaskPage.countdownOff')}}</div>
            <ul class="column_right">
              <li>
                <img src="../../image/task/Temperature-icon.png">
                <span>{{$t('setTaskPage.temperatureRunning')}}</span>
                <div style="clear:both"></div>
              </li>
              <li>
                <img src="../../image/task/Temperature-icon.png">
                <span>{{$t('setTaskPage.temperatureRunning')}}</span>
                <div style="clear:both"></div>
              </li>
              <div class="line_left">
                <div class="icon icon_off"></div>
              </div>
            </ul>
            <div class="bottom_line"></div>
          </div> -->
        </div>
      </div>
    </div>

    <!-- 弹窗 -->
    <!-- 结束当前所有设定的任务 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='endTaskOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
          {{$t('messageBox.allRunningTasks')}}
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(1)">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
          <div class="cancle">
            <span @click="alertChoose(0)">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../components/header/header'
import messageBox from '../../components/common/messageBox'
import {
  // powerSwitchCountdownResponse,
  // alarmTemperatureValueResponse,
  countdownDataResponse,
  // spendingCountdownAlarmResponse,
  temperatureAndHumidityDataResponse,
  // spendingCountdownDataResponse,
  commonPatternListDataResponse
  // commonPatternTimingResponse,
  // spendingCountdownAlarmResponse
} from '../../service/getData'
import {
  // powerSwitchCountdownRequest,
  // alarmTemperatureValueRequest,
  countdownDataRequest,
  // spendingCountdownAlarmRequest,
  // spendingCountdownDataRequest,
  // commonPatternEditTimingRequest,
  temperatureAndHumidityDataRequest,
  // spendingCountdownAlarmRequest,
  commonPatternListDataRequest
} from '../../service/method'
import { getStore, weekFormat } from '../../config/utils'
export default {
  data () {
    return {
      temperatureMode: this.$t('setTaskPage.heaterRun'), // 温度设定模式
      spendingCountdownMode: '定量定费已启动', // 定量定费模式
      taskTime: '00', // 任务设定时间
      taskText: null, // 设定任务内容
      taskShow: false, // 清空任务
      tempData: JSON.parse(getStore('temperatureData')), // 温度届面数据
      spendcostData: null, // 定量定费界面数据
      localTime: JSON.parse(getStore('countDownData')),
      timeData: JSON.parse(getStore('timeData')),
      timer: null
    }
  },
  created () {
    temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    this.timer = setTimeout(() => {
      countdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      this.timer = null
    }, 300)
    this.timer = setTimeout(() => {
      commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(this.timer)
    }, 300)
    // spendingCountdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'task')
    // spendingCountdownDataResponse(this.NAVIGATOR, this)
    countdownDataResponse(this.NAVIGATOR, this, 'task')
    commonPatternListDataResponse(this.NAVIGATOR, this, 'task')
    // commonPatternTimingResponse(this.NAVIGATOR, this)
    // powerSwitchCountdownResponse(this.NAVIGATOR, this)
    // alarmTemperatureValueResponse(this.NAVIGATOR, this)
    // spendingCountdownAlarmResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      window.temperatureAndHumidityDataResponse(this.currentDevice.mac, '{"temperature":{"currentValue":23,"hotAlarmValue":0,"hotAlarmSwitch":true,"codeAlarmValue":20,"codeAlarmSwitch":true}}')

      // window.spendingCountdownDataResponse(this.currentDevice.mac, true, '{"power":{"currentValue":100,"alarmValue":50,"alarmSwitch":true,"year":2018,"month":6,"day":6},"cost":{"currentValue":30,"alarmValue":40,"alarmSwitch":false,"year":2018,"month":1,"day":16}}')

      window.countdownDataResponse(this.currentDevice.mac, '{"hour":10,"minute":15,"Switchgear":true,"countdownSwitch":true}')

      window.commonPatternListDataResponse(this.currentDevice.mac, '[{"id":1,"time":"7:4","switch":false,"week":31,"state":false},{"id":2,"time":"17:23","switch":true,"week":63,"state":true},{"id":3,"time":"16:15","switch":true,"week":63,"state":true}]', '[]')
      // window.powerSwitchCountdownResponse(this.currentDevice.mac, false, true)
      // console.log(this.temperatureMode)
      // console.log(this.spendingCountdownMode)
      // console.log(this.taskTime)
      // console.log(this.taskText)
      // console.log(this.tempData)
    }
    this.temperatureFormat()
  },
  mounted () {
    console.log('20171158')
    console.log(this.timingOff)
    if (this.spendingCountdownOff || this.countDownOFF || this.temperatureOFF || this.timingOff) {
      this.taskShow = true
      if (this.timingOff) {
        this.timeData = JSON.parse(getStore('timeData'))
      }
    } else {
      this.taskShow = false
    }
    console.log('20170723')
    console.log(this.$i18n.locale)
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBoxType',
      'countDownOFF',
      'temperatureOFF',
      'spendingCountdownOff',
      'timingOff',
      'currentDevice',
      'currentDevice'
    ]),
    timeformatext () {
      let time
      if (this.localTime.countdownSwitch) {
        time = this.localTime
      } else {
        time = JSON.parse(getStore('countDownRange'))
      }
      let str1, str2
      if (time.hour !== 0) {
        if (this.$i18n.locale === 'en') {
          str1 = 'After ' + time.hour + ' hours '
        } else if (this.$i18n.locale === 'zh') {
          str1 = time.hour + ' 小时 '
        }
      } else {
        if (this.$i18n.locale === 'en') {
          str1 = 'After '
        } else if (this.$i18n.locale === 'zh') {
          str1 = ''
        }
      }
      if (this.$i18n.locale === 'zh') {
        str2 = time.minute + ' 分钟后'
      } else if (this.$i18n.locale === 'en') {
        str2 = time.minute + ' minutes'
      }
      if (time.Switchgear) {
        this.taskText = this.$t('setTaskPage.turnONplug')
      } else {
        this.taskText = this.$t('setTaskPage.turnOFFplug')
      }

      return str1 + str2
    }
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),
    dataForamt (objData, type) {
      for (let i in objData) {
        objData[i].time = this.timeFormat(objData[i].time).str
        if (type === 'senior') {
          objData[i].time2 = this.timeFormat(objData[i].time2).str
        }
        objData[i].timeNumber = this.timeFormat(objData[i].time).num
        objData[i].week = weekFormat(objData[i].week, this)
        console.log(objData[i].week)
      }
      for (let j = 0; j < objData.length; j++) {
        let temp = {}
        for (let k = j + 1; k < objData.length; k++) {
          if (objData[j].timeNumber > objData[k].timeNumber) {
            temp = objData[j]
            objData[j] = objData[k]
            objData[k] = temp
          }
        }
      }
      console.log(objData)
      return objData
    },
    // 时间不足两位补零
    timeFormat (str) {
      console.log(str)
      let array = str.split(':')
      let h = parseInt(array[0])
      let m = parseInt(array[1])
      if (h < 10) {
        h = '0' + h
      }
      if (m < 10) {
        m = '0' + m
      }
      return {
        str: h + ' : ' + m,
        num: parseInt('' + h + m)
      }
    },
    deleteTask () {
      if (this.taskShow) {
        this.MESSAGEBOX_SWITCH({type: 'endTaskOpreation'})
      }
    },
    temperatureFormat () {
      // let data = JSON.parse(getStore('temperatureData'))
      if (this.tempData.temperature) {
        if (this.tempData.temperature.codeAlarmSwitch) {
          this.temperatureMode = this.$t('setTaskPage.coolRun')
        } else if (this.tempData.temperature.hotAlarmSwitch) {
          this.temperatureMode = this.$t('setTaskPage.heaterRun')
        }
      }
    },

    alertChoose (num) {
      console.log(num)
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          this.taskShow = false
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          // 关闭倒计时
          // powerSwitchCountdownRequest(this.NAVIGATOR, this, this.currentDevice.mac, 0, 0, this.powerActive, !this.startActive)
          // // 关闭定时
          // let timeData = JSON.parse(getStore('timeData'))
          // console.log(timeData)
          // timeData.forEach((element) => {
          //   if (element.state) {
          //     // let week = this.getweekday(element.week)
          //     console.log('关闭任务' + element.week)
          //     let weekday = getWeekday(element.week)
          //     console.log(weekday)
          //     commonPatternEditTimingRequest(this.NAVIGATOR, this, this.currentDevice.mac, element.id, element.switch, element.time, weekday, false)
          //   }
          // })
          // 关闭温度
          // let hotvalue = this.tempData.temperature.hotAlarmValue
          // let codevalue = this.tempData.temperature.codeAlarmValue
          // if (this.temperatureMode === '温度（制冷模式）设定已启动') {
          //   alarmTemperatureValueRequest(this.NAVIGATOR, this, this.currentDevice.mac, false, hotvalue, 2)
          // } else if (this.temperatureMode === '温度（制热模式）设定已启动') {
          //   alarmTemperatureValueRequest(this.NAVIGATOR, this, this.currentDevice.mac, false, codevalue, 1)
          // }
          if (this.spendingCountdownOff) {
            // let spendAlarmValue = spendcostData.power.alarmValue
            // let costAlarmValue = spendcostData.cost.alarmValue
            // let pm = spendcostData.power.month < 10 ? '0' + spendcostData.power.month : spendcostData.power.month
            // let pd = spendcostData.power.day < 10 ? '0' + spendcostData.power.day : spendcostData.power.day
            // let cm = spendcostData.cost.month < 10 ? '0' + spendcostData.cost.month : spendcostData.cost.month
            // let cd = spendcostData.cost.day < 10 ? '0' + spendcostData.cost.day : spendcostData.cost.day
            // if (this.spendingCountdownMode === '定量定费已启动') {
            //   spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 1, 0, false, 0, 0, 0)
            //   spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 2, 0, false, 0, 0, 0)
            // } else if (this.temperatureMode === '电量设定已启动') {
            //   spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 1, 0, false, 0, 0, 0)
            // } else if (this.temperatureMode === '定量定费已启动') {
            //   spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 2, 0, false, 0, 0, 0)
            // }
          }
          break
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.setTask{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .delete{
    .wh(1rem, 100%);
    float: right;
    margin-right: .2rem;
    position: relative;
    img{
      height: .666667rem;
      .f-center;
    }
  }
  .setTask_container{
    .fs-noTop;
    padding: .133333rem .4rem .666667rem .4rem;
    .task_column{
      .wh(100%, 100%);
      padding-top: .586667rem;
      // background: url('../../image/task/Taskbackground-icon.png') 0 0 no-repeat;
      background-size: 100% 100%;
      overflow-y: auto;
      .full{
        .wh(85px,auto);
        margin: 0 auto;
        margin-top: 142px;
        font-size: 14px;
        img{
          .wh(80px,80px);
        }
      }
      .task_column_li{
        .wh(100%, auto);
        float: left;
        .column{
          .wh-f(100%, auto);
          font-size: .32rem;
          position: relative;
          display: flex;
          // margin-top: .586667rem;
          justify-content: space-around;
          .column_center{
            .wh(140px, 100%);
            padding: 0 20% 0 10%;
            font-size: 11px;
          }
          .column-countdown{
            padding: 0 5% 0 10%;
            text-align: left;
            word-break: break-all;
          }
          .column_right{
            .wh-f(auto, auto);
            flex: 1;
            // position: relative;
            li{
              margin-bottom: 30px;
              img{
                .wh(.666667rem, .666667rem);
                float: left;
                margin-right: .4rem;
                position: relative;
                top: -.106667rem;
              }
              span{
                // .wh(160px, .666667rem);
                width: 160px;
                min-height: .666667rem;
                display: block;
                // word-wrap: break-word;
                word-break:break-all;
                text-align: left;
                line-height: 20px;
                float: left;

              }
            }
          }
          .line_left{
              height: 100%;
              position: absolute;
              left: 4%;
              top: 0;
              .line{
                height: 100%;
                border-left: 1px solid #DBE6E7;
              }
              .icon{
                .wh(.666667rem, .666667rem);
                border: .133333rem solid #F6FCFC;
                display: block;
                background: url('../../image/task/Thecurrent-icon@3x.png') 0 0 no-repeat;
                background-size: cover;
                position: absolute;
                top: -.08rem;
                left: -.333333rem; 
                z-index: 1;
              }
              .icon_off{
                background: url('../../image/task/before-icon.png') 0 0 no-repeat;
                background-size: cover;
              }
            }
            .no_line_left{
              border-left: 1px solid transparent;
            }
          .bottom_line{
            width: 66%;
            border-bottom: 1px solid #DBE6E7;
            position: absolute;
            left: 17%;
            bottom: 0;
          }
        }
      }      
    }    
  }
}

</style>



