<template>
  <div class="header">
    <div class="head-left">
      <slot name="setLeft"></slot>
      <div class="back" v-if="goBack" @click="$router.go(-1)">
        <img src="../../image/global/back.png" />
      </div>
    </div>
    <div class="head-center">{{title}}
      <slot name="system"></slot>
    </div>
    <div class="head-right">
      <slot name="setRight"></slot>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {}
  },

  props: ['title', 'goBack'],

  computed: {
    ...mapState([])
  },

  methods: {}
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import "../../style/mixin.less";

.header {
  .wh-l(100%, 1.333333rem);
  background: @themeColor;
  font-size: 0.48rem;
  color: #f5faff;
  .head-left {
    .wh-f(30%, 100%);
    .back {
      .wh(1.066667rem, 100%);
      margin-left: 0.2rem;
      img {
        height: 0.533333rem;
        vertical-align: middle;
      }
    }
  }
  .head-center {
    .wh-f(40%, 100%);
    text-align: center;
  }
  .head-right {
    .wh-f(30%, 100%);
  }
}
</style>



