<template>
  <div class="add">
    <head-top :title="$t('addPage.tittle')"></head-top>
    <section class="main_container">

      <div class="container_top">
        <img src="../../image/socket.png">
        <span>{{$t('addPage.noDevices')}}</span>        
      </div>
      <div class="container_bottom">
        <div class="button" @click="add">
          <span>{{$t('addPage.addDevices')}}</span>
        </div>
        <div class="experience">
          <strong @click="experience">
            <span>{{$t('addPage.experience')}}</span>
            <img src="../../image/Resize.png">
          </strong>
        </div>
        <!-- <div class="language" @click="setLanguage">
          {{$t('addPage.language')}}
          <span>
            <b v-show="$i18n.locale==='zh'">{{$t('addPage.chinese')}} </b>
            <b v-show="$i18n.locale==='en'">{{$t('addPage.english')}} </b>            
            <div class="after after_icon"></div>
          </span>
        </div> -->
      </div>
    </section>

    <!-- 断开蓝牙 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='turnOffBlueToothOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">您的设备断开连接了，请检查蓝牙开关及连接范围是否正常</div>
        <div class="alert_operation">
          <div class="determine_one"  @click="alertChoose(1)">{{$t('messageBox.confirm')}}</div>
        </div>
      </div>
    </messageBox>

    <!-- 添加设备 -->
    <toast :describe="$t('toast.addEquipment')" icon="addEquipment" position="center" v-if="ToastType==='addEquipment'"></toast>

    <!-- 断开设备 -->
    <toast :describe="$t('toast.disconnectEquipment')" icon="disconnectEquipment" position="center" v-if="ToastType==='disconnectEquipment'"></toast>

    <!-- 添加成功 -->
    <toast :describe="$t('toast.addSuccess')" icon="connectSuccess" position="center" v-if="ToastType==='connectSuccess'"></toast>

    <!-- 添加失败 -->
    <toast :describe="$t('toast.addFail')" icon="addFail" position="center" v-if="ToastType==='addFail'"></toast>
    
    <transition :name="transitionName" mode="in-out">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import { systemLanguageRequest, isOpenBluetoothRequest } from '../../service/method'
import { blueToothStateResponse, systemLanguageResponse, setSystemLanguageResponse, isOpenBluetoothResponse, isFirstBindingResponse } from '../../service/getData'
import { ISDEBUG } from '../../config/env'
import headTop from '../../components/header/header'
import toast from '../../components/common/toast'
import messageBox from '../../components/common/messageBox'
import {setStore} from '../../config/utils'
export default {
  data () {
    return {
      flag: false // true 断开蓝牙弹框显示
    }
  },

  mounted () {
    // let n = 0
    // let h = 0
    // window.onresize = () => {
    //   h = document.documentElement.clientHeight
    //   if (++n === 1) {
    //     this.fullHeight = h
    //   }
    // }
    // 版本号
    console.log('版本号2.000')
    // 蓝牙开关状态返回
    let obj = {
      init: true
    }
    setStore('timeData', obj)
    blueToothStateResponse(this.NAVIGATOR, this)

    // 查询系统语言返回
    systemLanguageResponse(this.NAVIGATOR, this)

    // 查询系统语言
    systemLanguageRequest(this.NAVIGATOR, this)

    // 设置系统语言返回
    setSystemLanguageResponse(this.NAVIGATOR, this)

    // 判断蓝牙是否开启返回
    isOpenBluetoothResponse(this.NAVIGATOR, this)

    // 判断蓝牙是否开启
    if (this.firstRunning) {
      isOpenBluetoothRequest(this.NAVIGATOR, this)
    }

    // 判断是否首次绑定返回
    isFirstBindingResponse(this.NAVIGATOR, this)

    // setTimeout(() => {
    //   window.isFirstBindingResponse(false, '{"state":0,"signal":-77,"mac":"FF:EE:DD:CC:BB:AA","name":"TRSPX-BLE"}')
    // }, 2000)
  },

  components: {
    headTop,
    toast,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBoxType', 'ToastType', 'virtualExperience', 'currentDevice', 'firstRunning'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    alertChoose (num) {
      switch (num) {
        case 1:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
      }
    },

    add () {
      this.$root.ISDEBUG = ISDEBUG
      this.$store.state.virtualExperience = false
      this.$router.push({path: '/add/connect'})
      this.$store.firstRunning = true
    },

    experience () {
      this.$root.ISDEBUG = true
      this.$store.state.virtualExperience = true
      this.$router.push({path: '/add/connect'})
    },

    setLanguage () {
      this.$root.ISDEBUG = ISDEBUG
      this.$router.push({path: '/add/language'})
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.add{
  .fs;
  background: #FFF;
  .main_container{
    .fs-noTop; 
    .container_top{
      .wh(100%, 70%);
      position: relative;
      img{
        .wh(5.6rem, 5.6rem);
        position: absolute;
        top: 1.946667rem;
        left: 50%;
        margin-left: -2.8rem;
      }
      span{
        .wh-l(100%, 1.066667rem);
        display: block;
        font-size: .4rem;
        color: #464646;
        position: absolute;
        left: 0;
        top: 8rem;
      }
    }
    .container_bottom{
      .wh(100%, 30%);
      position: relative;
      padding-top: 1.653333rem;
      .button{
        .wh-l(6.666667rem, 1.066667rem);
        border-radius: 1.066667rem;
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: -3.333333rem;
        background: url(../../image/timing/button.png) no-repeat;
        background-size: cover;
        box-shadow: 0 5px 10px #bbd8f5;
        span{
          .wh(40%, 100%);
          margin: 0 auto;
          display: block;
          font-size: .453333rem;
          color: #FFF;
        }
      }
      .experience{
        .wh(100%, 1.066667rem);
        strong{
          .wh(auto, 100%);
          margin: 0 auto;
          display: block;
          span{
            font-size: 16px;
            color: #4D4D4D;
            position: relative;
            left: 4px;
          }
          img{
            height: .4rem;
            position: relative;
            top: .053333rem;
            left: 6px;
          }
        }        
      }
      .language{
        .wh-l(200px, 30px);
        font-size: 16px;
        position: absolute;
        left: 50%;
        bottom: 20px;
        margin-left: -100px;
        text-align: left;
        padding-left: 24px;
        span{
          margin-left: 20px;
          position: relative;
          .after_icon{
            right: -12px;
          }
        }
      }
    }
  }
}

</style>



