<template>
  <div class="dataShow">
    <div class="dataShow_container">
      <div class="dataShow_top">
        <div class="head">
          <div class="head-left">
            <div class="back" @click="$router.go(-1)">
              <img src="../../image/global/back.png" />
            </div>
          </div>
          <div class="head-center">
            <ul class="nav">
              <li @click="nav(1)" :class="navIndex==1 ? 'navActive' : ''">{{$t('costPage.power')}}</li>
              <li @click="nav(2)" :class="navIndex==2 ? 'navActive' : ''">{{$t('costPage.costspent')}}</li>
            </ul>
          </div>
          <div class="head-right"></div>
        </div>
        <div class="table">
          <ul class="time">
            <li @click="timeNav(1)" :class="timeNavIndex==1 ? 'timeAvtive' : ''">{{$t('costPage.day')}}</li>
            <li @click="timeNav(2)" :class="timeNavIndex==2 ? 'timeAvtive' : ''">{{$t('costPage.week')}}</li>
            <li @click="timeNav(3)" :class="timeNavIndex==3 ? 'timeAvtive' : ''">{{$t('costPage.month')}}</li>
          </ul>
          <div class="table_container">
            <div class="myChart" id="myChart"></div>
          </div>
        </div>        
      </div>
      <div class="dataShow_bottom">
        <div class="describe">
          <span>{{$t('costPage.date')}}，{{$t('costPage.powerConsumption')}}</span>
          <strong>（Kwh）</strong>
        </div>
        <div class="value">13</div>
      </div>
    </div>
  </div>
</template>

<script>
import {} from 'vuex'
import {} from 'mint-ui'
import headTop from '../../components/header/header'
import echarts from 'echarts'

export default {
  data () {
    return {
      navIndex: 1, // 导航选中
      timeNavIndex: 1 // 时间导航选中
    }
  },

  mounted () {
    this.myChartInit('day')
  },

  components: {
    headTop
  },

  computed: {},

  methods: {
    // 重绘echarts
    myChartInit (type) {
      let myChart = echarts.init(document.getElementById('myChart'))

      var month = new Date().getMonth() + 1
      var day = new Date().getDate()
      var xData = []
      var yData = []
      var data

      switch (type) {
        case 'day':
          data = [
            {
              name: month + '/' + (day - 6),
              value: 85
            },
            {
              name: month + '/' + (day - 5),
              value: 36
            },
            {
              name: month + '/' + (day - 4),
              value: 65
            },
            {
              name: month + '/' + (day - 3),
              value: 45
            },
            {
              name: month + '/' + (day - 2),
              value: 36
            },
            {
              name: month + '/' + (day - 1),
              value: 66
            },
            {
              name: '今天',
              value: 70
            }
          ]
          break
        case 'week':
          data = [
            {
              name: this.printWeek(-6),
              value: 65
            },
            {
              name: this.printWeek(-5),
              value: 90
            },
            {
              name: this.printWeek(-4),
              value: 30
            },
            {
              name: this.printWeek(-3),
              value: 45
            },
            {
              name: this.printWeek(-2),
              value: 36
            },
            {
              name: this.printWeek(-1),
              value: 66
            },
            {
              name: '本周',
              value: 70
            }
          ]
          break
        case 'month':
          data = [
            {
              name: this.printMonth(-6),
              value: 65
            },
            {
              name: this.printMonth(-5),
              value: 20
            },
            {
              name: this.printMonth(-4),
              value: 30
            },
            {
              name: this.printMonth(-3),
              value: 95
            },
            {
              name: this.printMonth(-2),
              value: 36
            },
            {
              name: this.printMonth(-1),
              value: 66
            },
            {
              name: '本月',
              value: 70
            }
          ]
          break
      }

      data.map(function (a, b) {
        xData.push(a.name)
        yData.push(a.value)
      })

      myChart.setOption({
        color: ['#3398DB'],
        legend: {
          data: ['直接访问', '背景'],
          show: false
        },
        grid: {
          left: '5%',
          right: '3%',
          bottom: '5%',
          top: '15%',
          height: '76%',
          containLabel: true,
          z: 22
        },
        xAxis: [
          {
            type: 'category',
            gridIndex: 0,
            data: xData,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#fff'
              }
            },
            axisLabel: {
              show: true,
              color: '#fff',
              fontSize: 12
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: this.$t('costPage.power') + '（Kwh）',
            gridIndex: 0,
            splitLine: {
              show: false
            },
            axisTick: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: '#fff'
              }
            },
            axisLabel: {
              color: '#fff',
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            gridIndex: 0,
            splitNumber: 12,
            splitLine: {
              show: false
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            },
            splitArea: {
              show: false,
              areaStyle: {
                color: ['rgba(250,250,250,0.0)', 'rgba(250,250,250,0.05)']
              }
            }
          }
        ],
        series: [
          {
            name: '合格率',
            type: 'bar',
            animationDelay: 250,
            label: {
              show: true,
              position: 'top',
              padding: 10,
              color: '#fff',
              fontSize: 14,
              formatter: '{c}'
            },
            barWidth: '45%',
            xAxisIndex: 0,
            yAxisIndex: 0,
            itemStyle: {
              normal: {
                barBorderRadius: 30,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#97D8FF'
                  },
                  {
                    offset: 0.5,
                    color: '#75D5FF'
                  },
                  {
                    offset: 1,
                    color: '#2EC6FF'
                  }
                ])
              },
              // 鼠标悬停时：
              emphasis: {
                barBorderRadius: 30,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#F0FAFD'
                  },
                  {
                    offset: 0.5,
                    color: '#A6E8FC'
                  },
                  {
                    offset: 1,
                    color: '#2ACBFF'
                  }
                ])
              }
            },
            data: yData,
            zlevel: 11
          }
        ]
      })
    },

    // 一周起止时间（num:0 本周 num:-1 上周 num:1 下周）
    printWeek (num) {
      var now = new Date()
      var nowDayOfWeek = now.getDay()
      var nowDay = now.getDate()
      var nowMonth = now.getMonth()
      var nowYear = now.getYear()
      nowYear += (nowYear < 2000) ? 1900 : 0
      var weekStartDate = new Date(nowYear, nowMonth, nowDay - nowDayOfWeek + (num * 7))
      var weekEndDate = new Date(nowYear, nowMonth, nowDay + (6 - nowDayOfWeek) + (num * 7))
      return this.weekFormatDate(weekStartDate) + '-' + this.weekFormatDate(weekEndDate)
    },

    // 格式化日期
    weekFormatDate (date) {
      var mymonth = date.getMonth() + 1
      var myweekday = date.getDate() + 1 // 从周一开始
      return (mymonth + '/' + myweekday)
    },

    // 月份（num:0 本月 num:-1 上月 num:1 下月）
    printMonth (num) {
      var now = new Date()
      var nowDay = now.getDate()
      var nowMonth = now.getMonth()
      var nowYear = now.getYear()
      nowYear += (nowYear < 2000) ? 1900 : 0
      var monthDate = new Date(nowYear, nowMonth + num, nowDay)
      return this.monthFormatDate(monthDate)
    },

    // 格式化日期
    monthFormatDate (date) {
      var mymonth = date.getMonth() + 1
      return (mymonth + '月')
    },

    nav (i) {
      this.navIndex = i
    },

    // 日、周、月
    timeNav (i) {
      this.timeNavIndex = i
      if (i === 1) {
        this.myChartInit('day')
      } else if (i === 2) {
        this.myChartInit('week')
      } else if (i === 3) {
        this.myChartInit('month')
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import "../../style/mixin.less";

.dataShow {
  .fs;
  z-index: 11;
  background-color: #fff;
  .dataShow_container {
    .fs;
    .dataShow_top {
      .wh(100%, 64%);
      background: linear-gradient(#4da8ff, #26d0fe);
      .head {
        .wh(100%, 50px);
        .head-left {
          .wh-f(30%, 100%);
          .back {
            .wh(40px, 100%);
            line-height: 50px;
            margin-left: 0.2rem;
            img {
              height: 20px;
              vertical-align: middle;
            }
          }
        }
        @navHeight: 28px;
        .head-center {
          .wh-f(40%, 100%);
          text-align: center;
          position: relative;
          .nav {
            .wh-l(150px, @navHeight);
            border-radius: @navHeight / 2;
            font-size: 18px;
            color: #fff;
            border: 1px solid #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-left: -75px;
            margin-top: -@navHeight / 2;
            li {
              .wh(50%, 101%);
              float: left;
              border-radius: @navHeight / 2;
            }
            li:nth-child(1) {
              position: relative;
              left: -1%;
            }
            li:nth-child(2) {
              position: relative;
              right: -1%;
            }
            .navActive {
              background: #fff;
              color: #4aaafe;
            }
          }
        }
        .head-right {
          .wh-f(30%, 100%);
        }
      }
      .table {
        .wh(100%, calc(~"100% - 50px"));
        padding-top: 18px;
        .time {
          .wh(276px, 40px);
          margin: 0 auto;
          li {
            .wh-l(40px, 40px);
            float: left;
            border-radius: 50%;
            font-size: 18px;
            color: #fff;
            margin: 0 25px;
          }
          .timeAvtive {
            background: #fff;
            color: #4aaafe;
          }
        }
        .table_container {
          .wh(100%, calc(~"100% - 40px"));
          .myChart {
            .wh(100%, 100%);
          }
        }
      }
    }
    .dataShow_bottom {
      .wh(100%, 36%);
      .describe {
        .wh-l(100%, 78px);
        font-size: 18px;
        padding: 0 20px;
        span {
          float: left;
        }
        strong {
          float: right;
        }
      }
      .value {
        font-size: 70px;
      }
    }
  }
}
</style>



