<template>
  <div class="scroll" ref='wrapper'>
    <div class="content" :style="{top: pullDownStyle + 'px', height: contentHeight}" ref="content">
      <slot></slot>
      <div class="top_tip" v-if="pullRefreshObj.pulldown">
        <div class="before_trigger" v-if="beforePullDown">
          <bubble :y="bubbleY"></bubble>
        </div>
        <div class="after_trigger" v-else>
          <div v-if="pullRefreshObj.finishPullDown" class="msg_txt">刷新完成</div>
          <div class="msg_icon" v-else>
            <loading></loading>
          </div>          
        </div>
      </div>
      <div class="bottom_tip" v-if="pullRefreshObj.pullup">
        <div class="before_trigger" v-if="pullRefreshObj.finishPullUp">
          <span v-if="!pullRefreshObj.noMoreData">加载更多</span>
          <span v-if="pullRefreshObj.noMoreData">没有更多数据了</span>
        </div>
        <div class="after_trigger" v-else>
          <div class="msg_icon">
            <loading></loading>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import Bubble from './components/bubble'
import Loading from './components/loading'
export default {
  props: {
    /**
     * 1 滚动的时候会派发scroll事件会截流。
     * 2 滚动的时候实时派发scroll事件不会截流。
     * 3 除了实时派发scroll事件在swipe的情况下仍然能实时派发scroll事件 */
    probeType: {
      type: Number,
      default: 2
    },
    /** * 点击列表是否派发click事件 */
    click: {
      type: Boolean,
      default: true
    },
    /** * 是否开启横向滚动 */
    scrollX: {
      type: Boolean,
      default: false
    },
    /** * 是否派发滚动事件 */
    listenScroll: {
      type: Boolean,
      default: false
    } /** * 列表的数据 */,
    data: {
      type: Array,
      default: null
    },
    /** * 是否派发顶部下拉的事件用于下拉刷新、上拉加载 */
    pullRefreshObj: {
      type: Object,
      default: () => {
        return {
          pulldown: false,
          finishPullDown: true,
          pullup: false,
          finishPullUp: true,
          noMoreData: false
        }
      }
    },
    /** * 是否派发列表滚动开始的事件 */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /** * 当数据更新后刷新scroll的延时。 */
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  data () {
    return {
      beforePullDown: true, // 下拉前
      isPullUpLoad: false, // 是否上拉加载
      bubbleY: 0,
      pullDownStyle: 0, // 下拉停留位置
      contentHeight: 'auto' // 高度（随数据变化）
    }
  },
  components: {
    Bubble,
    Loading
  },
  mounted () {
    // console.log(this.$refs.wrapper.clientHeight)
    // console.log(this.$refs.content.clientHeight)
    // 保证在DOM渲染完毕后初始化better-scroll
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  methods: {
    _initScroll () {
      if (!this.$refs.wrapper) {
        return
      }
      // better-scroll的初始化
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX
      })
      // 是否派发滚动事件
      if (this.listenScroll) {
        this.scroll.on('scroll', pos => {
          // 动态改变下拉样式
          if (this.pullRefreshObj.pulldown) {
            this.bubbleY = Math.max(0, pos.y - 50)
            // console.log(pos.y)
          }
          this.$emit('scroll', pos)
        })
      }
      // // 是否派发滚动到底部事件
      // if (this.pullRefreshObj.pullup) {
      //   this.scroll.on('scrollEnd', () => {
      //     // 滚动到底部
      //     if (this.scroll.y <= this.scroll.maxScrollY) {}
      //   })
      // }
      // 是否派发顶部下拉事件用于下拉刷新、上拉加载
      if (this.pullRefreshObj.pulldown || this.pullRefreshObj.pullup) {
        this.scroll.on('touchEnd', pos => {
          // console.log(this.scroll)
          // console.log(pos.y)
          // 下拉动作
          if (pos.y > 50) {
            this.$emit('pulldown')
            this.pullDownStyle = Math.min(pos.y - 50, 40)
            this.beforePullDown = false
            this.pullRefreshObj.finishPullDown = false
            this.initPullUp()
          } else if (pos.y < this.scroll.maxScrollY - 30) {
            // 是否数据未加载
            if (!this.pullRefreshObj.noMoreData) {
              this.pullRefreshObj.finishPullUp = false
              this.initPullDown()
              this.$emit('pullup')
            }
          }
        })
      }
      // 是否派发列表滚动开始的事件
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    initPullDown () {
      this.bubbleY = 0 // 初始canvas
      this.beforePullDown = true  // 初始下拉前
      this.pullDownStyle = 0  // 初始下拉距离
    },
    initPullUp () {
      this.pullRefreshObj.noMoreData = false // 初始没有更多数据
      this.pullRefreshObj.finishPullUp = true // 初始下拉完成
    },
    disable () {
      // 代理better-scroll的disable方法
      this.scroll && this.scroll.disable()
    },
    enable () {
      // 代理better-scroll的enable方法
      this.scroll && this.scroll.enable()
    },
    refresh () {
      // 代理better-scroll的refresh方法
      this.scroll && this.scroll.refresh()
    },
    scrollTo () {
      // 代理better-scroll的scrollTo方法
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement () {
      // 代理better-scroll的scrollToElement方法
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },
  watch: {
    // 监听数据的变化延时refreshDelay时间后调用refresh方法重新计算保证滚动效果正常
    data () {
      this.contentHeight = 'auto'
      setTimeout(() => {
        this.refresh()
        if (this.$refs.content.clientHeight < this.$refs.wrapper.clientHeight) {
          this.contentHeight = '101%'
        } else {
          this.contentHeight = 'auto'
        }
        // console.log(this.$refs.content.clientHeight)
        // console.log(this.$refs.wrapper.clientHeight)
      }, this.refreshDelay)
    },

    'pullRefreshObj.finishPullDown': function (newVaule) {
      if (newVaule) {
        setTimeout(() => {
          this.initPullDown()
          this.refresh()
        }, 500)
      }
    }
  }
}
</script>

<style scoped lang="less">
@import '../../style/mixin.less';
.scroll{
  .wh(100%, 100%);
  .content{
    .wh(100%, auto);
    padding: 15px 0 40px 0;
    position: relative;
    .top_tip{
      .wh-l(100%, 60px);
      position: absolute;
      left: 0;
      top: -60px;
      z-index: -1;
      font-size: 14px;
      .after_trigger{
        .wh(100px, 20px);
        margin: 0 auto;
        position: relative;
        top: 40px;
        .msg_icon{
          .wh(20px, 20px);
          margin: 0 auto;
          img{
            width: 100%;
          }
        }
        .msg_txt{
          .wh(100%, 100%);
          line-height: normal;
        }        
      }
    }
    .bottom_tip{
      .wh-l(100%, 50px);
      position: absolute;
      left: 0;
      bottom: 0px;
      z-index: -1;
      font-size: 14px;
      .after_trigger{
        .wh(100px, 20px);
        margin: 0 auto;
        position: relative;
        top: 20px;
        .msg_icon{
          .wh(20px, 20px);
          margin: 0 auto;
          img{
            width: 100%;
          }
        }
      }
    }
  }
}
</style>



