<template>
  <div class="addsuccess">
       <head-top title="添加失败" goBack="true" ></head-top>
       <div class="contactimg">
         <img src="../../image/adddirver/iphonedefeated@3x.png" >
       </div>  
       <div class="prompt"> 
           <p>手机未连接Wifi网络</p>
           <p class="tit">请到手机设置页面进行Wifi连接</p>
       </div>  
       <div class="nextbtn" @click="next">
           <span>完成</span>
       </div>
  </div>
</template>
<script>
import { } from 'vuex'
import headTop from '../../components/header/header'

export default {
  data () {
    return {time: '3:00'
    }
  },
  created () {

  },
  mounted () {

  },

  components: {
    headTop
  },

  computed: {},

  methods: {
    next () {
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .addsuccess{
   .fs;
   background: #ffffff;
   .contactimg{
     .wh(100%,auto);
     margin-top:46px;
     display: flex;
     flex-direction: column;
     align-items: center;
     margin-bottom: 60px;
     img{
      .wh(260px,80px)
     }
    }
   .prompt{
        width:100%;
        text-align: center;
        p{
            color: #000;
            margin-top: 18px;
            font-size: 18px;
        }
        .tit{
            color: #b7b7b7;
            margin-top: 23px;
        }
    }
     .nextbtn{
      .wh-l(240px,40px);
      margin: 0 auto;
      margin-top: 180px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      span{
          font-size: 17px;
          color: #fff;
        }
    }
  }
 </style>


