import Vue from 'vue'

/**
 * UNITSIZE
 * 字节大小单位转化为M、G
 */

Vue.filter('unitSize', function (value) {
  if (!value) { return '0M' }
  if (value / 1024 < 1) {
    return value + 'B'
  } else if (value / 1024 / 1024 < 1) {
    return (value / 1024).toFixed(2) + 'K'
  } else if (value / 1024 / 1024 / 1024 < 1) {
    return (value / 1024 / 1024).toFixed(2) + 'M'
  } else if (value / 1024 / 1024 / 1024 / 1024 < 1) {
    return (value / 1024 / 1024 / 1024).toFixed(2) + 'G'
  }
})

/**
 * DATEFORMAT
 * 日期格式化(YMDH、MDH、MD)
 */

Vue.filter('dateFormat', function (value, type) {
  if (!value) { return '' }
  let date = new Date(value)
  let y = date.getFullYear()
  let m = date.getMonth() + 1
  let d = date.getDate()
  let h = date.getHours()
  let mm = date.getMinutes()
  switch (type) {
    case 'YMDH':
      let ymdhString = y + '-' + m + '-' + d + ' ' + h + ':' + mm
      return ymdhString
    case 'MDH':
      let mdhString = m + '-' + d + ' ' + h + ':' + mm
      return mdhString
    case 'MD':
      let mdString = m + '-' + d
      return mdString
  }
})



