/**
 * MOCKJS
 * 模拟数据生成器
 */
import Mock from 'mockjs'

export const timing = Mock.mock('/timing', {
  'data|1-15': [{
    'time': '@time("H : mm")',
    'time2': '12 ：12',
    'swicth|1': true,
    'week|1-100': 1,
    'state|1': false,
    'id|+1': 1
  }]
})

export const equipment = Mock.mock('/equipment', {
  'data': {
    'name': '@pick(["客厅的插座", "卧室的插座", "厨房的插座", "阳台插座"])',
    'mac': '@id()',
    'state': 0,
    'signal': '@pick([1, 3, 2])'
  }
})

export const socketStatus = Mock.mock('/socketStatus', {
  'data': '{"power": {"value":2422,"averageValue": 1258, "maximumValue": 3410},"voltage": 22.3,"electricity": 22.4,"frequency":22.5,"temperature": {"value": 21.555555,"alarmValue": 100},"humidity": {"value": 22,"alarmValue": 60},"weight": 23,"cost": 166, "countDownType": "ON", "time": 360000}'
})



