<template>
  <div class="forgot">
    <div class="header">
      <div class="header-left">
        <div class="back" @click="back">
          <img src="../../image/global/back.png" alt="">
        </div>
      </div>
      <div class="header-center">
       Forgot Password
      </div>
    </div>
    <div class="content">
      <div class="input">
        <input type="email" placeholder="Email" v-model="forgotEmail">
      </div>
      <button class="found_button"  :class="{foundActive: validate.foundActive}" :disabled="!validate.foundActive" @click="found">
        <span v-show="!founding">Found</span>
        <span v-show="founding" class="founding">
          <img src="../../image/adddirver/Loadingcirclesfreeicon.png" alt="">
          Founding...
        </span>
      </button>  
    </div>
    <transition :name="transitionName" mode="in-out">
      <router-view></router-view>
    </transition>
    <toast :describe="forgotEmail + ' is  not registered,please sign up first !'" v-if="ToastType==='notRegistered'" position="bottom"></toast>
    <toast describe="Email format error, please enter the correct Email !" v-if="ToastType==='formatError'" position="bottom"></toast>
    <toast describe="We have sent a confirmation email to abc@example.com, please check!" v-if="ToastType==='success'" position="bottom"></toast>
    <toast describe="undefined!" v-if="ToastType==='emailForgotFailed'" position="bottom"></toast>
  </div>
</template>
<script>
import toast from '../../components/common/toast'
import { emailForgotRequest } from '../../service/method.js'
import { emailForgotResponse } from '../../service/getData.js'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      forgotEmail: '',
      founding: false,
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0 // 偏移量（当软键盘遮住输入框偏移）
    }
  },
  component: {
    toast
  },
  computed: {
    ...mapState([
      'ToastType'
    ]),
    validate () {
      let vEmail = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(this.forgotEmail)
      return {
        foundActive: vEmail
      }
    }
  },
  mounted () {
      // 初始化延时，否则fullHeight值为0
    setTimeout(() => {
      let h = 0
      this.fullHeight = document.documentElement.clientHeight
      window.onresize = () => {
        h = document.documentElement.clientHeight
        // 软键盘是否弹起
        if (h < this.fullHeight) {
          this.keyboard = true
        } else {
          this.keyboard = false
        }
        // 解决小屏幕遮挡输入框
        if (h < 340) {
          this.offsetTop = 340 - h
        } else {
          this.offsetTop = 0
        }
      }
    }, 100)
    emailForgotResponse(this.NAVIGATOR, this)
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    found () {
      this.founding = true
      emailForgotRequest(this.NAVIGATOR, this, this.forgotEmail)
      if (this.$root.ISDEBUG) {
        setTimeout(() => {
          this.founding = false
          this.$router.push({path: '/login/emailLogin/forgot/success'})
          window.emailForgotResponse(false, '0x801804')
        }, 2000)
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../style/mixin.less";
.forgot{
  .fs;
  background: #ffffff;
  z-index: 999;
  .header{
    .wh-l(100%, 1.333333rem);
    background: @themeColor;
    font-size: .48rem;
    color: #F5FAFF;
    .header-left{
      .wh-f(10%, 100%);
      .back{
        .wh(1.066667rem, 100%);
        margin-left: .2rem;
        img{
          height: .533333rem;
          vertical-align: middle;
        }
      }
    }
    .header-center{
      .wh-f(90%, 100%);
      text-align: center;
    }
  }
  .content{
    .input{
      .wh-l(280px, 40px);
      margin: 20px auto;
      border-bottom: 1px solid #ccc;
      text-align: left;
      margin-bottom: 40px;
      input{
        font-size: 16px;
      }
    }
  }
  .found_button{
    width: 280px;
    height: 40px;
    color: #fff;
    background: #C7C7C7;
    margin: 20px auto;
    // border-radius: 120px;
    box-shadow: 0px 3px 11px 1px #d2d2d2; /* no */
    font-size: 16px;
    .founding{
      .wh(100%, 100%);
      line-height: 40px;
      display: block;
      font-size: 17px;
      color: #fff;
      vertical-align: middle;
      img{
        height: 17px;
        position: relative;
        top: 2px;
        left: -5px;
        transform-origin: center;
        animation: rotate 1s infinite;
      }
    }
  }
  .foundActive{
    background: @themeColor;
    box-shadow: 0px 3px 11px 1px #A3CFFE; /* no */
  }
}
</style>




