<template>
  <div class="setTime">
    <head-top :title="$t('addPage.language')" goBack="true"></head-top>
    <div class="setTime_container">
      <ul class="item">
        <div class="top-pixel"></div>
        <li v-for="item in langArray">
          <a @click="checkRadio(item)">
            {{item.lang}}
            <img v-show="item.state" src="../../image/login/right.png" alt="">
            <img v-show="!item.state" src="../../image/login/cricle.png" alt="">
          </a>
          <div class="bom-pixel"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Radio } from 'mint-ui'
import headTop from '../../components/header/header'
import { setSystemLanguageRequest } from '../../service/method.js'
export default {
  data () {
    return {
      Timer: null,
      langArray: [
        {
          lang: this.$t('addPage.chinese'),
          value: 'zh',
          state: false
        },
        {
          lang: this.$t('addPage.english'),
          value: 'en',
          state: false
        }
      ]
    }
  },

  mounted () {
    // 勾选初始化
    this.langArray.forEach((value) => {
      if (value.value === this.$i18n.locale) {
        value.state = true
      }
    })
    // commonPatternListDataResponse(this.NAVIGATOR, this)
    // if (this.$root.ISDEBUG) {
    //   setSystemLanguageResponse(true, 'en')
    //   setTimeout(() => {
    //     console.log(this)
    //     window.commonPatternListDataResponse(this.currentDevice.mac, '[{"id":14,"time":"10:42","switch":false,"week":96,"state":false}]', '')
    //   })
    // }
  },

  components: {
    headTop,
    Radio
  },

  computed: {...mapState(['currentDevice'])},

  methods: {
    checkRadio (item) {
      this.langArray.forEach((value) => {
        if (value.value === item.value) {
          // console.log(item.value)
          value.state = true
          // 设置系统语言
          setSystemLanguageRequest(this.NAVIGATOR, this, item.value)
          if (this.$root.ISDEBUG) {
            this.$i18n.locale = item.value
            this.$router.go(-1)
          }
        } else {
          value.state = false
        }
      })
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.setTime{
  .fs;
  background: #fff;
  z-index: 99;
  .setTime_container{
    width: 100%;
    height: calc(~"100% - 50px");
    padding-top: 10px;
    .mtRadio{
      text-align: left;
    }
    .item{
      .wh(100%, auto);
      position: relative;
      li{
        .wh-l(100%, 48px);
        position: relative;
        a{
          .wh(100%, 100%);
          display: block;
          text-align: left;
          font-size: 16px;
          padding: 0 25px;
          img{
            height: 32px;
            position: absolute;
            top: 6px;
            right: 14px;
          }
        }
      }
    }
  }
}

</style>



