<template>
  <div class="cost">
    <head-top goBack="true" :title="$t('costPage.spendingCountdown')">
      <router-link to="/main/cost/data" slot="setRight" class="data">
        <img src="../../image/cost/Analytics-icon.png">
      </router-link>
      <!-- <div slot="setRight" class="data">
        <img src="../../image/cost/Analytics-icon.png">
      </div> -->
    </head-top>
    <div class="cost_container">
      <ul class="timing_nav">
         <li>
           <div class="nav_button" :class="tabActive=='tab-container1'?'navActive':''" @click="navSwicth(1)">
             <span>{{$t('costPage.power')}}</span>
             <strong v-if="tabActive=='tab-container1'"></strong>
           </div>
         </li>
         <li>
           <div class="nav_button" :class="tabActive=='tab-container2'?'navActive':''" @click="navSwicth(2)">
             <span>{{$t('costPage.costspent')}}</span>
             <strong v-if="tabActive=='tab-container2'"></strong>
           </div>
         </li>
      </ul>

      <mt-tab-container v-model="tabActive" :swipeable="false" class="main_column">
        <!-- 电量 -->
        <mt-tab-container-item id="tab-container1">
          <div class="tab_column">
            <div class="set_top">
              <div class="circle customHeight">
              <span>{{$t('costPage.powerConsumption')}}</span>
                <span>
                  <strong>{{electricityValue}}</strong>
                  <em>Kwh</em>
                </span>
                <span>{{eAlarmValue}} Kwh</span>
                <span >{{$t('costPage.powerwarning')}}</span>
                <span >
                  <em @click="setValue(1)" :style="{'background':!eStartActive?'':'grey'}">{{$t('costPage.setpowerwarn')}}</em>
                </span>
                <!-- <div class="progress">
                  <svg xmlns="http://www.w3.org/200/svg" :height="260/37.5 + 'rem'" :width="280/37.5 + 'rem'">
                    <circle ref="spend" class="circle" :cx="144/37.5+'rem'" :cy="127/37.5+'rem'" :r="122.6/37.5+'rem'" fill="none" stroke="#4EA3FE" stroke-width=".373333rem" :stroke-dasharray="tStrokeDasharray" stroke-linecap="round"/>
                  </svg>
                </div> -->
                <div class="progress">
                  <svg xmlns = "http://www.w3.org/200/svg" height="260px" width="280px">
                    <circle class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round"/>
                  </svg>
                  <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px" class="radius_bar">
                    <circle ref="countdown" class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#4EA3FE" stroke-width="13px" :stroke-dasharray="tStrokeDasharray" stroke-linecap="round"/>
                  </svg>
                </div>
              </div>
            </div>
            <div class="set_bottom">
              <div class="time">
                <ul class="time_column cost_colum">
                  <!-- {{$t('timerPage.startTime')}} -->
                  <li>开始时间</li>
                  <li>本地电价 </li>
                </ul>
                <ul class="time_column cost_colum">
                  <!-- @click="setDate(1)"  选择开始时间窗-->
                  <li >{{eStartTime}}</li>
                  <li>{{electLocal}}</li>
                </ul>
              </div>

              <div class="describe">
                <span class="red" v-if="!eStartActive">{{$t('costPage.powerNotActive')}}</span>
                <span v-if="eStartActive">{{$t('costPage.powerActive')}}</span>
              </div>
            </div>

            <div class="themeButton bom_position" v-if="!eStartActive">
              <span @click="buttonOperation(1)">{{$t('commonPage.run')}}</span>
            </div>
            <div class="themeButton_red bom_position" v-if="eStartActive">
              <span @click="buttonOperation(2)">{{$t('commonPage.end')}}</span>
            </div>
          </div>
        </mt-tab-container-item>

        <!-- 花费 -->
        <mt-tab-container-item id="tab-container2">
          <div class="tab_column">
            <div class="set_top">
              <div class="circle customHeight">
                <span>{{$t('costPage.costSpent')}}</span>
                <span>
                  <strong>{{spendValue}}</strong>
                <em>{{montaryUintType}}</em>
                </span>
                <span>{{sAlarmValue}}{{montaryUintType}}</span>
                <span>{{$t('costPage.costwarning')}}</span>
                <span>
                  <em @click="setValue(2)" :style="{'background':!sStartActive?'':'grey'}">{{$t('costPage.setcostwarn')}}</em>
                </span>
                <!-- <div class="progress">
                  <svg xmlns="http://www.w3.org/200/svg" :height="260/37.5 + 'rem'" :width="280/37.5 + 'rem'">
                    <circle ref="spend" class="circle" :cx="144/37.5+'rem'" :cy="127/37.5+'rem'" :r="122.6/37.5+'rem'" fill="none" stroke="#4EA3FE" stroke-width=".373333rem" :stroke-dasharray="hStrokeDasharray" stroke-linecap="round"/>
                  </svg>
                </div> -->
                <div class="progress">
                  <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px">
                    <circle class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round"/>
                  </svg>
                  <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px" class="radius_bar">
                    <circle ref="countdown" class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#4EA3FE" stroke-width="13px" :stroke-dasharray="hStrokeDasharray" stroke-linecap="round"/>
                  </svg>
                </div>
              </div>
            </div>
            <div class="set_bottom">
              <div class="time" >
                <ul class="time_column cost_colum">
                  <!-- {{$t('timerPage.startTime')}} -->
                  <li>开始时间</li>
                  
                  <li>本地电价</li>
                </ul>
                <ul class="time_column cost_colum">
                  <li @click="setDate(3)">{{sStartTime}}</li>
                  
                  <li>{{electLocal}}</li>
                </ul>
              </div>
              
              <div class="describe">
                <span class="red" v-if="!sStartActive">{{$t('costPage.costNotActive')}}</span>
                <span v-if="sStartActive">{{$t('costPage.costActive')}}</span>
              </div>
            </div>

            <div class="themeButton bom_position" v-if="!sStartActive">
              <span @click="buttonOperation(3)">{{$t('commonPage.run')}}</span>
            </div>
            <div class="themeButton_red bom_position" v-if="sStartActive">
              <span @click="buttonOperation(4)">{{$t('commonPage.end')}}</span>
            </div>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>

    <!-- 弹窗 -->
    <!-- 电量告警 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='eAlarmValueOpreation'">
      <div slot="pormptColumn" class="common_alert cost-center">
        <div class="alert_title">{{$t('messageBox.enterTargetCountdown')}}</div>
        <div class="alert_container">
          <div class="alert_cost">
            <input type="number" ref="eAlarmValueTemp" :class="eAlarmValueFlag?'':'error'" @input="regexpnum(1)">
            <span>Kwh</span>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(1)" :style="{'background':disabledeter?'':'grey'}">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 结束电量警告 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='eAlarmEndOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.quitTargetCountdown')}}</div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(2)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 电费告警进行 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='eAlarmRunningOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.targetCountdownRunning')}}</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)">{{$t('messageBox.confirm')}}</div>
        </div>
      </div>
    </messageBox>

    <!-- 输入花费值 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='sAlarmValueOpreation'">
      <div slot="pormptColumn" class="common_alert cost-center" >
        <div class="alert_title">{{$t('messageBox.enterTargetSpending')}}</div>
        <div class="alert_container">
          <div class="alert_cost">
            <input type="number" ref="sAlarmValueTemp" :class="sAlarmValueFlag?'':'error'" @input="regexpnum(2)">
            <span>{{montaryUintType}}</span>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(3)" :style="{'background':disabledeter?'':'grey'}">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 结束花费告警 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='sAlarmEndOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.quitTargetSpending')}}</div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(4)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 花费告警进行 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='sAlarmRunningOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.targetSpendingRunning')}}</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)">{{$t('messageBox.confirm')}}</div>
        </div>
      </div>
    </messageBox>

    <!-- 选择时间 -->
    <messageBox setHeight="290" setType="pormpt" :scroll="{data: pickerData, wheels: wheels}" v-show="messageBoxType==='setDateOpreation'">
      <div slot="pormptColumn" class="common_alert" v-show="wheelState">
        <div class="alert_title">{{$t('messageBox.chooseDate')}}</div>
        <div class="alert_container">
          <div class="scroll_column" ref="wheelWrapper">
            <div class="wrapper scroll_width" v-for="(data, index) in pickerData">
              <ul class="wheel-scroll">
                <li class="wheel-item" v-for="item in data">{{item}}</li>
              </ul>
              <div class="mask_top"></div>
              <div class="mask_bottom"></div>
              <div class="describe describe_year" v-if="index===0">
                {{$t('messageBox.year')}}
              </div>
              <div class="describe" v-if="index===1">
                {{$t('messageBox.month')}}
              </div>
              <div class="describe" v-if="index===2">
                {{$t('messageBox.day')}}
              </div>
            </div>
          </div>
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(5)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(6)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div> 
      </div>
    </messageBox>

    <transition :name="transitionName">
      <router-view></router-view>
    </transition>

  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../components/header/header'
import messageBox from '../../components/common/messageBox'
import { createPicker, pickerSave, pickerCancel } from '../../config/utils'
import {
  spendingCountdownDataRequest,
  spendingCountdownAlarmRequest} from '../../service/method'
import {
  spendingCountdownDataResponse,
  spendingCountdownAlarmResponse} from '../../service/getData'
let data1 = []
let data2 = ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12']
let data3 = []

for (var i = 2018; i < 2100; i++) {
  data1.push(i.toString())
}

export default {
  data () {
    return {
      tabActive: 'tab-container1', // 导航栏切换
      radius: 119, // svg进度条半径
      electricityValue: 30, // 电量值
      eAlarmValue: 0, // 电量告警值
      eAlarmValueTemp: 0, // 输入框电量告警值
      eAlarmValueFlag: true, // 电量告警值验证
      eStartActive: false, // 电量告警启动结束状态
      eStartTime: this.getTime(), // 电量告警开始时间
      spendValue: 80, // 花费值
      sAlarmValue: 0, // 花费告警值
      sAlarmValueTemp: 0, // 输入框花费告警值
      sAlarmValueFlag: true, // 花费告警值验证
      sStartActive: false, // 花费告警启动结束状态
      sStartTime: this.getTime(), // 花费告警开始时间
      setDateType: null, // 设置日期类型 1、2、3、4
      pickerData: [
        data1,
        data2,
        data3
      ], // 选择器列数据
      wheels: null, // 选择器实例化数组
      wheelState: false // 选择器显示隐藏（避免数据延迟，造成选择器无法滚动到指定的selectedIndex）
    }
  },

  mounted () {
    // 定量定费数据请求
    spendingCountdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    spendingCountdownDataResponse(this.NAVIGATOR, this)
    // 定量定费告警设置返回
    spendingCountdownAlarmResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      window.spendingCountdownDataResponse(this.currentDevice.mac, true, '{"power":{"currentValue":100,"alarmValue":50,"alarmSwitch":true,"year":2018,"month":6,"day":6},"cost":{"currentValue":30,"alarmValue":40,"alarmSwitch":false,"year":2018,"month":1,"day":16}}')
    }
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'ToastType', 'messageBoxType', 'electLocal', 'disabledeter', 'montaryUint', 'currentDevice'
    ]),
    montaryUintType () {
      let type = this.montaryUint
      let str = ''
      switch (type) {
        case 1:
          str = '元'
          break
        case 2:
          str = '英镑'
          break
        case 3:
          str = '美元'
          break
        case 4:
          str = '欧元'
          break
        case 5:
          str = '日元'
          break
        default:
          break
      }
      return str
    },
    tStrokeDasharray () {
      return Math.floor(2 * Math.PI * this.radius) * (this.electricityValue / 100) * 280 / 360 + ',10000'
    },
    hStrokeDasharray () {
      return Math.floor(2 * Math.PI * this.radius) * (this.spendValue / 100) * 280 / 360 + ',10000'
    },
    startTime () {
      let esTime = this.eStartTime.split('-') // 定电量开始时间
      let ssTime = this.sStartTime.split('-') // 定花费开始时间
      let eyear, emonth, eday, syear, smonth, sday
      eyear = parseInt(esTime[0])
      emonth = parseInt(esTime[1])
      eday = parseInt(esTime[2])
      syear = parseInt(ssTime[0])
      smonth = parseInt(ssTime[1])
      sday = parseInt(ssTime[2])
      return {eyear, emonth, eday, syear, smonth, sday}
    }
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    navSwicth (num) {
      this.tabActive = 'tab-container' + num
    },
    getTime () {
      let d = new Date()
      return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
    },
    // 设置告警温湿度值
    setValue (num) {
      switch (num) {
        case 1:
          if (!this.eStartActive) {
            if (this.eAlarmValue) {
              this.$store.state.disabledeter = true
            }
            this.eAlarmValueFlag = true
            this.eAlarmValueTemp = this.eAlarmValue
            this.MESSAGEBOX_SWITCH({type: 'eAlarmValueOpreation'})
          }
          break
        case 2:
          if (!this.sStartActive) {
            if (this.sAlarmValue) {
              this.$store.state.disabledeter = true
            }
            this.sAlarmValueFlag = true
            this.sAlarmValueTemp = this.sAlarmValue
            this.MESSAGEBOX_SWITCH({type: 'sAlarmValueOpreation'})
          }
          break
      }
    },

    // 弹框
    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          if (this.disabledeter) {
            this.eAlarmValueFlag = true
            this.eAlarmValue = this.eAlarmValueTemp
            this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          } else {
            this.$store.state.ToastType = 'costTip'
          }
          break
        case 2:
          spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 1, this.eAlarmValue, false, this.startTime.eyear, this.startTime.emonth, this.startTime.eday)
          console.log(this.currentDevice.mac, 1, this.eAlarmValue, false, this.startTime.eyear, this.startTime.emonth, this.startTime.eday)
          if (this.$root.ISDEBUG) {
            window.spendingCountdownAlarmResponse(this.currentDevice.mac, true, false, 1)
          }
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 3:
          if (this.disabledeter) {
            this.sAlarmValueFlag = true
            this.sAlarmValue = this.sAlarmValueTemp
            this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          } else {
            this.$store.state.ToastType = 'costTip'
          }
          break
        case 4:
          spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 2, this.sAlarmValue, false, this.startTime.syear, this.startTime.smonth, this.startTime.sday)
          console.log(this.currentDevice.mac, 2, this.sAlarmValue, false, this.startTime.syear, this.startTime.smonth, this.startTime.sday)
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 5:
          // 禁用选择器
          pickerCancel(this)
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 6:
          console.log(this.setDateType)
          switch (this.setDateType) {
            case 1:
              this.eStartTime = this.getSelectDate()
              break
            // case 2:
            //   // this.eEndTime = this.getSelectDate()
            //   break
            case 3:
              this.sStartTime = this.getSelectDate()
              break
            // case 4:
            //   this.sEndTime = this.getSelectDate()
            //   break
          }
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
      }
    },

    // 日期选择
    // 计算月份天数、data3数据更新
    countDay (y, m) {
      let countDay = (new Date(y, m, 0)).getDate()
      for (var i = 1; i <= countDay; i++) {
        if (i < 10) {
          i = '0' + i
        } else {
          i = i.toString()
        }
        data3.push(i)
      }
    },

    getSelectDate () {
      let array = pickerSave(this)
      let y = parseInt(array[0])
      let m = parseInt(array[1])
      let d = parseInt(array[2])
      if (m < 10) {
        m = '0' + m
      }
      if (d < 10) {
        d = '0' + d
      }
      return y + '-' + m + '-' + d
    },

    _initWheel (date) {
      let array = date.split('-')
      let y = parseInt(array[0])
      let m = parseInt(array[1])
      let d = parseInt(array[2])
      let selecteArray = [y - 2018, m - 1, d - 1]

      // 动态更新日期天数（data3）
      this.countDay(y, m)

      // 为date时，为日期轮子创建滚动监听
      createPicker(this, selecteArray, 'date')
    },

    setDate (num) {
      this.setDateType = num
      this.wheelState = true
      switch (num) {
        case 1:
          this._initWheel(this.eStartTime)
          break
        case 2:
          this._initWheel(this.eEndTime)
          break
        case 3:
          this._initWheel(this.sStartTime)
          break
        case 4:
          // this._initWheel(this.sEndTime)
          break
      }
      this.MESSAGEBOX_SWITCH({ type: 'setDateOpreation' })
    },

    // 年份、月份滚动监听
    scrollEnd (i) {
      let array = pickerSave(this)
      let y = parseInt(array[0])
      let m = parseInt(array[1])

      // 动态改变轮子的日期范围
      data3 = []
      if (i === 0) {
        this.countDay(y, 1)
        this.wheels[1].wheelTo(0)
        this.wheels[2].wheelTo(0)
      } else if (i === 1) {
        this.countDay(y, m)
        this.wheels[2].wheelTo(0)
      }
      this.pickerData.splice(2, 1, data3)
    },

    // 启动、结束
    buttonOperation (num) {
      switch (num) {
        case 1:
          if (this.eAlarmValue === 0 && (this.eStartTime === '' || this.eEndTime === '')) {
            this.$store.state.ToastType = 'setElectricityAndTime'
            return false
          } else if (this.eAlarmValue === 0) {
            this.$store.state.ToastType = 'setElectricity'
            return false
          } else if (this.eStartTime === '') {
            this.$store.state.ToastType = 'setCostTime'
            return false
          }
          spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 1, this.eAlarmValue, true, this.startTime.syear, this.startTime.smonth, this.startTime.sday)
          console.log(this.currentDevice.mac, 1, this.eAlarmValue, true, this.startTime.eyear, this.startTime.emonth, this.startTime.eday)
          break
        case 2:
          this.MESSAGEBOX_SWITCH({type: 'eAlarmEndOpreation'})
          break
        case 3:
          if (this.sAlarmValue === 0 && (this.sStartTime === '' || this.sEndTime === '')) {
            this.$store.state.ToastType = 'setSpendAndTime'
            return false
          } else if (this.sAlarmValue === 0) {
            this.$store.state.ToastType = 'setSpend'
            return false
          } else if (this.sStartTime === '') {
            this.$store.state.ToastType = 'setCostTime'
            return false
          }
          spendingCountdownAlarmRequest(this.NAVIGATOR, this, this.currentDevice.mac, 2, this.sAlarmValue, true, this.startTime.syear, this.startTime.smonth, this.startTime.sday)
          console.log(this.currentDevice.mac, 2, this.sAlarmValue, true, this.startTime.syear, this.startTime.smonth, this.startTime.sday)
          if (this.$root.ISDEBUG) {
            window.spendingCountdownAlarmResponse(this.currentDevice.mac, true, true, 2)
          }
          break
        case 4:
          this.MESSAGEBOX_SWITCH({type: 'sAlarmEndOpreation'})
          break
      }
    },
    regexpnum (num) { // val-->输入框获取的值 num-->1电量 2花费
      let val
      switch (num) {
        case 1 :
          val = this.$refs.eAlarmValueTemp.value
          if (val.length > 3) {
            this.eAlarmValueTemp = val.slice(0, 3)
            this.$refs.eAlarmValueTemp.value = this.eAlarmValueTemp
          } else {
            this.eAlarmValueTemp = val
          }
          if (this.eAlarmValueTemp > 1000 || this.eAlarmValueTemp < 0 || !/^[1-9][0-9]{0,}$/.test(val)) { // || (val < 50 || val > 100)
            this.$store.state.disabledeter = false
            if (val) {
              this.eAlarmValueFlag = false
            } else {
              this.eAlarmValueFlag = true
            }
          } else {
            this.$store.state.disabledeter = true
            this.eAlarmValueFlag = true
          }
          break
        case 2 :
          val = this.$refs.sAlarmValueTemp.value
          if (val.length > 4) {
            this.sAlarmValueTemp = val.slice(0, 4)
            this.$refs.sAlarmValueTemp.value = this.sAlarmValueTemp
          } else {
            this.sAlarmValueTemp = val
          }
          if (this.sAlarmValueTemp > 1000 || this.sAlarmValueTemp < 0 || !/^[1-9][0-9]{0,}$/.test(val)) {
            this.$store.state.disabledeter = false
            if (val) {
              this.sAlarmValueFlag = false
            } else {
              this.sAlarmValueFlag = true
            }
          } else {
            this.$store.state.disabledeter = true
            this.sAlarmValueFlag = true
          }
          break
      }
    }
  },
  watch: {
    electricityValue (currentValue, oldValue) {
      // console.log(123456)
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.cost{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .data{
    .wh(100%, 100%);
    display: block;
    position: relative;
    &:active{
      background: transparent;
    }
    img{
      height: 24px;
      position: absolute;
      right: 14px;
      top: 50%;
      margin-top: -12px;
    }
  }
  .tittle{
    font-size: .48rem;
    color: #F5FAFF;
    top:15px;
    left:30%;
    position: absolute;
  }
  .cost_container{
    .fs-noTop;
    .timing_nav{
      .wh-l(100%, 1.333333rem);
      .navActive{
        color: @themeColor;
      }
      li{
        .wh-f(50%, 100%);
        .nav_button{
          .wh(4.5rem, 100%);
          margin: 0 auto;
          position: relative;
          span{
            font-size: .48rem;
          }
          strong{
            .wh(.746667rem, .08rem);
            border-radius: .08rem;
            position: absolute;
            bottom: .08rem;
            left: 50%;
            margin-left: -.373333rem;
            margin-top: -.04rem;
            background: @themeColor;
          }
        }
      }
    }
    .main_column{
      .wh(100%, calc(~"100% - 1.333333rem"));
      position: relative;
      .tab_column{
        .fs;
        .set_top{
          .wh(100%, 60%);
          padding-top: .2rem;
          .circle{
            width: 280px; /* no*/
            height: 260px; /* no*/
            margin: 0 auto;
            position: relative;
            // background: url(../../image/humidity/circle.png) 15px 10px no-repeat;
            // background-size: 250px auto;
            span{
              display: block;
              position: relative;
              z-index: 1;
            }
            span:nth-child(1){
              .wh(100%, 25%);
              font-size: .4rem;
              line-height: 2.666667rem;
            }
            span:nth-child(2){
              .wh(100%, 35%);
              strong{
                font-size: 1.653333rem;
              }
              em{
                font-size: .96rem;
              }
            }
            span:nth-child(3){
              .wh(100%, 10%);
              line-height: .8rem;
            }
            span:nth-child(4){
              .wh(100%, 10%);
              font-size: .4rem;
              color: #A2A2A2;
              line-height: 1.013333rem;
            }
            span:nth-child(5){
              .wh(100%, 20%);
              position: relative;
              em{
                .wh-l(2.72rem, .8rem);
                display: block;
                font-size: .32rem;
                background: url(../../image/humidity/Smallbox.png) 0 0 no-repeat;
                background-size: 2.72rem .8rem;
                color: #DEF0FF;
                position: absolute;
                left: 50%;
                top: .586667rem;
                margin-left: -1.36rem;
              }
            }
            .progress{
              .fs;
              .radius_bar{
                position: absolute;
                left: 0;
                top: 0;
              }
              .circle{
                transform-origin: center;
                transform: rotate(-230deg);
                transition: stroke-dasharray .3s ease-in;
              }
            }
          }
        }
        .set_bottom{
          .wh(100%, 40%);
          position: relative;
          .time{
            .wh(100%, 1.6rem);
            .time_column{
              .wh(100%, 50%);
              font-size: .4rem;  
              padding: 0 1.6rem;
              li:nth-child(1){
                .wh-f(35%, 100%);
              }
              li:nth-child(2){
                .wh-f(30%, 100%);
              }
              li:nth-child(3){
                .wh-f(35%, 100%);
              }
            }
            .cost_colum {
              li:nth-child(2){
                .wh-f(35%, 100%);
                float: right;
              }
            }
            .time_column:nth-child(2){
              line-height: .96rem;
              li:nth-child(1), li:nth-child(3){
                border-bottom: .026667rem solid #D1E3EA;
              }
