  }
  .alert_cost{
    .wh(100px, 45px);
    margin: 0 auto;
    position: relative;
    input{
      .wh(80px, 100%);
      float: left;
      font-size: 35px;
      padding: 10px 0 0 0;
      text-align: center;
      border-bottom: 2px solid #828282;
    }
    span{
      height: 100%;
      position: absolute;
      right: -22px;
      top: 0;
      line-height: 60px;
      font-size: 20px;
      font-weight: bold;
