  .error{
    border-bottom: 2px solid #E1295D !important;
  }

  // 弹框
  .scroll_column {
    .wh(100%, 100%);
    position: relative;
    .scroll_width{
      width: 100%/3;
      float: left;
      font-size: 20px;
      position: relative;
      .describe{
        .wh-l(50px, 50px);
        font-size: 15px;
        position: absolute;
        top: 50%;
        right: -8px;
        margin-top: -24px;
      }
      .describe_year{
        right: -18px;
      }
    }
  }
  .cost-center .determine{
    width: 100%;
    float: none;
  }
}

</style>



