/**
 * 添加设备返回 Device
 * @method addDeviceResponse
 * @param  {string}   navigator   设备类型，必选
 * @param  {object}   self        this对象，必选
 * @param  {string}   page        响应界面，必选
 * @param  {number}   type        1、蓝牙未开启 2、正在搜索设备 3、未找到 4、显示设备列表
 * @param  {string}   data        type为4时，返回的设备列表数据
 */

export const addDeviceResponse = (navigator, self, page) => {
  let Data, eqData
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.addDeviceResponse = function (type, data) {
        self.pageType = type
        if (data === '') {
          return
        }
        Data = JSON.parse(data)
        eqData = self.$store.state.equipmentData
        for (let i = 0; i < eqData.length; i++) {
          if (eqData[i].mac === Data.mac) {
            return false
          }
        }
        if (type === 4) {
          self.$store.state.equipmentData.push(Data)
          if (page === 'connect') {
            console.log('20180808')
            self.equipmentData.forEach(element => {
              console.log('081047')
              if (element.state === 1) {
                self.$router.push({path: '/add/state'})
              }
            })
          }
          self.isRefresh = false
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 连接、断开设备返回 Device
 * @method equipmentSwitchResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    page        响应界面，必选
 * @param  {string}    mac         mac地址
 * @param  {number}    state       连接状态
 * @param  {boolean}   result      返回结果
 */
var timer1 = null
var timer2 = null
export const equipmentSwitchResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.equipmentSwitchResponse = function (mac, state, result) {
        console.log('20180815')
        console.log(page)
        if (result) {
          if (!state && page !== 'equipment' && page !== 'connect') {
            self.$router.push({ path: '/add' })
            self.$store.state.firstRunning = false
            return false
          }
          self.$store.state.currentDevice.state = state
          self.$store.state.equipmentData.forEach(element => {
            if (element.mac === mac) {
              // 列表状态
              element.state = state
              if (state) {
                countdownDataRequest(self.NAVIGATOR, self, self.currentDevice.mac)
                timer1 = setTimeout(() => {
                  temperatureAndHumidityDataRequest(self.NAVIGATOR, self, self.currentDevice.mac)
                  clearTimeout(timer1)
                }, 300)
                timer2 = setTimeout(() => {
                  commonPatternListDataRequest(self.NAVIGATOR, self, self.currentDevice.mac)
                  clearTimeout(timer2)
                }, 600)
              }
              if (page === 'connect') {
                if (state) {
                  // 结束搜索
                  console.log('router')
                  closeSearchRequest(self.NAVIGATOR, self)
                  self.$router.push({path: '/add/state'})
                }
                self.$store.state.ToastType = null
                self.disRefsh = true
              } else if (page === 'equipment') {
                if (state) {
                  closeSearchRequest(self.NAVIGATOR, self)
                  self.$router.push({
                    path: '/main/state'
                  })
                }
                self.$store.state.ToastType = null
                self.disRefsh = true
              }
              // else {
              //   console.log('1507')
              //   console.log(page)
              //   if (!state) {
              //     if (page !== 'main') {
              //       self.$store.state.firstRunning = false
              //       self.$router.push({ path: '/add' })
              //     }
              //   }
              // }
            }
          })
        } else {
          // 返回失败时隐藏正在连接弹窗
          self.$store.state.ToastType = 'addFail'
          // 显示的连接状态
          self.$store.state.equipmentData.forEach(element => {
            if (element.mac === mac) {
              if (element.state) {
                element.state = 0
              } else {
                element.state = 1
              }
            }
          })
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

