/**
 * WiFi插座
 * 获取路由器连接的设备列表返回 Add
 * @method  deviceConnectedByRouterResponse
 * @param   {string}    navigator   设备类型，必选
 * @param   {boolean}   result      配置是否成功，必选
 * @param   {string}    data        连接到路由器的设备数据，必选
 */
export const deviceConnectedByRouterResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':case 'Android':
      // console.log('IOS')
      window.deviceConnectedByRouterResponse = function (result, data) {
        let Data = JSON.parse(data)
        // 清除超时
        clearTimeout(self.timer)
        if (result && data !== '') {
          if (Data.length === 0) {
            self.deviceList = []
            return false
          }
          // 过滤绑定设备
          Data = Data.filter((item) => {
            return !item.isBinding
          })
          if (self.deviceList.length === 0) {
            self.deviceList = Data
          } else {
            let newArr = Data.filter((item) => {
              let macArray = self.deviceList.map(i => i.mac)
              return !macArray.includes(item.mac)
            })
            let oldArr = Data.filter((item) => {
              let macArray = self.deviceList.map(i => i.mac)
              return macArray.includes(item.mac)
            })
            newArr.map((item) => {
              self.deviceList.push(item)
            })
            oldArr.map((item) => {
              self.deviceList.map((val, i, arr) => {
                if (item.mac === val.mac) {
                  self.$set(arr, i, item)
                }
              })
            })
          }
        }
      }
      break
    case 'Wechat':
      // console.log("Wechat")
      break
  }
}

