<template>
  <div class="slider">
    <ul>
      <li class="list-item" 
          :style="{height: itemHeight + 'px', lineHeight: itemHeight - 15 + 'px'}"
          v-for="(item,index) in data "
          data-type="0"
          @touchstart.capture="touchStart"
          @touchend.capture="touchEnd"
          @click="skip(item)">
        <div class="list-box">
          <slot :item="item"></slot>
          <div class="delete" @click.stop="deleteItem(item, $event)" :data-index="index">
            <img src="../../image/home/del.png">
            <span>{{$t('homePage.delete')}}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'slider',
  props: {
    data: {
      type: Array,
      default: null
    },
    itemHeight: {
      type: Number,
      default: 90
    }
  },
  data () {
    return {
      startX: 0,
      endX: 0
    }
  },
  methods: {
    // 跳转
    skip (item) {
      if (this.checkSlide()) {
        this.restSlide()
      } else {
        this.$emit('skip', item)
      }
    },

    // 判断当前是否有滑块处于滑动状态
    checkSlide () {
      let listItems = document.querySelectorAll('.list-item')

      for (let i = 0; i < listItems.length; i++) {
        if (listItems[i].dataset.type === '1') {
          return true
        }
      }
      return false
    },

    // 初始化位置
    restSlide () {
      let listItems = document.querySelectorAll('.list-item')

      for (let i = 0; i < listItems.length; i++) {
        listItems[i].dataset.type = 0
      }
    },

    // 滑动开始
    touchStart (e) {
      this.startX = e.touches[0].clientX
    },

    // 滑动结束
    touchEnd (e) {
      let parentElement = e.currentTarget
      this.endX = e.changedTouches[0].clientX

      if (parentElement.dataset.type === '0' && this.startX - this.endX > 30) {
        this.restSlide()
        parentElement.dataset.type = 1
      }

      if (parentElement.dataset.type === '1' && this.startX - this.endX < -30) {
        this.restSlide()
        parentElement.dataset.type = 0
      }

      this.startX = 0
      this.endX = 0
    },

    // 删除
    deleteItem (item, e) {
      // let index = e.currentTarget.dataset.index
      this.restSlide()
      this.$emit('delete', item)
    }
  }
}
</script>

<style scoped lang="less">
@import '../../style/mixin.less';

.slider {
  .wh(100%, 100%);
  ul{
    .wh(100%, auto);
    overflow: hidden;
    .list-item[data-type="0"] {
      transform: translate3d(0, 0, 0);
    }
    .list-item[data-type="1"] {
      transform: translate3d(-2rem, 0, 0);
    }
    .list-item{
      .wh(100%, 90px);
      line-height: 90px - 15px;
      padding-bottom: 15px;
      position: relative;
      transition: all 0.2s;
      .list-box{
        .wh(100%, 100%);
        position: relative;
        .delete{
          width: 75px;
          height: 100%;
          font-size: 17px;
          color: #fff;
          text-align: center;
          position: absolute;
          top: 0;
          right: -75px;
          img{
            height: 35px;
            .f-center;
            margin-top: -10px;
            margin-left: -5px;
          }
          span{
            font-size: 12px;
            color: #E2295D;
            .f-center;
            margin-top: 15px;
            margin-left: -5px;
          }
        }
      }
    }
  }
}
</style>



