<template>
  <div class="login">
    <div class="back" @click="navJump">
      <img src="../../image/global/back.png">
    </div>
    <div class="login_en" v-show="$i18n.locale==='en'">
      <div class="login-top"></div>
      <div class="login_container">
        <ul class="third_party_login">
          <li v-for="item in loginWay">
            <div class="item" @click="thirdPartyLogin(item)">
              <span>
                <img :src="item.icon">
              </span>
              <strong>{{item.software}}</strong>
            </div>            
          </li>
        </ul>
        <mt-button size="large" type="primary" class="login_button" @click="emailLogin" >{{$t('loginPage.email')}}</mt-button>        
      </div>
    </div>
    <div class="login_zh" v-show="$i18n.locale==='zh'" ref="element" :style="{marginTop: -offsetTop + 'px'}">
      <div class="login-top"></div>
      <div class="login_container">
        <div class="input_phone">
          <input type="number" v-model="phone" :placeholder="$t('loginPage.phoneInput')">
        </div>
        <div class="input_code">
          <input type="number" v-model="code" :placeholder="$t('loginPage.codeInput')">
          <button :class="{buttonActive: validate.codeActive}" @click="getCode" :disabled="!validate.codeActive">{{codeText}}</button>
        </div>

        <button class="login_button"  :class="{loginActive: validate.loginActive}" @click="login" :disabled="!validate.loginActive">
          <span v-show="!logining">{{$t('loginPage.login')}}</span>
          <span v-show="logining" class="logining">
            <img src="../../image/adddirver/Loadingcirclesfreeicon.png">
            正在登录
          </span>
        </button>        
      </div>
    </div>

    <div class="language" @click="setLanguage" v-show="!keyboard">
      {{$t('loginPage.language')}} 
      <span>
        <b v-show="$i18n.locale==='zh'">{{$t('loginPage.chinese')}} </b>
        <b v-show="$i18n.locale==='en'">{{$t('loginPage.english')}} </b>            
        <div class="after after_icon"></div>
      </span>
    </div>

    <transition :name="transitionName">
      <router-view></router-view>
    </transition>

    <!-- 吐司 -->
    <toast describe="获取验证码失败" v-if="ToastType==='getCodeFailed'" position="bottom"></toast>
    <toast describe="手机号码格式错误" v-if="ToastType==='phoneError'" position="bottom"></toast>
    <toast describe="手机号码未注册" v-if="ToastType==='noPhoneError'" position="bottom"></toast>
    <toast describe="验证码错误" v-if="ToastType==='codeError'" position="bottom"></toast>
    <toast describe="网络连接失败、服务器异常、请求超时" v-if="ToastType==='networkError'" position="bottom"></toast>
    <toast describe="登录失败" v-if="ToastType==='loginFailed'" position="bottom"></toast>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Field, Button } from 'mint-ui'
import { thirdPartyLoginRequest, getMobilePhoneCodeRequest, mobileLoginRequest } from '../../service/method.js'
import { thirdPartyLoginResponse, mobileLoginResponse, setSystemLanguageResponse, getMobilePhoneCodeResponse } from '../../service/getData.js'
import toast from '../../components/common/toast'

export default {
  data () {
    return {
      phone: '', // 手机号码
      code: '', // 手机验证码
      codeText: '获取验证码',
      loginWay: [
        {
          software: 'Facebook',
          icon: require('../../image/login/facebook.png')
        },
        {
          software: 'Twitter',
          icon: require('../../image/login/twitter.png')
        },
        {
          software: 'Google',
          icon: require('../../image/login/google.png')
        }
      ], // 第三方登录方式
      timer: null, // 定时器
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      logining: false // 登录中
    }
  },

  mounted () {
    // 初始化延时，否则fullHeight值为0
    setTimeout(() => {
      let h = 0
      this.fullHeight = document.documentElement.clientHeight
      window.onresize = () => {
        h = document.documentElement.clientHeight
        // 软键盘是否弹起
        if (h < this.fullHeight) {
          this.keyboard = true
        } else {
          this.keyboard = false
        }
        // 解决小屏幕遮挡输入框
        if (h < 340) {
          this.offsetTop = 340 - h
        } else {
          this.offsetTop = 0
        }
      }
    }, 100)

    // // 查询系统语言返回
    // systemLanguageResponse(this.NAVIGATOR, this)

    // 设置系统语言返回
    setSystemLanguageResponse(this.NAVIGATOR, this)

    // 第三方登录返回
    thirdPartyLoginResponse(this.NAVIGATOR, this)

    // 获取手机验证码返回
    getMobilePhoneCodeResponse(this.NAVIGATOR, this)

    // 手机登录返回
    mobileLoginResponse(this.NAVIGATOR, this)

    // // 查询系统语言
    // systemLanguageRequest(this.NAVIGATOR, this)

    if (this.$root.ISDEBUG) {
      //   window.thirdPartyLoginResponse(true, '{"linkURL":"","middleName":"","lastName":"","userID":"156821558496182","firstName":"","refreshDate":"2018-06-04 16:09:21","name":"WEB_VIEW"}')
      setTimeout(() => {
        // window.getMobilePhoneCodeResponse(false)
        // window.mobileLoginResponse(false, '0x80180')
      }, 5000)
      //   window.systemLanguageResponse('en')

      //   window.setSystemLanguageResponse(true, 'zh')
    }
  },

  components: {
    Field,
    Button,
    toast
  },

  computed: {
    ...mapState([
      'userInfo', 'ToastType'
    ]),

    // 验证状态
    validate () {
      let vPhone = /^\d{11}$/g.test(this.phone)
      let vText = this.codeText === '获取验证码'
      let vCode = /^.{1,}$/g.test(this.code)
      return {
        codeActive: vPhone && vText,
        loginActive: vPhone && vCode
      }
    }
  },

  methods: {
    navJump () {
      this.$router.go(-1)
    },

    getCode () {
      if (this.codeText === '获取验证码') {
        // 获取验证码
        getMobilePhoneCodeRequest(this.NAVIGATOR, this, this.phone)
        // this.codeText = '获取中'
        this.countdown()
        if (this.$root.ISDEBUG) {
          setTimeout(() => {
            window.getMobilePhoneCodeResponse(true)
          }, 2000)
        }
      }
    },

    // 获取验证码成功后的回调
    countdown () {
      let num = 60
      this.codeText = `${num}s`
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.codeText = `${--num}s`
        if (num === 0) {
          this.codeText = '获取验证码'
          clearInterval(this.timer)
        }
      }, 1000)
    },

    login () {
      mobileLoginRequest(this.NAVIGATOR, this, this.phone, this.code)
      this.logining = true
      if (this.$root.ISDEBUG) {
        setTimeout(() => {
          window.mobileLoginResponse(true)
        }, 2000)
      }
    },

    thirdPartyLogin (item) {
      thirdPartyLoginRequest(this.NAVIGATOR, this, item.software)
    },

    setLanguage () {
      this.$router.push({path: '/login/language'})
    },

    emailLogin () {
      this.$router.push({path: '/login/emailLogin'})
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.login{
  .fs;
  background-color: #FFF;
  .back{
    .wh(30px, 30px);
    position: absolute;
    left: 20px;
    top: 20px;
    img{
      height: 20px;
    }
  }
  .login_en{
    .wh(100%, 100%);
      .login-top{
        .wh(100%, 200px);
        background: url(../../image/login/advertisement.jpg);
        background-size: cover;
      }
      .login_container{
        width: 100%;
        height: calc(~"100% - 200px");
        padding-top: 60px;
        position: relative;
        .third_party_login{
          .wh(100%, 100px);
          li{
            .wh-f(100%/3, 100%);
            .item{
              .wh(60%, 100%);
              margin: 0 auto;
              span{
                .wh(100%, 80%);
                display: block;
                position: relative;
                img{
                  height: 65px;
                  .f-center;
                }
              }
              strong{
                .wh(100%, 20%);
                display: block;
                font-size: 14px;
              }
            }
          }
        }
      }
      .login_button{
        width: 240px;
        background: @themeColor;
        margin: 85px auto;
        border-radius: 120px;
        box-shadow: 0px 5px 11px 1px #A3CFFE; /* no */
      }
  }
  .login_zh{
    .wh(100%, 100%);
    transition: all .1s ease-in;
    .login-top{
      .wh(100%, 200px);
      background: url(../../image/login/advertisement.jpg);
      background-size: cover;
    }
    .login_container{
      width: 100%;
      height: calc(~"100% - 200px");
      padding-top: 15px;
      .input_phone{
        .wh-l(250px, 40px);
        margin: 0 auto;
        border-bottom: 1px solid #ccc;
        text-align: left;
        input{
          font-size: 16px;
        }
      }
      .input_code{
        .wh-l(250px, 40px);
        margin: 22px auto;
        border-bottom: 1px solid #ccc;
        text-align: left;
        position: relative;
        input{
          font-size: 16px;
        }
        button{
          .wh-l(80px, 30px);
          background: #C7C7C7;
          position: absolute;
          top: 3px;
          right: 0;
          font-size: 12px;
          text-align: center;
          border-radius: 5px;
          color: #FEFEFE;
        }
        .buttonActive{
          background: @themeColor;
        }
      }
      .login_button{
        width: 240px;
        height: 40px;
        color: #fff;
        background: #C7C7C7;
        margin: 85px auto;
        border-radius: 120px;
        box-shadow: 0px 3px 11px 1px #d2d2d2; /* no */
        font-size: 16px;
        .logining{
          .wh(100%, 100%);
          line-height: 40px;
          display: block;
          font-size: 17px;
          color: #fff;
          vertical-align: middle;
          img{
            height: 17px;
            position: relative;
            top: 2px;
            left: -5px;
            transform-origin: center;
            animation: rotate 1s infinite;
          }
        }
      }
      .loginActive{
        background: @themeColor;
        box-shadow: 0px 3px 11px 1px #A3CFFE; /* no */
      }
    }
  }

  // .language_warp, .language_warp_en{
  //   .wh(100%, 30px);
  //   position: relative;
  //   .language{
  //     .wh-l(200px, 30px);
  //     color: #999;
  //     font-size: 16px;
  //     position: absolute;
  //     left: 50%;
  //     bottom: 0px;
  //     margin-left: -100px;
  //     text-align: left;
  //     padding-left: 24px;
  //     span{
  //       margin-left: 20px;
  //       position: relative;
  //       .after_icon{
  //         right: -12px;
  //       }
  //     }
  //   }
  // }
  // .language_warp_en{
  //   .language{
  //     bottom: 0px;
  //   }
  // }
  
  .language{
    .wh-l(200px, 30px);
    color: #999;
    font-size: 16px;
    position: absolute;
    left: 50%;
    bottom: 20px;
    margin-left: -100px;
    text-align: left;
    padding-left: 24px;
    span{
      margin-left: 20px;
      position: relative;
      .after_icon{
        right: -12px;
      }
    }
  }
}

</style>



