// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'
import Vuex from 'vuex'
import store from './store/index'
import i18n from './language'
import { ISDEBUG, VERSION, NAVIGATOR } from './config/env'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import './config/mock'
import './config/error'
import './service/filter'
import './service/directive'
import 'amfe-flexible' // 自适应布局
import './icons' // svg图标 <svg-icon icon-class="id"/>
import scroll from './components/BScroll'

Vue.component('scroll', scroll)

Vue.config.productionTip = false
Vue.prototype.$http = Axios
// 路由切换动画
Vue.prototype.transitionName = 'slide-left'
// 用于判断设备类型
Vue.prototype.NAVIGATOR = NAVIGATOR
// 项目版本
Vue.prototype.VERSION = VERSION

// 引用
Vue.use(Vuex)
Vue.use(MintUI)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  template: '<App/>',
  components: { App },
  data () {
    return {
      ISDEBUG: ISDEBUG
    }
  },
  methods: {
    // 请求mock数据
    getMockData (path, fun) {
      if (this.ISDEBUG) {
        Axios({
          method: 'GET',
          url: path
        }).then((res) => {
          fun(res)
        })
      }
    }
  }
})



