import {
  LOADING_SWITCH,
  TABLE_SWITCH,
  MESSAGEBOX_SWITCH,
  ASSIGNMENT
} from './mutation-types'

export default {
  [LOADING_SWITCH] (state, boolean) {
    state.isLoading = boolean
  },

  [TABLE_SWITCH] (state, obj) {
    // console.log(obj.flag)
    state.actionSheet = obj.flag
    state.actionSheetType = obj.type
  },

  [MESSAGEBOX_SWITCH] (state, obj) {
    state.messageBox = !state.messageBox
    state.messageBoxType = obj.type
  },

  [ASSIGNMENT] (state, obj) {
    if (obj.field) {
      state[obj.name][obj.field] = obj.value
    } else {
      state[obj.name] = obj.value
    }
  }
}



