import Vue from 'vue'

Vue.directive('init', {
  bind: function (item, binding) {
    console.log(binding)
    console.log(binding.value.check)
    Vue.set(binding.value, 'check', false)
  },
  update: function (item, binding) {
    if (binding.value.check === undefined) {
      Vue.set(binding.value, 'check', false)
    }
  }
})



