<template>
  <div class="success">
    <div class="header">
      <div class="header-left">
        <div class="back" @click="back">
          <img src="../../image/global/back.png" alt="">
        </div>
      </div>
      <div class="header-center">
       Forgot Password
      </div>
    </div>
    <div class="content">
        <div class="imgBox">
          <img src="../../image/global/send.png" alt="">
        </div>
        <div class="tip_1">
          <p>We sent a password back to  <span class="emial">12345678@exmaple.com</span>, please click on the link in the email to complete the account activation.</p>
        </div>
        <button class="finish" @click="tologin">
          Finished
        </button>
      </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapState([
      'messageBox', 'messageBoxType'
    ])
  },
  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),
    back () {
      this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
      this.$router.go(-1)
    },
    tologin () {
      this.$router.push({path: '/login/emailLogin'})
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../style/mixin.less";
.success{
  .fs;
  background: #ffffff;
  z-index: 999;
  .header{
    .wh-l(100%, 1.333333rem);
    background: @themeColor;
    font-size: .48rem;
    color: #F5FAFF;
    .header-left{
      .wh-f(10%, 100%);
      .back{
        .wh(1.066667rem, 100%);
        margin-left: .2rem;
        img{
          height: .533333rem;
          vertical-align: middle;
        }
      }
    }
    .header-center{
      .wh-f(90%, 100%);
      text-align: center;
    }
  }
  .content{
    .imgBox{
      padding: 50px 0;
    }
    .tip_1{
      text-align:left;
      padding: 10px;
      font-size: 16px;
    }
    .finish{
      .wh-l(280px, 40px);
    }
  }
}
</style>




