<template>
  <div class="configfailed">
       <head-top title="配置失败">
         <div slot="setLeft" class="bluetooth" @click="next">
            <img src="../../image/global/back.png">
          </div>
       </head-top>
       <div class="contactimg">
         <img src="../../image/adddirver/Cancel@3x.png">
       </div>  
       <div class="prompt"> 
           <p>1、 确认所连接的WiFi网络为2.4G，并输入正确的密码（暂不支持5G频段的WIFI）</p>
           <p>2、 确定所连接的WiFi网络信号稳定、畅通；</p>
           <p>3、 确保您的设备已处于待连接的状态；</p>
           <p>4、 确保路由器已关闭黑白名单功能（即mac地址过滤)；</p>
           <p>5、 请尽量使您的路由器、手机和设备互相靠近</p>
       </div>  
       <div class="nextbtn" @click="next">
           <span>完成</span>
       </div>   
  </div>
</template>
<script>
import { mapState } from 'vuex'
import headTop from '../../components/header/header'

export default {
  data () {
    return {
    }
  },
  created () {

  },
  mounted () {

  },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'transitiondre'
    ])
  },

  methods: {
    next () {
      this.$store.state.transitiondre = true
      this.$router.go(-2)
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .configfailed{
   .fs;
   background: #ffffff;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
   .contactimg{
     .wh(100%,auto);
     margin-top:88px;
     display: flex;
     justify-content: center;
     img{
      .wh(100px,100px)
     }
    }
   .prompt{
      width:305px;
      margin: 0 auto;
      margin-top: 70px;
      text-align: left;
      p{
          color: #000;
          margin-top: 18px;
          font-size: 12px;
      }
    }
     .nextbtn{
      .wh-l(240px,40px);
      margin: 0 auto;
      margin-top: 26px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      span{
        .wh(100%, 100%);
        line-height: 40px;
        display: block;
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
      }
    }
  }
 </style>


