<template>
  <div class="massageBox">
    <transition name="currentMask">
      <div class="mask" v-show="messageBox" @click="disablemask?clickMask(): ''"></div>
    </transition>
    <transition name="currentAlert">
      <div class="alert" v-show="messageBox" :style="{height: setHeight/37.5 + 'rem', marginTop: -setHeight/37.5/2 + 'rem'}">
        <div class="confirm" v-if="setType==='confirm'">
          <slot name="confirmColumn"></slot>
        </div>
        <div class="pormpt" v-if="setType==='pormpt'">
          <slot name="pormptColumn"></slot>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      flagMessg: true, // 判断可否点击遮罩层
      timer: null // 定时器
    }
  },

  mounted () {
  },

  props: [ 'setHeight', 'setType', 'scroll' ],

  components: {},

  computed: {
    ...mapState([
      'messageBox', 'messageBoxType', 'disablemask'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    clickMask () {
      // 若弹窗隐藏，在300ms内禁止点击
      if (this.flagMessg) {
        this.flagMessg = false
        if (this.messageBox) {
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
        }
        // 滚动选择器（启用、禁用）
        if (this.scroll) {
          for (let i = 0; i < this.scroll.data.length; i++) {
            if (this.messageBox) {
              this.scroll.wheels[i].enable()
            } else {
              this.scroll.wheels[i].disable()
            }
          }
        }
      }
      window.clearTimeout(this.timer)
      this.timer = window.setTimeout(() => {
        this.flagMessg = true
        window.clearTimeout(this.timer)
      }, 600)
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';
.massageBox{
  .mask{
    .fs;
    z-index: 998;
    background: rgba(0, 0, 0, .5);
  }
  .alert{
    .wh(270px, 200px);
    color: #6D6D6D;
    border-radius: 5px;
    background: #FFF;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -100px;
    margin-left: -135px;
    z-index: 999;
    .confirm, .pormpt{
      .wh(100%, 100%);
    }
  }
}

//弹窗动画
.currentMask-leave-active{
  transition: translateY 0s ease-in .3s;
}
.currentAlert-enter-active,.currentAlert-leave-active{
  transition: all .2s ease-out;
}
/* 进入开始 */
.currentAlert-enter{
  transform:scale3d(.2,.2,.1);
  opacity: 0;
}
/* 出去终点 */
.currentAlert-leave-active{
  transform:scale3d(.2,.2,.1);
  opacity: 0;
}
</style>



