<template>
  <div class="recovery">
    <head-top :title="$t('mainMorePage.backupAndRecovery')" goBack="true"></head-top>
    <div class="recovery_container">
      <div class="recovery_top">
        <span>
          <img src="../../../image/mainSet/clouds.png">
        </span>
        <span v-show="this.backupdata">
          <img src="../../../image/mainSet/Correct.png">
          {{$t('mainMorePage.backup')}}
        </span>
        <span>{{$t('mainMorePage.latestBackup')}}:
          <em v-show="this.backupdata">{{date}}<strong>{{time}}</strong></em>
        </span>
        <span>{{$t('mainMorePage.backupPath')}}: {{dir}}
        </span>
      </div>
      <div class="recovery_bottom">
        <div class="button button_top" @click="opreation(1)">{{$t('mainMorePage.BackupToPhone')}}</div>
        <div class="button" @click="opreation(2)" ref="fontSizeEn">{{$t('mainMorePage.RecoverFromBackup')}}</div>
      </div>
    </div>

    <!-- 弹窗 -->
    <!-- 无备份，备份数据到手机 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='backupsOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
          {{$t('messageBox.backupToPhone')}}
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(1, 'backup')">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 已有备份，备份数据到手机 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='hasBackupsOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
         {{$t('messageBox.replaceBackup')}}
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(1, 'backup')">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>
    <!-- 当前无备份 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='recoveryOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
          {{$t('messageBox.noBackup')}}
        </div>
        <div class="alert_operation">
           <div class="determine_one" @click="alertChoose(0)">
             {{$t('messageBox.confirm')}}
           </div>
        </div>
      </div>
    </messageBox>

      <!-- 备份数据进度-->
      <messageBox setHeight="180" setType="pormpt" v-if="messageBoxType==='progressOpreation'">
        <div slot="pormptColumn" class="common_alert">
          <div class="alert_title">{{$t('messageBox.backupTitle')}}</div>
          <div class="alert_container">
            <div class="describe">{{$t('messageBox.backupTitleDesc')}}</div>
            <div class="progress">
              <span :style="{width: progressWidth + '%'}"></span>
              <em>{{progressWidth}}%</em>
            </div>
          </div>
          <div class="alert_operation">
            <div class="determine_two" @click="alertChoose(3, 'backup')">{{$t('messageBox.cancel')}}</div>
          </div>
        </div>
      </messageBox>

      <!-- 备份成功 -->
      <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='backupsSuccessOpreation'">
        <div slot="pormptColumn" class="common_alert">
          <div class="alert_title">{{$t('messageBox.backupSucceed')}}</div>
          <div class="alert_container">
            <div class="describe">{{$t('messageBox.backupSucceedDesc')}}</div>
            <!-- <div class="path">
              {{$t('messageBox.backupClick')}}
            </div> -->
          </div>
          <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)">
              {{$t('recoverPage.ok')}}
            </div>
          </div>
        </div>
      </messageBox>

      <!-- 取消备份数据到手机 -->
    <messageBox setHeight="180" setType="confirm" v-if="messageBoxType==='cancelBackup'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe" v-if="this.$i18n.locale === 'zh'">
           已备份{{progressWidth}}%现在取消备份将无法备份成功，确定继续吗
        </div>
         <div class="alert_describe" v-if="this.$i18n.locale === 'en'">
          {{progressWidth}}% backup done, are you sure to quit?
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(2, 'backup')">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(0)">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 备份失败 -->
      <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='backupFailed'">
        <div slot="pormptColumn" class="common_alert">
          <div class="alert_title">{{$t('messageBox.backupFail')}}</div>
          <div class="alert_container">
            <div class="describe small-size">{{$t('messageBox.backupFailDesc')}}</div>
          </div>
          <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)">
              {{$t('messageBox.backStatus')}}
            </div>
          </div>
        </div>
      </messageBox>
      <!-- 备份替换 -->
      <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='backupReplacesOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
          {{$t('messageBox.coverBackup')}}
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(1, 'recover')">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

     <!-- 恢复数据进度-->
      <messageBox setHeight="180" setType="pormpt" v-if="messageBoxType==='recoverProgressOpreation'">
        <div slot="pormptColumn" class="common_alert">
          <div class="alert_title">{{$t('recoverPage.recoverData')}}</div>
          <div class="alert_container">
            <div class="describe">{{$t('messageBox.recoverBackup')}}</div>
            <div class="progress">
              <span :style="{width: progressWidth + '%'}"></span>
              <em>{{progressWidth}}%</em>
            </div>
          </div>
          <div class="alert_operation">
            <div class="determine_two" @click="alertChoose(3, 'recover')">{{$t('messageBox.cancel')}}</div>
          </div>
        </div>
      </messageBox>

    <!-- 取消恢复数据到手机 -->
    <messageBox setHeight="180" setType="confirm" v-if="messageBoxType==='cancelRecover'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe" v-if="this.$i18n.locale === 'zh'">
           备份数据已恢复{{progressWidth}}%，现在取消恢复将无法恢复成功，确定继续吗
        </div>
        <div class="alert_describe" v-if="this.$i18n.locale === 'en'">
          {{progressWidth}}% of recovering done, are you sure to quit?
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(2, 'recover')">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(0)">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 恢复数据成功 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='recoverSuccessOpreation'">
        <div slot="pormptColumn" class="common_alert">
          <div class="alert_title"> {{$t('recoverPage.recoverSuccess')}}</div>
          <div class="alert_container">
            <div class="describe small-size"> {{$t('messageBox.recoverSuccess')}}</div>
          </div>
          <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)">
              {{$t('recoverPage.ok')}}
            </div>
          </div>
        </div>
      </messageBox>
      <!-- 恢复失败 -->
      <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='recoverFailed'">
        <div slot="pormptColumn" class="common_alert">
          <div class="alert_title">{{$t('recoverPage.recoverFailed')}}</div>
          <div class="alert_container">
            <div class="describe small-size">{{$t('messageBox.recoverFailed')}}</div>
          </div>
          <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(0)">
              {{$t('messageBox.backStatus')}}
            </div>
          </div>
        </div>
      </messageBox>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../../components/header/header'
import messageBox from '../../../components/common/messageBox'
import { BackupTimeAndDirectoryRequest, BackupDataRequest, BackupRecoveryDataRequest, queryTemperatureUnitRequest } from '../../../service/method'
import { BackupTimeAndDirectoryResponse, BackupDataResponse, BackupRecoveryDataResponse } from '../../../service/getData'

export default {
  data () {
    return {
      date: '2018年2月1日', // 备份日期
      time: '19:00', // 备份时间
      dir: '/strong/image', // 备份路径
      backupStatus: false, // 备份或恢复 成功或失败
      timer: null, // 计时器
      backupdata: false // 当前有无备份
    }
  },

  mounted () {
    // 备份时间目录查询
    BackupTimeAndDirectoryRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    // 备份时间目录查询返回
    BackupTimeAndDirectoryResponse(this.NAVIGATOR, this)
    // 备份数据到手机返回
    BackupDataResponse(this.NAVIGATOR, this)
    // 备份中恢复数据返回
    BackupRecoveryDataResponse(this.NAVIGATOR, this)
    window.BackupTimeAndDirectoryResponse(this.currentDevice.mac, 7536556, '/images/recover')
    // 英文字体调整
    if (this.$i18n.locale === 'en') {
      this.$refs.fontSizeEn.style.fontSize = '14px'
    }
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBoxType', 'progressWidth', 'currentDevice', 'TemperatureUnit'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    opreation (num) {
      switch (num) {
        case 1:
          if (this.backupdata) {
            this.MESSAGEBOX_SWITCH({type: 'hasBackupsOpreation'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'backupsOpreation'})
          }
          break
        case 2:// 备份中恢复数据
          if (this.backupdata) {
            this.MESSAGEBOX_SWITCH({type: 'backupReplacesOpreation'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'recoveryOpreation'})
          }
          break
      }
    },

    alertChoose (num, type) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          this.$store.state.progressWidth = 0
          if (type === 'backup') { // 备份数据到手机
            let obj = {
              unit: this.TemperatureUnit
            }
            this.MESSAGEBOX_SWITCH({type: 'progressOpreation'})
            let n = Math.floor(Math.random() * 5)
            let countFlag = true
            this.timer = setInterval(() => {
              if (this.progressWidth >= 95) {
                if (countFlag) {
                  BackupDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, JSON.stringify(obj))
                  countFlag = false
                }
                if (this.backupStatus) {
                  this.$store.state.progressWidth = 100
                  clearInterval(this.timer)
                  this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
                  this.MESSAGEBOX_SWITCH({type: 'backupsSuccessOpreation'})// 备份数据成功
                  return false
                } else {
                  this.$store.state.progressWidth = 99
                }
              } else {
                n = Math.floor(Math.random() * 5)
                this.$store.state.progressWidth += n
              }
            }, 100)
          } else if (type === 'recover') { // 恢复数据到手机
            this.MESSAGEBOX_SWITCH({type: 'recoverProgressOpreation'})
            let n = Math.floor(Math.random() * 5)
            let countFlag = true
            this.timer = setInterval(() => {
              if (this.progressWidth >= 95) {
                if (countFlag) {
                  BackupRecoveryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
                  countFlag = false
                }
                if (this.backupStatus) {
                  clearInterval(this.timer)
                  this.$store.state.progressWidth = 100
                  this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
                  this.MESSAGEBOX_SWITCH({type: 'recoverSuccessOpreation'}) // 恢复数据成功
                } else {
                  this.$store.state.progressWidth = 99
                }
              } else {
                n = Math.floor(Math.random() * 5)
                this.$store.state.progressWidth += n
              }
            }, 100)
          }
          console.log(this.timer)
          if (this.$root.ISDEBUG) {
            // window.BackupRecoveryDataResponse(this.currentDevice.mac, '{"unit": 2}', false)
          }
          if (this.$root.ISDEBUG) {
            setTimeout(() => {
              // window.BackupDataResponse(this.currentDevice.mac, true)
            }, 3000)
          }
          break
        case 2:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          if (type === 'backup') { // 备份数据到手机
            let obj = {
              unit: this.TemperatureUnit
            }
            this.MESSAGEBOX_SWITCH({type: 'progressOpreation'})
            let n = Math.floor(Math.random() * 5)
            let countFlag = true
            this.timer = setInterval(() => {
              if (this.progressWidth >= 95) {
                if (countFlag) {
                  BackupDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, JSON.stringify(obj))
                  countFlag = false
                }
                if (this.backupStatus) {
                  this.$store.state.progressWidth = 100
                  clearInterval(this.timer)
                  this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
                  this.MESSAGEBOX_SWITCH({type: 'backupsSuccessOpreation'})// 备份数据成功
                } else {
                  this.$store.state.progressWidth = 99
                }
              } else {
                n = Math.floor(Math.random() * 5)
                this.$store.state.progressWidth += n
              }
            }, 100)
          } else if (type === 'recover') { // 恢复数据到手机
            this.MESSAGEBOX_SWITCH({type: 'recoverProgressOpreation'})
            let n = Math.floor(Math.random() * 5)
            let countFlag = true
            this.timer = setInterval(() => {
              if (this.progressWidth >= 95) {
                if (countFlag) {
                  BackupRecoveryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
                  countFlag = false
                }
                if (this.backupStatus) {
                  clearInterval(this.timer)
                  this.$store.state.progressWidth = 100
                  this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
                  this.MESSAGEBOX_SWITCH({type: 'recoverSuccessOpreation'}) // 恢复数据成功
                } else {
                  this.$store.state.progressWidth = 99
                }
              } else {
                n = Math.floor(Math.random() * 5)
                this.$store.state.progressWidth += n
              }
            }, 100)
          }
          // if (type === 'backup') {
          //   this.MESSAGEBOX_SWITCH({type: 'progressOpreation'})
          // } else if (type === 'recover') {
          //   this.MESSAGEBOX_SWITCH({type: 'recoverProgressOpreation'})
          // }
          // this.timer = setInterval(() => {
          //   if (this.progressWidth >= 100) {
          //     clearInterval(this.timer)
          //     this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          //     if (type === 'backup') { // 备份数据成功
          //       // BackupDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
          //       this.MESSAGEBOX_SWITCH({type: 'backupsSuccessOpreation'})
          //     } else if (type === 'recover') { // 恢复数据成功
          //       this.MESSAGEBOX_SWITCH({type: 'recoverSuccessOpreation'})
          //       // BackupRecoveryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
          //     }
          //   } else {
          //     if (!this.backupStatus && this.progressWidth === 100) {
          //       this.$store.state.progressWidth = 99
          //     }
          //     // this.$store.state.progressWidth = this.progressWidth + 1
          //     // console.log(this.progressWidth)
          //   }
          // }, 100)
          break
        case 3:
          if (this.progressWidth < 100) {
            clearInterval(this.timer)
            this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
            if (type === 'backup') {
              this.MESSAGEBOX_SWITCH({type: 'cancelBackup'})
            } else if (type === 'recover') {
              this.MESSAGEBOX_SWITCH({type: 'cancelRecover'})
            }
          }
          break
        default:
          break
      }
    }
  },
  destroyed () {
    queryTemperatureUnitRequest(this.NAVIGATOR, this, this.currentDevice.mac)
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../../style/mixin.less';

.recovery{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .recovery_container{
    .fs-noTop;
    .recovery_top{
      .wh(100%, 65%);
      padding-top: 60px;
      span{
        display: block;
        font-size: 13px;
        color: #666666;
      }
      span:nth-child(1){
        img{
          .wh(auto, 142px);
          margin: 0 auto;
        }
      }
      span:nth-child(2){
        .wh-l(100%, 55px);
        font-size: 20px;
        img{
          .wh(auto, 20px);
          position: relative;
          top: 3px;
          margin-right: 2px;
        }
      }
      span:nth-child(3){
        .wh(100%, 50px);
        text-align: left;
        padding-left: 18px;
        margin-top: 30px;
        em{
          strong{
            margin-left: 15px;
          }
        }
      }
      span:nth-child(4){
        text-align: left;
        padding: 0 18px;
      }
    }
    .recovery_bottom{
      .wh(100%, 35%);
      position: relative;
      .button{
        .wh-l(240px, 40px);
        position: absolute;
        bottom: 40px;
        left: 50%;
        margin-left: -120px;
        border: 1px solid #9e9e9e;
        border-radius: 20px;
        font-size: 14px;
        &:active{
          background: #F1F1F1;
        }
      }
      .button_top{
        bottom: 100px;
      }
    }
  }
  .describe{
    font-size: 12px;
    margin-bottom: 22px;
  }
  .small-size{
    margin-top:20px;
    font-size: 15px;
    padding: 0 15px;
  }
  .progress{
    .wh(210px, 12px);
    margin: 0 auto;
    background: url('../../../image/mainSet/bgstrip.png');
    background-size: 100% 100%;
    position: relative;
    padding: 2px 3px;
    span{
      .wh(0%, 8px);
      display: block;
      background: #2692F4;
      border-radius: 5px;
      // background-image: linear-gradient(-45deg, #66EAFA 0%,#1C98F3 50%/3*2,#66EAFA 50%/3, #66EAFA 50%, #1C98F3 50%,#1C98F3 50%/3*2+50%,#66EAFA 50%/3*2+50%, #66EAFA 100%);
      // background-size: 12px 12px;
      transition: width .5s linear;
      box-shadow: 1px 1px 8px #004BAF;
      background: url('../../../image/mainSet/plan2.png') 0 0 no-repeat;
      background-size: auto 100%;
    }
    em{
      position: absolute;
      bottom: -20px;
      right: 0;
      color: @themeColor;
      font-size: 14px;
    }
  }
  .path{
    font-size: 14px;
    font-style: italic;
    text-decoration: underline;
    color: #66ACFE;
    font-family: 'NSimSun'
  }
}

</style>



