          <div class="nav_li" v-if="textShow.m">
            {{$t('addPage.last')}}<span>7</span>{{$t('addPage.month')}}
          </div>
        </div>
        <div class="echarts" id="myChart"></div>
      </div>    
    </div>
    <div class="state_bottom">
      <div class="column_top">
        <div class="li_left">
          <div class="msg_top">
            {{status!==undefined&&status.power}}<span>w</span>
