            <div class="msg_bottom_li">
              <strong>{{status!==undefined&&status.powerMaximum}}<span>W</span></strong>
              <em>12HR5 PEAK</em>
            </div>
          </div>
        </div>
        <ul class="li_right">
          <li>
            {{status!==undefined&&status.voltage}}<span>V</span>
          </li>
          <li>
            {{status!==undefined&&status.electricity}}<span>A</span>
          </li>
          <li>
            {{status!==undefined&&status.frequency}}<span>Hz</span>
          </li>
