      </div>
      <div class="column_center">
        <div class="column_top_list">
          <div class="list_li">
            <div class="list_li_top">
              {{status!==undefined&&status.temperature}}<span>{{TemperatureUnit === 1 ? '℃':'℉'}}</span>
            </div>
            <div class="list_li_bottom">
              TARGET > {{status!==undefined&&status.alarmTemperature}}<span>{{TemperatureUnit === 1 ? '℃':'℉'}}</span>
            </div>            
          </div>
        </div>
        <div class="column_top_list">
          <div class="list_li">
            <div class="list_li_top">
              {{status!==undefined&&status.weight}}
            </div>
            <div class="list_li_bottom">kg</div>  
          </div>
        </div>
        <div class="column_bottom_list">
          <div class="list_li_left">
            <strong>REMANS</strong>
            <span>{{status!==undefined&&status.cost}}<em>{{montaryUintType}}</em></span>
          </div>
          <div class="list_li_right">
            <strong v-if="status.countDownType === 'ON'">TIME TO ON</strong>
            <strong v-if="status.countDownType === 'OFF'">TIME TO OFF</strong>
            <span>{{status!==undefined&&status.hours}}<em>时</em>{{status!==undefined&&status.minutes}}<em>分 </em></span>
          </div>
        </div>
      </div>
      <div class="column_bottom">
        <div class="column_left_list">
          <div class="list_li">
            <img src="../../image/state/Wifisign.png">
          </div>
          <div class="list_li">
            <img src="../../image/state/Bluetoothsignalfreeicon.png">
          </div>
          <div class="list_li">
            <img src="../../image/state/ON.png">
          </div>
        </div>
        <div class="column_right_list" @click="go"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import {} from 'mint-ui'
import {
  socketStatusRequest,
  countdownDataRequest,
  temperatureAndHumidityDataRequest,
  // queryAlarmVoltageRequest,
  // queryAlarmCurrentRequest,
  // queryAlarmPowerRequest,
  queryTemperatureUnitRequest,
  // queryMonetarytUnitRequest,
  // queryLocalElectricityRequest,
  commonPatternListDataRequest
  // spendingCountdownDataRequest
} from '../../service/method'
import {
  socketStatusResponse,
  powerSwitchResponse,
  countdownDataResponse,
  temperatureAndHumidityDataResponse,
  // queryAlarmVoltageResponse,
  // queryAlarmCurrentResponse,
  // queryAlarmPowerResponse,
  queryTemperatureUnitResponse,
  // queryMonetarytUnitResponse,
  // queryLocalElectricityResponse,
  commonPatternListDataResponse
  // spendingCountdownDataResponse
} from '../../service/getData'
import echarts from 'echarts'

export default {
  data () {
    return {
      navTab: 1, // 导航选中 （1、2、3）
      date: null, // 横轴坐标
      data: null, // 纵轴坐标
      textShow: { // 表显示的时间段
        h: true, // 小时
        m: false, // 月
        d: false // 天
      }
    }
  },

  mounted () {
    let self = this
    this.$root.getMockData('/socketStatus', function (res) {
      if (self.currentDevice === null) { return }
      window.socketStatusResponse(self.currentDevice.mac, res.data.data)
    })
    // 插座状态数据请求
    socketStatusRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, true)

    // 插座状态数据返回
    socketStatusResponse(this.NAVIGATOR, this)
    // 插座电源开关返回
    powerSwitchResponse(this.NAVIGATOR, this, 'state')
    this.hoursData()
    if (this.$root.ISDEBUG) {
      // window.socketStatusResponse(this.currentDevice.mac, '{"power":{"value":2422,"averageValue":1258,"maximumValue":3410},"voltage":22.3,"electricity":22.4,"frequency":22.5,"temperature":{"value":21,"alarmValue":100},"humidity":{"value":22,"alarmValue":60},"weight":23,"cost":166,"countDownType":"ON","time":3600000}')
      // setTimeout(() => {
      //   // window.socketStatusResponse(this.currentDevice.mac, '{"power":{"value":2422,"averageValue":1258,"maximumValue":3410},"voltage":25.3,"electricity":5.4,"frequency":25.5,"temperature":{"value":21,"alarmValue":100},"humidity":{"value":22,"alarmValue":60},"weight":23,"cost":166,"countDownType":"ON","time":3600000}')
      // }, 5000)
      // setTimeout(() => {
      //   window.powerSwitchResponse(this.currentDevice.mac, false)
      // }, 7000)
    }
    // var base = +new Date(1968, 9, 3)
    // var oneDay = 24 * 3600 * 1000
    // for (var i = 1; i < 60 * 24; i++) {
    //   // var now = new Date((base += oneDay))
    //   time += 60

    //   if (Math.floor(time / 60 / 60) === 0) {
    //     h = 0
    //     m = time / 60
    //   } else {
    //     h = Math.floor(time / 60 / 60)
    //     m = (time - Math.floor(time / 60 / 60) * 60 * 60) / 60
    //   }
    //   if (m < 10) {
    //     m = '0' + m
    //   }
    //   var now = h + ':' + m
    //   date.push(now)
    //   data.push(Math.abs(Math.round((Math.random() - 0.5) * 20 + data[i - 1])))
    // }
    // 定量定费数据请求
    // spendingCountdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    // spendingCountdownDataResponse(this.NAVIGATOR, this)
    // 定时列表数据请求
    commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    commonPatternListDataResponse(this.NAVIGATOR, this, 'state')
    // 倒计时页面数据
    countdownDataRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // 温湿度页面数据
    temperatureAndHumidityDataRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // 倒计时页面数据
    countdownDataResponse(self.NAVIGATOR, self, 'state')
    // 温度页面数据
    temperatureAndHumidityDataResponse(self.NAVIGATOR, self, 'state')
    // 查询电压警告值
    // queryAlarmVoltageRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // queryAlarmVoltageResponse(self.NAVIGATOR, self)
    // 查询电流警告值
    // queryAlarmCurrentRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // queryAlarmCurrentResponse(self.NAVIGATOR, self)
    // 查询功率警告值
    // queryAlarmPowerRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // queryAlarmPowerResponse(self.NAVIGATOR, self)
    // 查询温度单位
    queryTemperatureUnitRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    queryTemperatureUnitResponse(self.NAVIGATOR, self)
    // // 查询货币单位
    // queryMonetarytUnitRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // queryMonetarytUnitResponse(self.NAVIGATOR, self)
    // // 查询 本地电价
    // queryLocalElectricityRequest(self.NAVIGATOR, self, self.currentDevice.mac)
    // queryLocalElectricityResponse(self.NAVIGATOR, self)
  },

  components: {},

  computed: {
    ...mapState([
      'currentDevice', 'socketStatusData', 'settingData', 'socketSwitch', 'TemperatureUnit', 'montaryUint'
    ]),
    montaryUintType () {
      let type = this.montaryUint
      let str = ''
      switch (type) {
        case 1:
          str = '元'
          break
        case 2:
          str = '英镑'
          break
        case 3:
          str = '美元'
          break
        case 4:
          str = '欧元'
          break
        case 5:
          str = '日元'
          break
        default:
          break
      }
      return str
    },
    status () {
      let power, powerAverage, powerMaximum, voltage, electricity, frequency, temperature, alarmTemperature, humidity, alarmHumidity, weight, cost, hours, minutes, countDownType, time
      let tempType = this.TemperatureUnit
      let costType = this.montaryUint
      if (this.socketStatusData !== null) {
        if (this.socketStatusData.power !== undefined) {
          power = this.socketStatusData.power.value
          powerAverage = this.socketStatusData.power.averageValue
          powerMaximum = this.socketStatusData.power.maximumValue
        } else {
          power = 0
          powerAverage = 0
          powerMaximum = 0
        }
        if (this.socketStatusData.voltage !== undefined) {
          voltage = this.socketStatusData.voltage
        } else {
          voltage = 0
        }
        if (this.socketStatusData.electricity !== undefined) {
          electricity = this.socketStatusData.electricity
        } else {
          electricity = 0
        }
        if (this.socketStatusData.frequency !== undefined) {
          frequency = this.socketStatusData.frequency
        } else {
          frequency = 0
        }
        if (this.socketStatusData.temperature !== undefined) {
          if (tempType === 1) {
            temperature = Math.round(this.socketStatusData.temperature.value)
            alarmTemperature = this.socketStatusData.temperature.alarmValue
          } else {
            temperature = Math.round(this.socketStatusData.temperature.value * 1.8 + 32)
            alarmTemperature = Math.round(this.socketStatusData.temperature.alarmValue * 1.8 + 32)
          }
        } else {
          temperature = 0
          alarmTemperature = 0
        }
        if (this.socketStatusData.humidity !== undefined) {
          humidity = Math.round(this.socketStatusData.humidity.value)
          alarmHumidity = this.socketStatusData.humidity.alarmValue
        } else {
          humidity = 0
          alarmHumidity = 0
        }
        if (this.socketStatusData.weight !== undefined) {
          weight = this.socketStatusData.weight
        } else {
          weight = 0
        }
        if (this.socketStatusData.cost !== undefined) {
          switch (costType) {
            case 1:
              cost = this.socketStatusData.cost
              break
            case 2:
              cost = Math.round(this.socketStatusData.cost * 0.117295)
              break
            case 3:
              cost = Math.round(this.socketStatusData.cost * 0.156052)
              break
            case 4:
              cost = Math.round(this.socketStatusData.cost * 0.133737)
              break
            case 5:
              cost = Math.round(this.socketStatusData.cost * 16.96119)
              break
            default:
              break
          }
        } else {
          cost = 0
        }
        if (this.socketStatusData.time !== undefined) {
          time = this.socketStatusData.time
          // console.log(typeof time, Math.floor(time / 1000 / 60 / 60))
          hours = Math.floor(time / 1000 / 60 / 60)
          minutes = Math.floor((time - (hours * 1000 * 60 * 60)) / 1000 / 60)
        } else {
          time = 0
          hours = 0
          minutes = 0
        }
        if (this.socketStatusData.countDownType !== undefined) {
          countDownType = this.socketStatusData.countDownType
        } else {
          countDownType = null
        }
        return {power, powerAverage, powerMaximum, voltage, electricity, frequency, temperature, alarmTemperature, humidity, alarmHumidity, weight, cost, hours, minutes, time, countDownType}
      } else {
        return {
          power: 0,
          powerAverage: 0,
          powerMaximum: 0,
          voltage: 0,
          electricity: 0,
          frequency: 0,
          temperature: 0,
          alarmTemperature: 0,
          humidity: 0,
          alarmHumidity: 0,
          weight: 0,
          cost: 0,
          countDown: 0,
          hours: 0,
          minutes: 0
        }
      }
    }
  },

  methods: {
    ...mapMutations([]),

    navLi (num) {
      this.navTab = num
      switch (num) {
        case 1:
          this.textShow = {
            h: true,
            m: false,
            d: false
          }
          this.hoursData()
          break
        case 2:
          this.textShow = {
            h: false,
            m: false,
            d: true
          }
          this.dayData()
          break
        case 3:
          this.textShow = {
            h: false,
            m: true,
            d: false
          }
          this.monData()
          break
      }
    },
    makeEchart (num) {
      let myChart = echarts.init(document.getElementById('myChart'))
      myChart.setOption({
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%']
          }
        },
        title: {
          // left: 'center',
          // text: '大数据量面积图'
        },
        legend: {
          top: 'bottom',
          data: ['意向']
        },
        toolbox: {
          // feature: {
          //   dataZoom: {
          //     yAxisIndex: 'none'
          //   },
          //   restore: {},
          //   saveAsImage: {}
          // }
        },
        grid: {
          // 直角坐标系内绘图网格
          show: false, // 是否显示直角坐标系网格。[ default: false ]
          left: '15%', // grid 组件离容器左侧的距离。
          right: '10%',
          bottom: '16%',
          top: '20%'
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              type: 'solid',
              color: '#B6B60C', // 左边线的颜色
              width: '2' // 坐标线的宽度
            }
          },
          axisLabel: {
            textStyle: {
              color: '#fff' // 坐标值得具体的颜色
            },
            interval: num
          },
          data: this.date
        },
        yAxis: {
          type: 'value',
          boundaryGap: false,
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              type: 'solid',
              color: '#B6B60C', // 左边线的颜色
              width: '2' // 坐标线的宽度
            }
          },
          axisLabel: {
            textStyle: {
              color: '#fff' // 坐标值得具体的颜色
            }
          }
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 60,
            show: false
          },
          {
            start: 0,
            end: 10,
            show: false,
            handleIcon:
              'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
            handleSize: '80%',
            handleStyle: {
              color: '#fff',
              shadowBlur: 3,
              shadowColor: 'rgba(0, 0, 0, 0.6)',
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }
        ],
        series: [
          {
            name: '模拟数据',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: '#46AF46'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#46AF46'
                  },
                  {
                    offset: 1,
                    color: 'transparent'
                  }
                ])
              }
            },
            data: this.data
          }
        ]
      })
    },
    hoursData () {
      let d = new Date()
      // var time = 0
      let h = d.getHours() + 1
      // let m = d.getMinutes()
      let m = 0
      let arr1 = []
      let arr2 = []
      let i = 0
      while (i < 12) {
        if (m < 0) {
          h--
          i++
          m = 60 + m
        }
        let now = (h < 10 ? '0' + h : h) + ':' + (m < 10 ? '0' + m : m)
        arr1.unshift(now)
        arr2.push(Math.abs(Math.round((Math.random() - 0.5) * 50 + (Math.random() * 300))))
        m -= 5
      }
      arr1.shift(arr1[0])
      this.date = arr1
      console.log(arr1)
      arr2.shift(arr2[0])
      this.data = arr2
      this.makeEchart(11)
    },
    dayData () {
      let d = new Date()
      // var time = 0
      let h = d.getHours()
      // let m = d.getMinutes()
      let m = 0
      let day = d.getDate() + 1
      let day2 = d.getDate() + 1
      console.log(day)
      let mon = d.getMonth() + 1
      let arr1 = []
      let arr2 = []
      let i = 0
      let now
      while (i <= 7) {
        if (m < 0) {
          h--
          m = 60 + m
        }
        if (h < 0) {
          now = (mon < 10 ? '0' + mon : mon) + '-' + (day < 10 ? '0' + day : day)
          arr1.unshift(now)
          arr2.push(Math.abs(Math.round((Math.random() - 0.5) * 50 + arr2[i - 1])))
          day--
          i++
          h = 23
        }
        if (day <= 0) {
          mon--
          day = 31
        }
        if (day < day2) {
          now = (h < 10 ? '0' + h : h) + ':' + (m < 10 ? '0' + m : m)
          arr1.unshift(now)
          arr2.push(Math.abs(Math.round((Math.random() - 0.5) * 50 + (Math.random() * 300))))
        }
        m -= 30
      }
      console.log(arr1, arr2)
      arr1.shift(arr1[0])
      arr2.shift(arr2[0])
      this.date = arr1
      this.data = arr2
      this.makeEchart(48)
    },
    monData () {
      let d = new Date()
      let day = d.getDate()
      let mon = d.getMonth() + 1
      let arr1 = []
      let arr2 = []
      let i = 0
      let now
      while (i < 7) {
        if (day <= 0) {
          mon--
          day = 31
          i++
        }
        if (mon <= 0) {
          mon = 12
        }
        now = (mon < 10 ? '0' + mon : mon) + '-' + (day < 10 ? '0' + day : day)
        arr1.unshift(now)
        arr2.push(Math.abs(Math.round((Math.random() - 0.5) * 50 + (Math.random() * 300))))
        day--
      }
      console.log(arr1, arr2)
      arr1.shift(arr1[0])
      arr2.shift(arr2[0])
      this.date = arr1
      this.data = arr2
      this.makeEchart(30)
    },
    go () {
      if (this.VERSION === 1) {
        this.$router.push({ path: '/main' })
      } else if (this.VERSION === 2) {
        this.$router.go(-1)
      }
    }
  },

  destroyed () {
    if (!this.$root.ISDEBUG) {
      socketStatusRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, false)
    }
  }
}
</script>

