              .zoom-font(0.9);
              position: absolute;
              top: 28px;
              left: 50%;
              margin-left: -58px;
            }
          }
          .msg_bottom_li:nth-child(2) {
            em {
              margin-left: -65px;
            }
          }
        }
      }
      .li_right {
        .wh-f(40%, 100%);
        padding: 0 0 0 10px;
        li {
          .wh(100%, 30%);
          background: url(../../image/state/frame-right.png) 0 0 no-repeat;
          background-size: 100% 100%;
          font-size: 18px;
          position: relative;
          line-height: 46px;
          span {
            position: absolute;
            bottom: -5px;
            right: 10px;
            font-size: 12px;
            font-family: normal;
          }
        }
        li:nth-child(2) {
          margin: 7px 0;
          .list_li_bottom {
            color: #ffffff;
          }
        }
      }
    }
    .column_center {
      .wh(100%, 45%);
      padding: 0 15px;
      overflow: hidden;
      div:nth-child(1){
        margin-right: 2%;
        .list_li_bottom {
          color: rgb(113, 152, 159);;
        }
      }
      div:nth-child(2){
        margin-left: 2%;
        .list_li_bottom {
          color: #ffffff;
        }
      }
      .column_top_list {
        .wh-f(48%, 48%);
        margin-bottom: 2%;
        background: url(../../image/state/frame-centre.png) 0 0 no-repeat;
        background-size: 100% 100%;
        .list_li {
          .wh(100%, 100%);
          float: left;
          position: relative;
          .list_li_top {
            .wh(100%, 60%);
            font-size: 28px;
            line-height: 56px;
            span {
              font-size: 22px;
              font-family: normal;
              margin-left: 10px;
            }
          }
          .list_li_bottom {
            .wh(140px, 40%);
            font-size: 20px;
            .zoom-font(0.6);
            position: absolute;
            top: 46px;
            left: 50%;
            margin-left: -40px;
            span {
              font-size: 16px;
            }
          }
        }
        .list_li:nth-child(2) {
          .list_li_bottom {
            span {
              font-size: 20px;
            }
          }
        }
        .list_li:nth-child(3) {
          .list_li_bottom {
            color: #d0dbdd;
          }
        }
      }
      .column_bottom_list {
        clear: both;
        .wh(100%, 50%);
        background: url(../../image/state/frame-centre.png) 0 0 no-repeat;
        background-size: 100% 100%;
        .list_li_left {
          .wh(100%/3, 100%);
          float: left;
          position: relative;
          strong {
            .wh(140px, 40%);
            display: block;
            font-size: 12px;
            color: #71989f;
            line-height: 50px;
            .zoom-font(0.9);
            position: absolute;
            top: 2px;
            left: 50%;
            margin-left: -60px;
          }
          span {
            .wh(100%, 60%);
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            font-size: 30px;
            line-height: 45px;
            em {
              font-size: 16px;
              position: absolute;
              right: 0px;
              bottom: 5px;
            }
          }
        }
        .list_li_right {
          .wh(100%/3, 100%);
          float: right;
          position: relative;
          strong {
            .wh(140px, 40%);
            display: block;
            font-size: 12px;
            color: #71989f;
            line-height: 50px;
            .zoom-font(0.9);
            position: absolute;
            top: 2px;
            left: 50%;
            margin-left: -63px;
          }
          span {
            .wh(150%, 60%);
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            font-size: 30px;
            margin-left: -50%;
            line-height: 45px;
            em {
              font-size: 14px;
              margin: 0 10px;
            }
          }
        }
      }
    }
    .column_bottom {
      .wh(100%, 15%);
      padding: 10px 15px;
      .column_left_list {
        .wh(40%, 100%);
        float: left;
        background: url(../../image/state/frame-left-small.png) 0 0 no-repeat;
        background-size: 100% 100%;
        .list_li {
          .wh(100%/3, 100%);
          float: left;
          position: relative;
          img {
            height: 16px;
            .f-center;
          }
        }
      }
      .column_right_list {
        .wh(35%, 100%);
        float: right;
        background: url(../../image/state/config.png) 0 0 no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}
</style>



