<template>
    <div class="blue-toothstate" :class="{stateBg: socketSwitch}" @touchend="go($event,'end')" @touchstart="go($event, 'start')">
        <div class="head_mask"></div>
        <div class="head">Dashboard</div>
        <div class="top-progress">
            <div class="progress">
                <svg xmlns="http://www.w3.org/200/svg" class="circle_inside">
                    <circle class="circle" cx="130px" cy="130px" :r="hRadius+'px'" fill="none" stroke="#C4C4C4" stroke-width="13px" stroke-dasharray="537,10000" stroke-linecap="round" />
                </svg>
                <svg xmlns="http://www.w3.org/200/svg" class="radius_bar">
                    <circle class="circle" cx="130px" cy="130px" :r="hRadius+'px'" fill="none" :stroke="socketSwitch?'#21BFFC':'#66FECB'" stroke-width="13px" :stroke-dasharray="hStrokeDasharray" stroke-linecap="round" />
                </svg>
                <div class="tempeture" :class="{heatingBg: isHeating}">{{tTargetValue}}<span>{{TemperatureUnit === 2?'℉':'℃'}}</span></div>
            </div>
            <div class="bottom-side">
                <div class="left">
                    <img v-show="!socketSwitch" src="../../image/state/room-green@3x.png" alt="">
                    <img v-show="socketSwitch" src="../../image/state/room@3x.png" alt="">
                    <span  :class="socketSwitch?'open':'close'">{{parseFloat(currentTemperature.toFixed(1))}}{{TemperatureUnit===2? '℉': '℃'}}</span>
                    <!-- <span v-if="TemperatureUnit===2" :class="socketSwitch?'open':'close'">{{currentTemperature}}</span> -->
                </div>
                <div class="right">
                    <!-- <span v-if="false">40℃</span> -->
                    <img v-show="!isHeating&&!socketSwitch" src="../../image/state/refrgeration-green@3x.png" alt="">
                    <img v-show="!isHeating&&socketSwitch" src="../../image/state/refrgeration@3x.png" alt="">
                    <img v-show="isHeating" src="../../image/state/warm@3x.png" alt="">
                </div>
            </div>
        </div>
        <div class="bottom-progress">
            <div class="time">
              <img src="../../image/state/pointer@3x.png" alt="" :style="{transform: `rotate(${statePage.countdownRotate- 136}deg)`}">

              <div class="text">{{statePage.countdownTimeText}}</div>
            </div>
            <div class="remain">
                <svg xmlns="http://www.w3.org/200/svg" height="150px" width="150px" v-if="timingOff && taskSwitch">
                    <circle ref="countdown" class="circle" cx="75px" cy="75px" :r="tRadius+'px'" fill="none" :stroke="socketSwitch?'#21BFFC':'#66FECB'" stroke-width="10px" :stroke-dasharray="tStrokeDasharray" stroke-linecap="round" />
                </svg>
                <div class="time_pointer" :style="{transform: `rotate(${timingRotate}deg)`}" >
                  <img v-if="timingOff" src="../../image/state/yellow-pointer@3x.png" alt="">
                  <img v-if="!timingOff" src="../../image/state/black-pointer@3x.png" alt="">
                </div>
                <div class="time-wrap">
                  <div class="time-text">{{taskHour}} hr {{taskMinnte}}  mins</div>
                  <div class="tit">Remains</div>
                  <div class="time_start">{{currentTask}}</div>
                </div>
                <!-- <div class="time_text" :style="{transform: `rotate(${taskRotate}deg)`}">
                  <span :style="{transform: `rotate(-${taskRotate}deg)`}">{{currentTask}}</span>
                </div> -->
            </div>
        </div>
        <div class="arrow" @click="go($event, 'click')">
          <img src="../../image/state/next@3x.png" alt="">
        </div>
    </div>
</template>
<script>
import headTop from '../../components/header/header'
import { mapState, mapMutations } from 'vuex'
import {
  queryTemperatureUnitRequest,
  temperatureAndHumidityDataRequest,
  countdownDataRequest,
  powerSwitchStatusRequest,
  commonPatternListDataRequest
} from '../../service/method'
import {
  queryTemperatureUnitResponse,
  temperatureAndHumidityDataResponse,
  countdownDataResponse,
  powerSwitchResponse,
  commonPatternListDataResponse,
  equipmentSwitchResponse
} from '../../service/getData'
import {
  // digitFormat,
  weekToArrayFormat,
  sortFormat,
  timeToNumFormat,
  // numToTimeFormat,
  getStore,
  getWeekday,
  weekFormat
// setStore
} from '../../config/utils.js'
export default {
  data () {
    return {
      Timer: null, // 时钟计时器
      Timer2: null, // 定时器
      Timer3: null, // 定时器
      Timer4: null, // 定时器
      Timer5: null, // 定时器
      // socketSwitch: true, // 继电器开关
      // isHeating: false, // 是否为制热模式
      hRadius: 110, // svg进度条半径
      tRadius: 70,
      // currentTemperature: 0, // 制冷制热当前温度
      // tTargetValue: 0, // 制冷制热目标温度
      countdownSwitch: false, // 倒计时开关
      timingValue: 30, // 定时
      // timingRotate: 0, // 定时指针旋转度数
      // ordinaryData: [], // 普通定时列表数据
      // advancedData: [], // 进阶定时列表数据
      // taskData: [], // 执行的任务数据
      // currentTask: '', // 当前任务
      // taskHour: 0, // 距下次任务执行小时
      // taskMinnte: 0, // 距下次任务执行分钟
      // hasTask: false // 是否有设置定时
      startY: 0,
      moveY: 0,
      startX: 0,
      moveX: 0
    }
  },

  mounted () {
    // 查询温度单位
    queryTemperatureUnitRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    // 查询温度单位返回
    queryTemperatureUnitResponse(this.NAVIGATOR, this)
    // 设备连接状态返回
    equipmentSwitchResponse(this.NAVIGATOR, this, 'blueToothState')
    this.Timer2 = setTimeout(() => {
      // 倒计时页面数据
      countdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    }, 300)
    // 插座电源开关返回
    powerSwitchResponse(this.NAVIGATOR, this, 'blueToothState')

    this.Timer3 = setTimeout(() => {
      // 温湿度页面数据
      temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    }, 600)
    // let temperatureData = JSON.parse(getStore('temperatureData'))
    // if (temperatureData) {
    //   this.currentTemperature = temperatureData.currentValue
    // }
    // 温湿度页面数据返回
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'blueToothState')

    this.Timer4 = setTimeout(() => {
      // 插座电源开关状态请求
      powerSwitchStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    }, 900)
    // 倒计时页面数据返回
    countdownDataResponse(this.NAVIGATOR, this, 'blueToothState')

    this.Timer5 = setTimeout(() => {
      // 定时列表数据
      commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    }, 1200)
    // 定时列表数据返回
    commonPatternListDataResponse(this.NAVIGATOR, this, 'blueToothState')

    if (this.$root.ISDEBUG) {
      // 继电器开关
      // window.powerSwitchResponse(this.currentDevice.mac, false)
      // 温度单位
      // window.queryTemperatureUnitResponse(this.currentDevice.mac, true, 1)
      // 定温度
      window.temperatureAndHumidityDataResponse(this.currentDevice.mac, '{"temperature":{"currentValue":64.21000000000000875959,"hotAlarmValue":118.5,"hotAlarmSwitch":true,"codeAlarmValue":0,"codeAlarmSwitch":false}}')
      // 倒计时
      // window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":1,"allTime":60,"Switchgear":true,"countdownSwitch":true}')
      // setTimeout(() => {
      //   window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":2,"allTime":2,"Switchgear":true,"countdownSwitch":true}')
      //   console.log(6666)
      // }, 30000)
      // setTimeout(() => {
      window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":1,"allTime":1,"Switchgear":true,"countdownSwitch":true}')
      //   console.log(101010)
      // }, 60000)
      // 定时
      // window.commonPatternListDataResponse(this.currentDevice.mac, '[]', '[]')
      window.commonPatternListDataResponse(this.currentDevice.mac, '[{"id":14,"time":"18:42","switch":true,"week":127,"state":true},{"id":11,"time":"16:11","switch":false,"week":127,"state":true},{"id":1,"time":"10:42","switch":false,"week":96,"state":false},{"id":2,"time":"7:23","switch":true,"week":63,"state":false},{"id":3,"time":"16:15","switch":true,"week":63,"state":true}, {"id":7,"time":"7:4","switch":true,"week":127,"state":false},{"id":8,"time":"7:23","switch":true,"week":96,"state":false},{"id":9,"time":"16:15","switch":true,"week":63,"state":false},{"id":4,"time":"7:4","switch":true,"week":3,"state":false},{"id":5,"time":"7:23","switch":true,"week":63,"state":false},{"id":6,"time":"16:15","switch":true,"week":63,"state":false}]', '[{"id":1,"time":"10:42","time2":"10:30","switch":false,"week":96,"state":false},{"id":2,"time":"7:23","time2":"11:42","switch":true,"week":63,"state":false}]')

      // console.log(this.ordinaryData)
    }

    // 时钟
    this.clockPointer()
    if (!this.tStartActive && !this.hStartActive) {
      // if (this.TemperatureUnit === 1) {
      //   this.$store.state.tTargetValue = 26
      // } else if (this.TemperatureUnit === 2) {
      //   this.$store.state.tTargetValue = 79
      // }
      this.$store.state.tTargetValue = '--'
    }
  },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'currentDevice', 'stateCountdown', 'statePage',
      'TemperatureUnit', 'timingOff', 'currentTemperature', 'tTargetValue', 'timingRotate', 'socketSwitch',
      'isHeating', 'taskHour', 'taskMinnte', 'taskSwitch', 'tStartActive', 'hStartActive', 'currentTask',
      'ordinaryData', 'taskData'
    ]),

    hStrokeDasharray () {
      let percentage
      if (this.TemperatureUnit === 1) {
        percentage = (parseInt(this.currentTemperature) + 10) / 70
        void (this.currentTemperature > 60 ? percentage = 1 : percentage)
        void (this.currentTemperature < -10 ? percentage = 0 : percentage)
      } else {
        percentage = (parseInt(this.currentTemperature) - 14) / 126
        void (this.currentTemperature > 140 ? percentage = 1 : percentage)
        void (this.currentTemperature < 14 ? percentage = 0 : percentage)
      }
      return (
        Math.floor(2 * Math.PI * this.hRadius) *
        percentage * 280 / 360 +
        ',10000'
      )
    },

    tStrokeDasharray () {
      let percentage = 1
      return (
        Math.floor(2 * Math.PI * this.tRadius) *
        percentage * 360 / 360 +
        ',10000'
      )
    },

    taskRotate () {
      return timeToNumFormat(this.currentTask) / (24 * 60) * 360
    }
  },

  methods: {
    ...mapMutations([
      'ASSIGNMENT'
    ]),

    dataForamt (objData, type) {
      for (let i in objData) {
        objData[i].time = this.timeFormat(objData[i].time).str
        if (type === 'senior') {
          objData[i].time2 = this.timeFormat(objData[i].time2).str
        }
        objData[i].timeNumber = this.timeFormat(objData[i].time).num
        objData[i].week = weekFormat(objData[i].week, this)
        console.log(objData[i].week)
      }
      for (let j = 0; j < objData.length; j++) {
        let temp = {}
        for (let k = j + 1; k < objData.length; k++) {
          if (objData[j].timeNumber > objData[k].timeNumber) {
            temp = objData[j]
            objData[j] = objData[k]
            objData[k] = temp
          }
        }
      }
      console.log(objData)
      return objData
    },
    // 时间不足两位补零
    timeFormat (str) {
      console.log(str)
      let array = str.split(':')
      let h = parseInt(array[0])
      let m = parseInt(array[1])
      if (h < 10) {
        h = '0' + h
      }
      if (m < 10) {
        m = '0' + m
      }
      return {
        str: h + ' : ' + m,
        num: parseInt('' + h + m)
      }
    },
    getMintiming () {
      let timedata = JSON.parse(getStore('timeData'))
      let date = new Date()
      let week = date.getDay()
      if (week === 0) { week = 7 }
      let hour = date.getHours()
      console.log(typeof timedata)
      let minute = date.getMinutes()
      if (!timedata.init) {
        if (timedata.length) {
          for (let i = 0; i < timedata.length; i++) {
            let minTime = parseInt(timedata[i].time.split(':')[0] * 60) + parseInt(timedata[i].time.split(':')[1])
            let currTime = hour * 60 + minute
            let eleWeek = getWeekday(timedata[i].week, this)
            let n = weekToArrayFormat(eleWeek).indexOf(week)
            console.log(weekToArrayFormat(eleWeek))
            if (timedata[i].state && minTime > currTime && n !== -1) {
              this.$store.state.taskSwitch = timedata[i].switch
              console.log(this.taskSwitch)
              this.$store.state.currentTask = timedata[i].time
              let x = parseInt(timedata[i].time.split(':')[1]) - minute
              if (x >= 0) {
                this.$store.state.taskHour = parseInt(timedata[i].time.split(':')[0]) - hour
                this.$store.state.taskMinnte = Math.abs(x)
              } else {
                this.$store.state.taskMinnte = 60 + x
                this.$store.state.taskHour = parseInt(timedata[i].time.split(':')[0]) - hour - 1
              }
              return false
            } else {
              this.$store.state.currentTask = ''
              this.$store.state.taskHour = 0
              this.$store.state.taskMinnte = 0
              this.$store.state.taskSwitch = false
            }
          }
        } else {
          this.$store.state.taskHour = 0
          this.$store.state.taskMinnte = 0
          this.$store.state.currentTask = ''
          this.$store.state.taskSwitch = false
        }
      }
    },
    // 定时（时钟）
    clockPointer () {
      clearInterval(this.Timer)

      this.getMintiming()
      this.Timer = setInterval(() => {
        let date = new Date()
        // let week = date.getDay()
        // if (week === 0) { week = 7 }
        let hour = date.getHours()
        let minute = date.getMinutes()
        // 当前时间
        this.$store.state.timingRotate = (hour * 60 + minute) / (24 * 60) * 360
        this.getMintiming()
      }, 1000)
    },
    runTask (ordinaryData) {
      // 显示最近的定时任务
      if (this.ordinaryData.length !== 0) {
        this.ordinaryData.forEach((value) => {
          console.log(weekToArrayFormat(value.week))
          weekToArrayFormat(value.week).forEach((value2) => {
            console.log(this.taskData[value2])
            if (this.taskData[value2] === undefined) {
              this.$store.state.taskData[value2] = []
            }
            this.$store.state.taskData[value2].push(timeToNumFormat(value.time))
          })
        })
        // 排序
        this.taskData.forEach((val, index, array) => {
          array[index] = sortFormat(val)
        })
        // 是否有设置定时
        this.$store.state.hasTask = true
        // 转换时间
        // this.taskData.forEach((val, index, array) => {
        //   val.forEach((val2, index2, array2) => {
        //     array2[index2] = numToTimeFormat(val2)
        //   })
        // })
        console.log(this.taskData)
      } else if (this.advancedData.length !== 0) {
        // 是否有设置定时
        this.$store.state.hasTask = true
      } else {
        this.$store.state.hasTask = false
      }
    },
    go (e, type) {
      console.log('1542')
      if (this.VERSION === 1) {
        if (type === 'start') {
          this.startY = e.changedTouches['0'].pageY
          this.startX = e.changedTouches['0'].pageX
        } else if (type === 'end') {
          this.moveY = e.changedTouches['0'].pageY - this.startY
          this.moveX = e.changedTouches['0'].pageX - this.startX
          console.log('1543')
          let b = Math.abs(this.moveY) - Math.abs(this.moveX) // 大于0下滑
          console.log(this.moveY)
          if (this.moveY > 35 && b > 0) {
            // this.$refs.mainwrap.style.opacity = 0
            this.$router.push({ path: '/main' })
          }
        }
        if (type === 'click') {
          this.$router.push({ path: '/main' })
        }
      } else if (this.VERSION === 2) {
        this.$router.go(-1)
      }
    }
  },

  destroyed () {
    // clearInterval(this.Timer)
    clearInterval(this.Timer)
    clearTimeout(this.Timer2)
    clearTimeout(this.Timer3)
    clearTimeout(this.Timer4)
    clearTimeout(this.Timer5)
  }
}
</script>
<style lang="less" scoped>
@import "../../style/mixin.less";
@onColor: #21BFFC;
@offColor: #66FECB;

.open{
 color: @onColor !important;
}
.close{
  color: @offColor !important;
}
.stateBg{
  background: url(../../image/state/bg@3x.png) no-repeat !important;
  background-size: 100% 100% !important;
}
.heatingBg{
  background: url(../../image/state/basecircle-red@3x.png) no-repeat !important;
  background-size: 100% 100% !important;
}
.blue-toothstate {
  .fs;
  z-index: 10;
  background: url(../../image/state/bg-close@3x.png) no-repeat;
  background-size: 100% 100%;
  font-family: "lcddregular";
  .head {
    .wh(100%, 62px);
    line-height: 67px;
    position: relative;
    color: #B1B1B1;
    font-size: 35px;
    font-weight: 800;
    font-family: 'imp';
    letter-spacing: 4px;
  }
  .head_mask{
    .wh-l(100%, 62px);
    background: #000;
    position: absolute;
    left: 0;
    top: 0;
    opacity: .5;
  }
  .state_top {
    .wh(100%, 40%);
    color: #f2faff;
    position: relative;
    padding: 5px 15px;
  }
  .top-progress {
    .wh(100%,275px);
    position: absolute;
    left: 0px;
    top: 10%;
    padding-top: 5px;
    .progress {
      width: 260px; /* no */
      height: 260px; /* no */
      margin: 0 auto;
      position: relative;
      .circle_inside {
        .wh(100%, 100%);
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0.8;
      }
      .radius_bar {
        .wh(100%, 100%);
        position: absolute;
        left: 0;
        top: 0;
      }
      .circle {
        position: absolute;
        top: 10%;
        transform-origin: center;
        transform: rotate(-230deg);
        transition: stroke-dasharray 0.3s ease-in;
      }
      .tempeture {
        .wh-l(200px,200px);
        .f-center;
        font-family: "Arial";
        border-radius: 50%;
        text-align: center;
        line-height: 200px;
        font-size: 65px;
        background: url(../../image/state/basecircle@3x.png);
        color: #ffffff;
        background-size: 100%;
        span {
          font-size: 45px;
          line-height: 190px;
        }
      }
    }

    .bottom-side {
      .wh(100%, auto);
      font-family: "Arial";
      overflow: hidden;
      font-size: 12px;
      padding: 0 35px;
      position: relative;
      top: -20px;
      .left {
        .wh(80px, 100%);
        float: left;
        img {
          height: 25px;
          float: left;
        }
        span{
          .wh-l(40px, 25px);
          display: block;
          float: right;
          font-size: 16px;
          color: @offColor;
        }
      }
      .right {
        float: right;
        img {
          height: 25px;
        }
      }
    }
  }
  .bottom-progress {
    .wh(100%, 150px);
    position: absolute;
    left: 0%;
    bottom: 14%;
    display: flex;
    padding: 0 15px;
    justify-content: space-between;
    .time {
      width: 150px; /* no */
      height: 150px; /* no */
      border-radius: 50%;
      background: url(../../image/state/time@3x.png);
      background-size: 100%;
      position: relative;
      overflow: hidden;
      // margin: 15px;
      img {
        width: 100%;
        transform: rotate(40deg);
        transform-origin: 2px 46px;
        .wh(50px,50px);
        position: absolute;
        top: 29px;
        left: 75px;
      }
      .text {
        margin-top: 95px;
        font-size: 28px;
        font-weight: 400;
      }
    }
    .remain {
      width: 150px; /* no */
      height: 150px; /* no */
      border-radius: 50%;
      background: url(../../image/state/timing@3x.png);
      background-size: 100%;
      position: relative;
      .time_pointer{
        .wh(10px, 67px);
        position: absolute;
        top: 0%;
        left: 50%;
        margin-top: 13px;
        margin-left: -5px;
        transform: rotate(0deg);
        transform-origin: 50% 100%;
        img{
          width: 11px;
          position: absolute;
          left: 0;
          top: 0;
          transform: rotate(-35deg);
        }
      }
      .time-wrap{
        font-family: "Arial";
        .wh(100%, 45px);
        .f-center;
        top: 54%;
        .time-text {
          font-size: 13px;
          font-weight: bold;
          color: black;
        }
        .tit {
          font-size: 8px;
          color: #828282;
          font-weight: bold;
          margin-top: 5px;
        }
        .time_start{
          font-size: 10px;
          margin-top: 5px;
          color: #000;
        }
      }
      .time_text{
        .wh(60px, 80px);
        position: absolute;
        top: 0%;
        left: 38%;
        margin-top: 0px;
        margin-left: -20px;
        transform: rotate(0deg);
        transform-origin: 50% 100%;
        span{
          font-family: 'Arial';
          font-size: 12px;
          position: absolute;
          left: 0;
          top: -20px;
          .zoom-font(0.8);
        }
      }
      .circle {
        position: absolute;
        top: 10%;
        .wh(160px,160px);
        transform-origin: center;
        transform: rotate(-230deg);
        transition: stroke-dasharray 0.3s ease-in;
      }
    }
  }
  .arrow{
    .wh(32px, 30px);
    position: absolute;
    left: 50%;
    bottom: 2%;
    margin-left: -15px;
    img{
      width: 100%;
    }
    animation: scalopacity 1.5s infinite;
  }
  @keyframes scalopacity {
    50% {
      transform: scale(1.2);
      opacity: 1;
    }
    100% {
      transform: scale(1);
      opacity: 0;
    }
  }
}
</style>



