      <toast :describe="$t('toast.timingError')"   v-if="ToastType==='timingError'" position="center"></toast>
      <toast :describe="$t('toast.hotRunning')"   v-if="ToastType==='hotRunning'" position="center"></toast>
      <toast :describe="$t('toast.codeRunning')"   v-if="ToastType==='codeRunning'" position="center"></toast>
      <toast :describe="$t('toast.clockNumber')"   v-if="ToastType==='clockNumber'" position="center"></toast>
    <!-- 虚拟体验 -->
      <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='virtualExperience'">
        <div slot="confirmColumn" class="common_alert">
