<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import {
  powerSwitchRequest,
  powerSwitchStatusRequest,
  reNameRequest,
  commonPatternListDataRequest,
  countdownDataRequest,
  temperatureAndHumidityDataRequest,
  relieveControlDeviceRequest,
  queryTemperatureUnitRequest,
  controlDeviceRequest
} from '../service/method'
import {
  blueToothStateResponse,
  powerSwitchResponse,
  equipmentSwitchResponse,
  countdownDataResponse,
  temperatureAndHumidityDataResponse,
  // queryAlarmVoltageResponse,
  reNameResponse,
  setSystemLanguageResponse,
  commonPatternListDataResponse,
  queryTemperatureUnitResponse,
  controlDeviceResponse,
  deviceListResponse,
  unbundlingDeviceResponse
  } from '../service/getData'
// import { dataInteractionRequest, dataInteractionResponse } from '../service/interface.js'
import headTop from '../components/header/header'
import actionSheet from '../components/common/actionSheet'
import toast from '../components/common/toast'
import messageBox from '../components/common/messageBox'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  data () {
    return {
      swiperOption: {
        notNextTick: true,
        resistanceRatio: 0,
        pagination: {
          el: '.swiper-pagination',
          dynamicBullets: true
        }
      },
      fullHeight: null, // 屏幕高度
      renameValue: null, // 重命名值
      renameFlag: true, // 重命名值
      Timer: null, // 提示框显示计时器
      Timer2: null, // 定时器
      Timer3: null, // 定时器
      Timer4: null, // 定时器
      Timer5: null, // 定时器
      turnoffON: false, // 电源开关
      timer5: null, // 更多禁止点击
      timerNUmber: null,
      transition: true,
      timer6: null, // 计时器
      flag: true // 禁止连续点击
    }
  },

  created () {
    // 屏幕高度
    let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
    this.fullHeight = h

    if (this.$root.ISDEBUG && this.currentDevice === null) {
      this.$store.state.currentDevice = {
        name: '卧室的插座01',
        mac: '00-00-00-00-00-00',
        state: 1
      }
    }
  },

  mounted () {
    clearTimeout(this.Timer2)
    clearTimeout(this.Timer3)
    clearTimeout(this.Timer4)
    clearTimeout(this.Timer5)
    console.log('sgsgsgsgs')
    console.log(this.timingOff)
    // 开关状态
    this.$store.state.socketSwitch = this.$store.state.currentDevice.switch

    // 控制设备返回
    controlDeviceResponse(this.NAVIGATOR, this)

    // 设置系统语言返回
    setSystemLanguageResponse(this.NAVIGATOR, this)

    // 关闭蓝牙返回
    blueToothStateResponse(this.NAVIGATOR, this)

    // 连接断开返回
    equipmentSwitchResponse(this.NAVIGATOR, this, 'main')

    // 插座电源开关返回
    powerSwitchResponse(this.NAVIGATOR, this, 'main')

    // 定时数据返回
    commonPatternListDataResponse(this.NAVIGATOR, this, 'main')

    // 倒计时页面数据
    countdownDataResponse(this.NAVIGATOR, this, 'main')

    // 温度页面数据
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'main')

    // 查询温度单位返回
    queryTemperatureUnitResponse(this.NAVIGATOR, this)

    // 重命名返回
    reNameResponse(this.NAVIGATOR, this, 'main')

    // 获取设备列表返回（解决在此界面连接状态更新）
    deviceListResponse(this.NAVIGATOR, this, 'main')

    // 解绑设备返回
    unbundlingDeviceResponse(this.NAVIGATOR, this)

    if (this.VERSION === 1) {
      // 初始化接口
      this._initInterface()
    } else if (this.VERSION === 2) {
      // 控制设备请求
      controlDeviceRequest(this.NAVIGATOR, this, this.$store.state.currentDevice)
    }

    // 数据同步（新数据结构）
    // dataInteractionResponse(this.NAVIGATOR, (data) => {
    //   if (data.msgType === '00000202' && data.result === '01') {
    //     if (data.message === '01') {
    //       this.$store.state.socketSwitch = true
    //     } else if (data.message === '00') {
    //       this.$store.state.socketSwitch = false
    //     }
    //   }
    // })
    // setTimeout(() => {
    //   dataInteractionRequest(this.NAVIGATOR, this, {
    //     'msgType': '0000020101',
    //     'message': '01'
    //   })
    // }, 10000)
    // setTimeout(() => {
    //   let str = '{"msgType":"0000020c","result":"01","message":"01"}'
    //   window.dataInteractionResponse(str)
    // }, 2000)

    this.swiper.slideTo()
    if (this.$root.ISDEBUG) {
      // queryAlarmVoltageResponse(this.NAVIGATOR, this)
      // window.queryAlarmVoltageResponse(this.currentDevice.mac, true, 15)
      // countdownDataResponse(this.NAVIGATOR, this)
      // setTimeout(() => {
        // window.powerSwitchResponse(this.currentDevice.mac, false)
        // 设备断开
      // window.equipmentSwitchResponse(this.currentDevice.mac, 0, 1)
      // }, 4000)
      //  手机蓝牙断开
      // window.blueToothStateResponse(false)
      // setTimeout(() => {
        // window.powerSwitchResponse(this.currentDevice.mac, false)
        // window.equipmentSwitchResponse(this.currentDevice.mac, 1, 1)
        // window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":0,"Switchgear":true,"countdownSwitch":true}')
      // }, 8000)
      // setTimeout(() => {
        // window.powerSwitchResponse(this.currentDevice.mac, false)
        // window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":0,"Switchgear":true,"countdownSwitch":true}')
      // }, 12000)
    }
  },

  components: {
    headTop,
    messageBox,
    actionSheet,
    toast,
    swiper,
    swiperSlide
  },

  computed: {
    ...mapState([
      'actionSheet',
      'actionSheetType',
      'ToastType',
      'messageBox',
      'messageBoxType',
      'progressWidth',
      'equipmentData',
      'currentDevice',
      'currentRouter',
      'socketSwitch',
      'countDownOFF',
      'temperatureOFF',
      'spendingCountdownOff',
      'timingOff',
      'deviceState',
      'virtualExperience',
      'TemperatureUnit'
    ]),
    swiper () {
      return this.$refs.mySwiper.swiper
    },
    // 表单验证
    // validate () {
    //   let vName = /^[\u4e00-\u9fa5_]{2,8}|[_a-zA-Z0-9]{2,16}$/g.test(this.renameValue)
    //   return {
    //     active: vName
    //   }
    // },
    // 任务数量
    taskCount () {
      let count = 0
      if (this.countDownOFF) {
        count++
      }
      if (this.temperatureOFF) {
        count++
      }
      if (this.spendingCountdownOff) {
        count++
      }
      if (this.timingOff) {
        count++
      }
      return count
    }
  },

  methods: {
    ...mapMutations([
      'LOADING_SWITCH', 'TABLE_SWITCH', 'MESSAGEBOX_SWITCH'
    ]),

    // 初始化接口
    _initInterface () {
      // 插座电源开关状态请求
      powerSwitchStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac)

      this.Timer2 = setTimeout(() => {
        // 定时列表数据请求
        commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      }, 300)

      this.Timer3 = setTimeout(() => {
        // 倒计时数据请求
        countdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      }, 600)

      this.Timer4 = setTimeout(() => {
        // 温湿度页面数据
        temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      }, 900)

      this.Timer5 = setTimeout(() => {
        // 查询温度单位
        queryTemperatureUnitRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      }, 1200)
    },

    // 弹窗
    alertChoose (num) {
      switch (num) {
        case 0:
          this.renameFlag = true
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 2:
          this.$refs.renameValue.value = ''
          break
        case 3:
          if (this.renameFlag) {
            reNameRequest(this.NAVIGATOR, this, this.currentDevice.mac, this.renameValue)
            if (this.$root.ISDEBUG) {
              window.reNameResponse(this.currentDevice.mac, true)
            }
            this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          }
          break
        case 4:
          // 断开电源
          powerSwitchRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, false)

          if (this.$root.ISDEBUG) {
            this.$store.state.socketSwitch = false
          }

          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 5:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 6:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 7:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          this.$router.go(-3)
          break
        case 8:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
      }
    },

    chooseItem (num) {
      // console.log(num)
      switch (num) {
        case 1:
          if (this.currentDevice.state) {
            // this.LOADING_SWITCH(true)
            if (this.socketSwitch) {
              // this.MESSAGEBOX_SWITCH({type: 'closeSocketOpreation'})
              // 断开电源
              powerSwitchRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, false)

              if (this.$root.ISDEBUG) {
                this.$store.state.socketSwitch = false
              }
            } else {
              // 开启电源
              powerSwitchRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, true)

              if (this.$root.ISDEBUG) {
                this.$store.state.socketSwitch = true
              }
            }
          } else {
            this.MESSAGEBOX_SWITCH({type: 'Undevices'})
          }
          break
        case 2:
          if (!this.virtualExperience) {
            this.$router.push({path: '/main/timing'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
        case 3:
          if (!this.virtualExperience) {
            this.$router.push({path: '/main/countdown'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
        case 4:
          if (!this.virtualExperience) {
            this.$router.push({path: '/main/humidity'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
        case 5:
          if (!this.virtualExperience) {
            this.$router.push({path: '/main/powerSaving'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
        case 6:
          if (!this.virtualExperience) {
            console.log(this.virtualExperience)
            this.$router.push({path: '/main/cost'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
      }
    },

    // 设定任务
    navJump (num) {
      switch (num) {
        case 1:
          if (!this.virtualExperience) {
            this.$router.push({path: '/main/eq'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
        case 2:
          if (this.flag) {
            // 解除控制设备请求
            relieveControlDeviceRequest(this.NAVIGATOR, this, this.currentDevice.mac)
            this.$router.go(-1)
          }
          this.flag = false
          window.clearTimeout(this.timer6)
          this.timer6 = window.setTimeout(() => {
            this.flag = true
            window.clearTimeout(this.timer6)
          }, 500)
          break
        case 3:
          if (!this.virtualExperience) {
            this.$router.push({path: '/main/task'})
          } else {
            this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
          }
          break
        case 4:
          this.TABLE_SWITCH({type: 'mainSheet', flag: true})
          break
      }
    },

    // 插座状态
    goState () {
      if (!this.virtualExperience) {
        if (this.VERSION === 1) {
          this.$router.push({path: '/main/state'})
        } else if (this.VERSION === 2) {
          this.$router.push({path: '/main/wifiState'})
        }
      } else {
        this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
      }
    },
    // 更多
    mainSheetItem (num, e) {
      if (this.timerNUmber === this.timer5) {
        this.TABLE_SWITCH({type: 'mainSheet'})
        switch (num) {
          case 1:
            // 重命名
            if (!this.virtualExperience) {
              this.renameValue = this.$store.state.currentDevice.name
              this.MESSAGEBOX_SWITCH({type: 'renameOpreation'})
              console.log('renameclick')
            } else {
              this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
            }
            break
          case 2:
            if (!this.virtualExperience) {
              this.$router.push({path: 'main/set'})
            } else {
              this.MESSAGEBOX_SWITCH({type: 'virtualExperience'})
            }
            e.preventDefault()
            break
          case 3:
            break
        }
        this.timer5 = setTimeout(() => {
          clearTimeout(this.timer5)
          this.timerNUmber = this.timer5
          console.log('1735')
        }, 2000)
      }
    },

    focus () {
      this.$refs.divHeight.style.height = this.fullHeight
    },
    // 设备断开操作
    goSearch () {
      this.$router.push({path: '/add/connect'})
      this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
    },
    // 表单验证
    textRegExp () {
      this.renameValue = this.$refs.renameValue.value
      if (this.renameValue.length > 16) {
        this.renameValue = this.renameValue.slice(0, 15)
        this.$refs.renameValue.value = this.renameValue
      } else {
        this.$refs.renameValue.value = this.renameValue
      }
      if (!/^[\u4e00-\u9fa5]{2,8}|[a-zA-Z0-9]{2,16}$/g.test(this.renameValue)) {
        this.renameFlag = false
      } else {
        this.renameFlag = true
      }
    }
  },

  watch: {
    // ToastType (currentValue, oldValue) {
    //   clearTimeout(this.Timer)
    //   this.Timer = setTimeout(() => {
    //     this.$store.state.ToastType = null
    //   }, 2500)
    // }
    $route (currentValue) {
      if (currentValue) {
        // 设备连接、断开
        equipmentSwitchResponse(this.NAVIGATOR, this, 'main')
        console.log(this.timingOff)
        if (this.$root.ISDEBUG) {
          setTimeout(() => {
            // window.powerSwitchResponse(this.currentDevice.mac, true)
            // console.log('powerSwitchResponse开启')
          }, 4000)
        }
      }
    },
    deviceState (currentValue) {
      if (currentValue) {
        console.log('设备已连接')
        this.$store.state.ToastType = null
      } else {
        console.log('设备已断开')
        this.MESSAGEBOX_SWITCH({ type: 'turnOffBlueToothOpreation' })
      }
    },
    currentRouter (currentValue, oldValue) {
      console.log(oldValue)
      if (currentValue === '/main') {
        // 插座电源开关返回
        powerSwitchResponse(this.NAVIGATOR, this, 'main')
        // 温度页面数据
        temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'main')
        // 倒计时页面数据
        countdownDataResponse(this.NAVIGATOR, this, 'main')
        // 重命名返回
        reNameResponse(this.NAVIGATOR, this, 'main')
      }
      if (currentValue === '/main/state' && oldValue === '/main') {
        this.transition = false
      } else {
        this.transition = true
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../style/mixin.less';
#main_wrap{
  .fs;
  z-index: 11;
  .main{
    .fs;
    background: #FFF;
    z-index: 11;
    .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .746667rem;
        vertical-align: middle;
      }
    }
    .bluetooth2{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
    .operation{
      .wh(2rem, 100%);
      float: right;
      margin-right: .2rem;
      .icon{
        .wh-f(50%, 100%);
        position: relative;
        img{
          height: .746667rem;
          vertical-align: middle;        
        }
        .most{
          height: .146667rem;
        }
        .point{
          position: absolute;
          background: red;
          top: 5px;
          right:0;
          border-radius: 50%; 
          .wh(15px,15px);
          text-align:center;
          font-size: 10px;
          line-height: 15px;
        }
      }
    }
    .main_container{
      .fs-noTop; 
      .socket_state{
        .wh-l(3.333333rem, 1.066667rem);
        position: absolute;
        right: 0;
        top: .693333rem;
        z-index: 10;
        background-color: #DBEEFF;
        border-radius: 1.066667rem 0 0 1.066667rem;
        overflow: hidden;
        .left_column{
          .wh-f(30%, 100%);
          position: relative;
          img{
            height: .533333rem;
            .f-center;
          }
        }
        .right_column{
          .wh-f(70%, 100%);
          text-align: left;
          font-size: .453333rem;
        }
      }
      .mySwiper{
        .wh(100%, 100%);
      }
      .container_top{
        .wh(100%, 68%);
        position: relative;
        img{
          .wh(5.6rem, 5.6rem);
          position: absolute;
          top: 50%;
          left: 50%;
          margin-top: -2.8rem;
          margin-left: -2.8rem;
        }
      }
      .container_bottom{
        .wh(100%, 32%);
        position: relative;
        @columnHeight: 3.2rem;
        .column{
          .wh(100%, @columnHeight);
          li{
            .wh-f(100%/4, 100%);
            strong{
              .wh-l(100%, @columnHeight*0.8);
              display: block;
              img{
                height: 1.866667rem;
                vertical-align: middle;
              }
            }
            span{
              .wh(100%, @columnHeight*0.2);
              display: block;
              font-size: .4rem;
            }
          }
        }
        .bottom_wave{
          width: 100%;
          img{
            .wh(100%, auto);
            position: absolute;
            left: 0;
            bottom: 0;
          }
        }
      }    
    }
  }
  // 弹窗
  .describe{
    font-size: 12px;
    margin-bottom: 22px;
  }
  
  .input_wrap{
    .wh(190px, 100%);
    margin: 0 auto;
    position: relative;
    .rename_input{
      .wh(190px, 30px);
      position: absolute;
      left: 0;
      top: 0;
      input{
        .wh(100%, 100%);
        float: left;
        border-bottom: 2px solid #949494;
      }
      span{
        .wh(30px, 36px);
        position: absolute;
        top: -2px;
        right: -5px;
        img{
          .wh(15px, 15px);
          .f-center;
        }
      }
    }
    .rename_describe{
      .wh(190px, 30px);
      position: absolute;
      left: 0;
      bottom: 10px;
      span{
        .wh(120%, auto);
        display: block;
        font-size: 12px;
        .zoom-font(0.8);
        text-align: left;
        color: #CBCBCB;
      }
      .errorColor{
        color: #E32E61;
      }
    }
  }
  

  // 操作表
  .main_sheet{
    .wh(100%, 100%);
    font-size: .48rem;
    padding: .266667rem 0;
    li{
      .wh(100%, 100%/3);
      position: relative;
      span{
        .wh-l(6.4rem, 1.066667rem);
        display: block;
        border-radius: .533333rem;
        border: 1px solid #C3C3C3;
        .f-center;
        &:active{
          background: #4EA3FE;
          color: #FFF;
        }
      }
    }
  }
  .slidetop-enter-active, .slidetop-leave-active{
    transition: all .3s linear;
    
  }
  .slidetop-enter, .slidetop-leave-to{
    transform: translateY(100%);
  }
}

</style>



