<template>
  <div class="alarm">
    <head-top :title="$t('mainMorePage.threhold')" goBack="true"></head-top>
    <div class="alarm_container">
      <ul class="set_column no_margin">
        <li @click="selectList(1)">
          <span>{{$t('mainMorePage.voltage')}}</span>
          <em>
            <div class="describe"><strong>{{settingData.voltage}}</strong>V</div>
            <div class="after"></div>
          </em>
          <div class="bom-pixel"></div>
        </li>
        <li @click="selectList(2)">
          <span>{{$t('mainMorePage.current')}}</span>
          <em>
            <div class="describe"><strong>{{settingData.electricity}}</strong>A</div>
            <div class="after"></div>
          </em>
          <div class="bom-pixel"></div>
        </li>
        <li @click="selectList(3)">
          <span>{{$t('mainMorePage.power')}}</span>
          <em>
            <div class="describe"><strong>{{settingData.power}}</strong>w</div>
            <div class="after"></div>
          </em>
          <div class="bom-pixel"></div>
        </li>
      </ul>
      <div class="tip">{{$t('mainMorePage.Tip')}}</div>
    </div>

    <!-- 弹框: 电压、电流、功率 -->
    <!-- 告警电压值 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='voltageOpreation'">
      <div slot="pormptColumn" class="common_alert">
        <div class="alert_title">
          {{$t('messageBox.voltageValue')}}
        </div>
        <div class="alert_container">
          <div class="alert_column">
            <input type="number" ref="volta"  :class="setflag?'':'error'" @input="regexpnum('volta')">
            <span>V</span>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(1)" :style="{'background':setfull?'':'grey'}">
              {{$t('timerPage.save')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 告警电流值 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='electricityOpreation'">
      <div slot="pormptColumn" class="common_alert">
        <div class="alert_title">
          {{$t('messageBox.currentValue')}}
        </div>
        <div class="alert_container">
          <div class="alert_column">
            <input type="number" ref="elect" :class="setflag?'':'error'" @input="regexpnum('elect')">
            <span>A</span>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(2)" :style="{'background':setfull?'':'grey'}">
              {{$t('timerPage.save')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 告警功率值 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='powerOpreation'">
      <div slot="pormptColumn" class="common_alert">
        <div class="alert_title">
          {{$t('messageBox.powerValue')}}
        </div>
        <div class="alert_container">
          <div class="alert_column">
            <input type="number" ref="power" :class="setflag?'':'error'" @input="regexpnum('power')">
            <span>w</span>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(3)" :style="{'background':setfull?'':'grey'}">
              {{$t('timerPage.save')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../../components/header/header'
import messageBox from '../../../components/common/messageBox'
import { settingAlarmVoltageRequest, settingAlarmCurrentRequest, settingAlarmPowerRequest } from '../../../service/method'
import { settingAlarmVoltageResponse, settingAlarmCurrentResponse, settingAlarmPowerResponse } from '../../../service/getData'

export default {
  data () {
    return {
      setValue: null, // 输入框设置值
      setflag: true, //  输入小数验证
      setfull: false, //  输入为空验证
      setRange: true, //  输入范围验证
      inputlength: null // 输入长度验证
    }
  },

  mounted () {
    // 设置电压、电流、功率返回
    settingAlarmVoltageResponse(this.NAVIGATOR, this)
    settingAlarmCurrentResponse(this.NAVIGATOR, this)
    settingAlarmPowerResponse(this.NAVIGATOR, this)
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBoxType', 'settingData', 'currentDevice'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    selectList (num) {
      switch (num) {
        case 1:
          this.MESSAGEBOX_SWITCH({type: 'voltageOpreation'})
          break
        case 2:
          this.MESSAGEBOX_SWITCH({type: 'electricityOpreation'})
          break
        case 3:
          this.MESSAGEBOX_SWITCH({type: 'powerOpreation'})
          break
      }
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          if (!this.setflag || !this.setfull || !this.setRange) {
            this.$store.state.ToastType = 'alarmVotalTip'
          } else {
            // 设置电压警告值请求
            this.$store.state.settingData.voltage = this.setValue
            settingAlarmVoltageRequest(this.NAVIGATOR, this, this.currentDevice.mac, parseInt(this.setValue))
            if (this.$root.ISDEBUG) {
              settingAlarmVoltageResponse(this.NAVIGATOR, this)
              window.settingAlarmVoltageResponse(this.currentDevice.mac, true)
            }
          }
          break
        case 2:
          if (!this.setflag || !this.setfull || !this.setRange) {
            this.$store.state.ToastType = 'alarmElectTip'
          } else {
            this.$store.state.settingData.electricity = this.setValue
            // 设置电流警告值请求
            settingAlarmCurrentRequest(this.NAVIGATOR, this, this.currentDevice.mac, parseInt(this.setValue))
            console.log(this.setValue)
          }
          break
        case 3:
          if (!this.setflag || !this.setfull || !this.setRange) {
            this.$store.state.ToastType = 'alarmPowerTip'
          } else {
            // 设置功率警告请求
            this.$store.state.settingData.power = this.setValue
            settingAlarmPowerRequest(this.NAVIGATOR, this, this.currentDevice.mac, parseInt(this.setValue))
            console.log(this.setValue)
          }
          break
      }
      this.setValue = null
      this.setflag = true
      this.setRange = true
    },
    regexpnum (type) { // val-->输入框获取的值
      // let flag = true
      // this.$refs.volta.readOnly = false
      let val
      switch (type) {
        case 'volta':
          val = this.$refs.volta.value
          if (val.length > 3) {
            this.setValue = val.slice(0, 3)
            this.$refs.volta.value = this.setValue
          } else {
            this.setValue = val
          }
          break
        case 'elect':
          val = this.$refs.elect.value
          if (val.length > 2) {
            this.setValue = val.slice(0, 2)
            this.$refs.elect.value = this.setValue
          } else {
            this.setValue = val
          }
          if (this.setValue > 16 || this.setValue <= 0) {
            this.setRange = false
          } else {
            this.setRange = true
          }
          break
        case 'power':
          val = this.$refs.power.value
          if (val.length > 4) {
            this.setValue = val.slice(0, 4)
            this.$refs.power.value = this.setValue
          } else {
            this.setValue = val
          }
          if (this.setValue > 3600 || this.setValue <= 0) {
            this.setRange = false
          } else {
            this.setRange = true
          }
          break
        default:
          break
      }
      if (/^[1-9][0-9]{0,}$/.test(val)) {
        this.setflag = true
        this.setfull = true
      } else {
        this.setflag = false
        this.setfull = false
      }
      if (val === '') {
        this.setfull = false
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../../style/mixin.less';

.alarm{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .alarm_container{
    .fs-noTop;
    .no_margin{
      margin-bottom: 0;
    }
    .tip{
      .wh-l(100%, 38px);
      font-size: 12px;
      color: #C0C0C0;
      text-align: left;
      padding-left: 15px;
    }
  }
  .alert_column{
    .wh(100px, 60px);
    margin: 0 auto;
    input{
      .wh(80%, 85%);
      border-bottom: 1px solid #757575;
      float: left;
      font-size: 30px;
      padding-top: 10px;
      text-align: center
    }
    span{
      .wh(20%, 85%);
      display: block;
      font-size: 20px;
      float: left;
      line-height: 70px;
    }
  }
  .error {
    border-bottom: 2px solid #e1295d !important;
  }
  .determine{
    width: 100%;
    float: none;
  }
}

</style>



