// import { VERSION } from '../config/env'

import Vue from 'vue'
import Router from 'vue-router'
import Main from '../pages/main.vue'

// add
import add from '../pages/add/add.vue'
import connect from '../pages/add/connect.vue'
import state from '../pages/add/BlueToothState.vue'

// language
import language from '../pages/language/language.vue'

// equipment
import equipment from '../pages/equipment/equipment.vue'

// task
import task from '../pages/task/task.vue'

// mainSet
import mainSet from '../pages/mainSet/mainSet.vue'
import alarm from '../pages/mainSet/children/alarm.vue'
import recovery from '../pages/mainSet/children/recovery.vue'
import location from '../pages/mainSet/children/location.vue'

// timing
import timing from '../pages/timing/timing.vue'
import setTiming from '../pages/timing/children/setTime.vue'
import seniorSet from '../pages/timing/children/seniorSet.vue'

// countdown
import countdown from '../pages/countdown/countdown.vue'

// humidity
import humidity from '../pages/humidity/humidity.vue'

// powerSaving
import powerSaving from '../pages/powerSaving/powerSaving.vue'

// cost
import cost from '../pages/cost/cost.vue'
import data from '../pages/cost/data.vue'

// login
import login from '../pages/login/login.vue'
import emailLogin from '../pages/login/emailLogin.vue'
import emailSuccess from '../pages/login/emailSuccess.vue'
import forgot from '../pages/login/forgot.vue'
import forgotSuccess from '../pages/login/forgotSuccess.vue'

import home from '../pages/home.vue'

// addequipment
import Adddirver from '../pages/adddirver/adddirver.vue'
import Addwizard from '../pages/adddirver/addwizard.vue'
import Configwifi from '../pages/adddirver/configwifi.vue'
import Connectwifi from '../pages/adddirver/connectwifi.vue'
import Addsuccess from '../pages/adddirver/addsuccess.vue'
import Configfailed from '../pages/adddirver/configfailed.vue'
import NotConnect from '../pages/adddirver/notConnect.vue'
import chooseDevice from '../pages/adddirver/chooseDevice.vue'
import renameDevice from '../pages/adddirver/renameDevice.vue'
import addfailed from '../pages/adddirver/addfailed.vue'
import scanningfailed from '../pages/adddirver/scanningfailed.vue'

import WiFiState from '../pages/add/state.vue'

// 商城
import shop from '../pages/store/shop.vue'

// Router.prototype.goBack = function () {
//   this.isBack = true
//   window.history.go(-1)
// }

Vue.use(Router)

const vueRouter = new Router({
  routes: [
    // {
    //   path: '/',
    //   redirect: '/login'
    // },
    {
      path: '/login',
      component: login,
      children: [
        {
          path: 'language',
          component: language
        },
        {
          path: 'emailLogin',
          component: emailLogin,
          children: [
            {
              path: 'success',
              component: emailSuccess
            },
            {
              path: 'forgot',
              component: forgot,
              children: [
                {
                  path: 'success',
                  component: forgotSuccess
                }
              ]
            }
          ]
        }
      ]
    },
    {
      path: '/add',
      component: add,
      children: [
        {
          name: 'connect',
          path: 'connect',
          component: connect
        },
        {
          path: 'language',
          component: language
        },
        {
          path: 'state',
          component: state
        }
      ]
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      children: [
        {
          name: 'adddirver',
          path: 'adddirver',
          component: Adddirver,
          children: [
            {
              path: 'addwizard',
              component: Addwizard
            },
            {
              name: 'configwifi',
              path: 'configwifi',
              component: Configwifi
            },
            {
              name: 'connectwifi',
              path: 'connectwifi',
              component: Connectwifi
            },
            {
              path: 'addsuccess',
              component: Addsuccess
            },
            {
              path: 'configfailed',
              component: Configfailed
            },
            {
              path: 'notConnect',
              component: NotConnect
            },
            {
              name: 'choose',
              path: 'choose',
              component: chooseDevice
            },
            {
              name: 'rename',
              path: 'rename',
              component: renameDevice
            },
            {
              path: 'addfailed',
              component: addfailed
            },
            {
              path: 'scanningfailed',
              component: scanningfailed
            }
          ]
        },
        {
          path: 'language',
          component: language
        },
        {
          path: 'shop',
          component: shop
        }
      ]
    },
    {
      path: '/main',
      component: Main,
      children: [
        {
          path: 'eq',
          component: equipment
        },
        {
          path: 'state',
          component: state
        },
        {
          path: 'wifiState',
          component: WiFiState
        },
        {
          path: 'task',
          component: task
        },
        {
          path: 'set',
          component: mainSet,
          children: [
            {
              path: 'alarm',
              component: alarm
            },
            {
              path: 'recovery',
              component: recovery
            },
            {
              path: 'location',
              component: location
            },
            {
              path: 'language',
              component: language
            }
          ]
        },
        {
          path: 'timing',
          component: timing,
          children: [
            {
              name: 'timingSet',
              path: 'set',
              component: setTiming
            },
            {
              path: 'senior',
              name: 'seniorSet',
              component: seniorSet
            }
          ]
        },
        {
          path: 'countdown',
          component: countdown
        },
        {
          path: 'humidity',
          component: humidity
        },
        {
          path: 'powerSaving',
          component: powerSaving
        },
        {
          path: 'cost',
          component: cost,
          children: [
            {
              path: 'data',
              component: data
            }
          ]
        }
      ]
    }
  ]
})

// 解决路由多次返回白屏现象
vueRouter.beforeEach((to, from, next) => {
  // if (to.path === '/') {
  //   if (VERSION === 1) {
  //     // next('/main')
  //   } else if (VERSION === 2) {
  //     // next('/home')
  //   }
  //   return false
  // }
  next()
})

export default vueRouter



