import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
import mutations from './mutations'
import action from './action'

Vue.use(Vuex)

const state = {
  errorMsg: '', // 错误信息
  LoginState: null, // 登录状态
  currentRouter: null, // 当前路由
  actionSheet: false, // 操作表
  actionSheetType: null, // 操作表类型
  ToastType: null, // 消息提示框类型
  messageBox: false, // 弹框
  messageBoxType: null, // 弹框类型
  equipmentData: [], // 设备信息列表数据
  currentDevice: null, // 当前设备的详细信息
  progressWidth: 0, // 备份进度
  socketStatusData: null, // 插座状态数据
  socketSwitch: false, // 插座电源开关
  disabledeter: null, // 警告值输入验证
  countDownOFF: false, // 倒数计时开关
  temperatureOFF: false, // 温度开关
  timingOff: false, // 定时开关
  spendingCountdownOff: false, // 定量定费开关
  electLocal: '0元 0角 0分', // 本地电价
  settingData: {
    voltage: 0,
    electricity: 0,
    power: 0
  }, // 设置电压、电流、功率
  TemperatureUnit: 1, // 温度单位转换 1 ℃ 2 ℉
  montaryUint: 1, // 货币单位
  deviceState: 0, // 当前设备连接状态
  transitiondre: false, // 配网界面动画切换
  userInfo: null, // 用户信息
  virtualExperience: false, // 虚拟模式
  statePage: {
    stateCountdown: 0, // 状态界面倒计时总时长
    Timer: null, // 计时器
    countdownRotate: 0, // 倒计时指针旋转度数
    countdownTimeText: '00:00' // 倒计时时间
  },
  firstRunning: true, // 首次启动
  countDownRange: 0, // 倒计时圈范围
  hasTask: false, // 是否有设置定时
  currentTemperature: 0, // 制冷制热当前温度
  tTargetValue: 26, // 状态页温度目标值
  timingRotate: 0, // 状态页定时指针旋转度数
  isHeating: false, // 是否为制热模式
  disablemask: true, // 禁止点击遮罩
  startActive: false, // 启动结束状态
  tStartActive: false, // 温度启动结束状态
  hStartActive: false, // 湿度启动结束状态
  countdownValue: 0, // 倒计时时长
  powerActive: true, // 倒计时开关机状态
  taskMinnte: 0, // 距下次任务执行分钟
  taskHour: 0, // 距下次任务执行小时
  taskSwitch: false, // 距离当前最近定时开关机状态
  currentTask: '', // 当前任务
  ordinaryData: [], // 普通定时列表数据
  advancedData: [], // 进阶定时列表数据
  taskData: [], // 执行的任务数据
  deviceListData: [], // 设备列表数据
  noNetwork: true, // 网络断开提示
  findPage: null, // 发现局域网设备入口
  weekarr: [] // 星期字符串转换
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  action
})



