<template>
  <div class="email">
    <div class="email-login" :style = "{minHeight: fullHeight + 'px', marginTop: -offsetTop + 'px'}">
      <head-top  goBack="true"></head-top>
      <div class="login_en">
        <div class="login-top"></div>
        <div class="login_container">
          <div class="input_email">
            <input type="email" placeholder="Email" v-model="email">
          </div>
          <div class="input_password">
            <input :type="pwdType" placeholder="Password" v-model="password">
            <button :class="eyeClass" @click= "changeType">
              
            </button>
          </div>
          <button class="login_button"  :class="{loginActive: validate.loginActive}" :disabled="!validate.loginActive"   @click="login" >
            <span v-show="!logining">{{$t('loginPage.login')}}</span>
            <span v-show="logining" class="logining">
              <img src="../../image/adddirver/Loadingcirclesfreeicon.png" alt="">
              Logining...
            </span>
          </button>  
          <div class="help">
            <span class="forgot" @click="toforgot">Forgot Password？</span>
            <span class="signup" @click="tosignUp">Sign up</span>
          </div>
        </div>
      </div>
    </div>
    <transition name="slide-bottom">
      <div class="email-signup" v-show="signup" :style = "{minHeight: fullHeight + 'px', marginTop: -offsetTop + 'px'}">
        <div class="header">
          <div class="header-left" @click="tosignUp">
            <div class="back">
              <img src="../../image/global/back.png" alt="">
            </div>
          </div>
          <div class="header-center">
            Email Registration
          </div>
        </div>
        <div class="content">
          <div class="input">
            <input type="email" placeholder="Your Email, 6-18 characters" maxlength="18" v-model="signEmail">
          </div>
          <div class="input">
            <input type="text" placeholder="Your Name，1-16 characters" maxlength="16" v-model="name">
          </div>
          <div class="input">
            <input type="text" placeholder="Your Surname,1-16 characters" maxlength="16" v-model="surname">
          </div>
          <div class="input">
            <input type="password" placeholder="Your Password，6-16 characters" maxlength="16" v-model="signPassword">
          </div>
          <div class="input">
            <input type="password" placeholder="Your Password，6-16 characters" maxlength="16" v-model="rePassword">
          </div>
          <button class="next_button"  :class="{loginActive: validataSignUp.nextActive}" :disabled="!validataSignUp.nextActive" @click="confirmSignup">
            <span v-show="!nexting">{{$t('homePage.chooseNext')}}</span>
            <span v-show="nexting" class="nexting">
              <img src="../../image/adddirver/Loadingcirclesfreeicon.png" alt="">
              {{$t('homePage.chooseNext')}}ing...
            </span>
          </button>  
        </div>
      </div>
    </transition>

    <!-- messageBox-->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType ==='confirm'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_title">
          Confirm Your Email 
        </div>
        <div class="alert_describe">The email you want to register is:  {{this.signEmail}}, please confirm</div>
        <div class="alert_operation">
          <div class="determine cancle" @click="cancle">Cancel</div>
          <div class="determine OK" @click="next">OK</div>
        </div>
      </div>
    </messageBox>
    <transition :name="transitionName" mode="in-out">
      <router-view></router-view>
    </transition>
    <toast describe="Email or Password error, please try again." v-if="ToastType==='passwordError'" position="bottom"></toast>
    <toast :describe="email + ' is not registered, please register and log in!'"  v-if="ToastType==='notRegister'" position="bottom"></toast>
    <toast :describe="email +  'is not activated yet, please activate and  log in!'" v-if="ToastType==='notActive'" position="bottom"></toast>
    <toast describe= "undefined"  v-if="ToastType==='emailLoginFailed'" position="bottom"></toast>
    <toast describe= "Email format error, please enter the correct Email !"  v-if="ToastType==='formatError'" position="bottom"></toast>
    <toast :describe= "'The email:' + signEmail + ' has been registered, please login directly.'"  v-if="ToastType==='registeredError'" position="bottom"></toast>
    <toast describe= "We have sent a confirmation email to abc@example.com, please check!"  v-if="ToastType==='success'" position="bottom"></toast>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
import messageBox from '../../components/common/messageBox'
import toast from '../../components/common/toast'
import { emailLoginRequest, emailSignupRequest } from '../../service/method.js'
import { emailLoginResponse, emailSignupResponse } from '../../service/getData.js'
import { mapState, mapMutations } from 'vuex'
export default {
  data () {
    return {
      email: '', // Email
      password: '', // Password
      signEmail: '', // SignUp Email
      signPassword: '', // SignUp Password`
      rePassword: '', // Repeat Password
      name: '', // Name
      surname: '', // SurName
      pwdType: 'password', // 密文模式
      eyeClass: 'eye', // 眼睛类名
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      logining: false, // 登录中
      nexting: false, // 下一步
      signup: false
    }
  },
  mounted () {
    // 初始化延时，否则fullHeight值为0
    setTimeout(() => {
      let h = 0
      this.fullHeight = document.documentElement.clientHeight
      window.onresize = () => {
        h = document.documentElement.clientHeight
        // 软键盘是否弹起
        if (h < this.fullHeight) {
          this.keyboard = true
        } else {
          this.keyboard = false
        }
        // 解决小屏幕遮挡输入框
        if (h < 340) {
          this.offsetTop = 340 - h
        } else {
          this.offsetTop = 0
        }
      }
    }, 100)
    emailLoginResponse(this.NAVIGATOR, this)
    emailSignupResponse(this.NAVIGATOR, this)
  },
  components: {
    headTop,
    messageBox,
    toast
  },
  computed: {
    ...mapState([
      'ToastType', 'messageBox', 'messageBoxType'
    ]),
    // 验证状态
    validate () {
      let vEmail = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(this.email)
      return {
        loginActive: vEmail && this.password
      }
    },
    // 验证注册信息
    validataSignUp () {
      let vSEmail = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(this.signEmail) && /^([a-zA-Z0-9]|[@.]){6,18}$/.test(this.signEmail)
      let vSPassword = /^[\w]{6,16}$/.test(this.signPassword)
      let vSrePassword = /^[\w]{6,16}$/.test(this.rePassword)
      let vSName = /^[\w]{1,16}$/.test(this.name)
      let vSsurName = /^[\w]{1,16}$/.test(this.surname)
      let boolPassWord = this.signPassword === this.rePassword ? 'true' : false
      return {
        nextActive: vSEmail && vSPassword && vSrePassword && vSName && vSsurName && boolPassWord
      }
    }
  },
  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),
    changeType () {
      this.pwdType = this.pwdType === 'password' ? 'text' : 'password'
      this.eyeClass = this.eyeClass === 'eye' ? 'eye-active' : 'eye'
    },
    login () {
      emailLoginRequest(this.NAVIGATOR, this, this.email, this.password)
      this.logining = true
      if (this.$root.ISDEBUG) {
        setTimeout(() => {
          // window.emailLoginResponse(false, '0x801804')
        }, 2000)
      }
    },
    tosignUp () {
      this.signup = !this.signup
      // this.$router.push({path: '/login/emailLogin/signup'})
    },
    toforgot () {
      this.$router.push({path: '/login/emailLogin/forgot'})
    },
    confirmSignup () {
      this.MESSAGEBOX_SWITCH({type: 'confirm'})
    },
    cancle () {
      this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
    },
    next () {
      console.log(this.signEmail, this.signPassword, this.surname)
      emailSignupRequest(this.NAVIGATOR, this, this.signEmail, this.signPassword, this.surname)
      this.nexting = true
      this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
      if (this.$root.ISDEBUG) {
        // setTimeout(() => {
        //   window.emailSignupResponse(0, '0x801701')
        // }, 2000)
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../style/mixin.less";
.email{
  .fs; 
  background: #fff;
  z-index: 99;
  .email-login{
    .header{
      // background: #fff
    }
    .login_en{
      .fs-noTop;
      .login_container{
        width: 100%;
        padding-top: 40px;
        .input_email,.input_password{
          .wh-l(280px, 40px);
          margin: 0 auto;
          margin-bottom: 22px;
          border-bottom: 1px solid #ccc;
          text-align: left;
          input{
            font-size: 16px;
          }
        }
        .input_password{
          position: relative;
          button{
            .wh-l(28px,21px);
            background: #fff;
            position: absolute;
            top: 3px;
            right: 15px;
          }
          .eye{
            background-image: url(../../image/adddirver/Closed@3x.png);
            background-size: cover;
          }
          .eye-active{
            background-image: url(../../image/adddirver/first@3x.png);
            background-size: cover;
          }
        }
        .help{
          .wh(280px,40px);
          font-size: 16px;
          margin: 0 auto;
          span{
            color: #4EA2FF;
          }
          .forgot{
            float: left;
          }
          .signup{
            float: right;
          }
        }
      }
    }
  }
  .email-signup{
    .fs;
    background: #fff;
    .header{
      .wh-l(100%, 1.333333rem);
      background: @themeColor;
      font-size: .48rem;
      color: #F5FAFF;
      .header-left{
        .wh-f(10%, 100%);
        .back{
          .wh(1.066667rem, 100%);
          margin-left: .2rem;
          img{
            height: .533333rem;
            vertical-align: middle;
          }
        }
      }
      .header-center{
        .wh-f(90%, 100%);
        text-align: center;
      }
    }
    .content{
      width: 100%;
      padding-top: 15px;
      .input,{
        .wh-l(280px, 40px);
        margin: 0 auto;
        border-bottom: 1px solid #ccc;
        text-align: left;
        margin-bottom: 40px;
        input{
          font-size: 16px;
        }
      }
    }
  }
  .login_button, .next_button{
    width: 235px;
    height: 40px;
    color: #fff;
    background: #C7C7C7;
    margin: 18px auto;
    border-radius: 120px;
    box-shadow: 0px 3px 11px 1px #d2d2d2; /* no */
    font-size: 16px;
    .logining,.nexting{
      .wh(100%, 100%);
      line-height: 40px;
      display: block;
      font-size: 17px;
      color: #fff;
      vertical-align: middle;
      img{
        height: 17px;
        position: relative;
        top: 2px;
        left: -5px;
        transform-origin: center;
        animation: rotate 1s infinite;
      }
    }
  }
  .loginActive{
    background: @themeColor;
    box-shadow: 0px 3px 11px 1px #A3CFFE; /* no */
  }
  // 弹框
  .pormpt{
    .alert_title{
      font-family: 'Arial Negreta', 'Arial Normal', 'Arial';
      font-weight: 700;
      font-style: normal;
      font-size: 16px;
    }
    .alert_cost{
      font-family: 'Arial Normal', 'Arial';
      font-weight: 400;
      font-style: normal;
      font-size: 13px;
      color: #666666;
    }
    .alert_operation{
      border-top: 1px solid #999999;
      .btn{
        font-family: 'Arial Normal', 'Arial';
        font-weight: 400;
        font-style: normal;
        font-size: 16px;
        color: #4EA2FF;
      }
    }
  }
  .confirm{
    .alert_describe{
      border-bottom: 1px solid #666666;
      height: 48%;
    }
    .alert_operation{
      padding-top: 0;
      height: 50px;
      .cancle,.OK{
        line-height: 50px;
        font-size: 13px;
      }
      .cancle{
        color: #FF0000; 
        border-right: 1px solid #666666;
      }
      .OK{
        color: #4EA2FF;
      }
    }
  }
}
.slide-bottom-enter-active, .slide-bottom-leave-active {
  transition: all 0.5s linear;
    transform: translate3d(0, 0, 0);
  // transition: opacity 5s;
}
.slide-bottom-enter, .slide-bottom-leave-to /* .fade-leave-active below version 2.1.8 */ {
  // opacity: 0;
   transform: translate3d(0%, 100%, 0);
}
</style>




