<template>
  <div class="addsuccess">
       <head-top :title="$t('homePage.addsuccess')" ></head-top>
       <div class="contactimg">
         <img src="../../image/adddirver/right@3x.png" >
       </div>  
       <div class="prompt"> 
           <p>{{$t('homePage.addsuccessTips')}}</p>
       </div>  
       <div class="nextbtn" @click="next">
           <span>{{$t('homePage.immediately')}}</span>
       </div>
  </div>
</template>
<script>
import { } from 'vuex'
import headTop from '../../components/header/header'

export default {
  data () {
    return {time: '3:00'
    }
  },
  created () {

  },
  mounted () {

  },

  components: {
    headTop
  },

  computed: {},

  methods: {
    next () {
      this.$store.state.transitiondre = false
      this.$router.push({name: 'home', params: {binding: true}})
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../style/mixin.less';
  .addsuccess{
   .fs;
   background: #ffffff;
   .contactimg{
     .wh(100%,auto);
     margin-top:90px;
     display: flex;
     flex-direction: column;
     align-items: center;
     margin-bottom: 70px;
     img{
      .wh(98px,98px)
     }
    }
   .prompt{
        width:100%;
        text-align: center;
        p{
          color: #000;
          margin-top: 18px;
          font-size: 18px;
        }
    }
     .nextbtn{
      .wh-l(240px,40px);
      margin: 0 auto;
      margin-top: 150px;
      border-radius: 40px;
      background:  @themeColor;
      text-align: center;
      span{
          font-size: 17px;
          color: #fff;
          .wh(100%, 100%);
          display: block;
        }
    }
  }
 </style>


