      <head-top :title="$t('temperaturePage.tittle_v1')" goBack="true"></head-top>
      <div class="humidity_container">
        <ul class="timing_nav">
          <li v-if="hotCodeOrder">
            <div class="nav_button" :class="tabActive=='tab-container1'?'navActive':''" @click="navSwicth(1)">
              <span>{{$t('temperaturePage.hotMode')}}</span>
              <strong v-if="tabActive=='tab-container1'"></strong>
            </div>
