          <li v-if="hotCodeOrder">
            <div class="nav_button" :class="tabActive=='tab-container2'?'navActive':''" @click="navSwicth(2)">
              <span>{{$t('temperaturePage.clooingMode')}}</span>
              <strong v-if="tabActive=='tab-container2'"></strong>
            </div>
          </li>
        </ul>

        <mt-tab-container v-model="tabActive" :swipeable="false" class="main_column">
         
          <!-- 温度 -->
          <mt-tab-container-item id="tab-container1" v-if="hotCodeOrder">
            <div class="tab_column">
              <div class="set_top">
                <div class="circle_wrap customHeight">
                  <span>{{$t('temperaturePage.targettemperature')}}</span>
                  <span>
                    <strong>{{parseInt(tAlarmValue)}}</strong>
                    <em>{{TemperatureUnit === 1 ? '℃':'℉'}}</em>
                  </span>
                  <span>{{temperatureValue.toFixed(1)}}{{TemperatureUnit === 1 ? '℃':'℉'}}</span>
                  <span >{{$t('temperaturePage.currenttemperature')}}</span>
                  <span >
                    <em>{{TemperatureUnit === 1 ? '-10℃':'14℉'}}</em>
                    <em @click="setValue(1)" :style="{'background':!tStartActive?'':'grey'}">{{$t('temperaturePage.setTemperature')}}</em>
                    <em>{{TemperatureUnit === 1 ? '60℃':'140℉'}}</em>
                  </span>
                  <!-- <div class="progress">
                  <svg xmlns="http://www.w3.org/200/svg" :height="260/37.5 + 'rem'" :width="280/37.5 + 'rem'">
                    <circle ref="temperature" class="circle" :cx="144/37.5+'rem'" :cy="127/37.5+'rem'" :r="122.6/37.5+'rem'" fill="none" stroke="#4EA3FE" stroke-width=".373333rem" :stroke-dasharray="tStrokeDasharray" stroke-linecap="round"/>
                  </svg>
                </div> -->
                  <div class="progress">
                    <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px">
                      <circle class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round" />
                    </svg>
                    <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px" class="radius_bar">
                      <circle ref="countdown" class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#4EA3FE" stroke-width="13px" :stroke-dasharray="tStrokeDasharray" stroke-linecap="round" />
                    </svg>
                  </div>
                </div>
              </div>
              <div class="set_bottom">

                <!-- <div class="describe">
                  <span class="red" v-if="!tStartActive">{{$t('temperaturePage.tempertatureIsNotActivated')}}</span>
                  <span v-if="tStartActive">{{$t('temperaturePage.tempertatureRunning')}}</span>
                </div> -->
              </div>

              <div class="themeButton bom_position" v-if="!tStartActive">
                <span @click="buttonOperation(1)">{{$t('commonPage.run')}}</span>
              </div>
              <div class="themeButton_red bom_position" v-if="tStartActive">
                <span @click="buttonOperation(2)">{{$t('commonPage.end')}}</span>
              </div>
            </div>
          </mt-tab-container-item>

           <!-- 湿度 制冷模式-->
          <mt-tab-container-item id="tab-container2" v-if="hotCodeOrder">
            <div class="tab_column">
              <div class="set_top">
                <div class="circle_wrap customHeight">
                  <span>{{$t('temperaturePage.targettemperature')}}</span>
                  <span>
                    <strong>{{parseInt(hAlarmValue)}}</strong>
                    <em>{{TemperatureUnit === 1 ? '℃':'℉'}}</em>
                  </span>
                  <span>{{humidityValue.toFixed(1)}}{{TemperatureUnit === 1 ? '℃':'℉'}}</span>
                  <span>{{$t('temperaturePage.currenttemperature')}}</span>
                  <span>
                    <em>{{TemperatureUnit === 1 ? '-10℃':'14℉'}}</em>
                    <em @click="setValue(2)" :style="{'background':!hStartActive?'':'grey'}">{{$t('temperaturePage.setTemperature')}}</em>
                    <em>{{TemperatureUnit === 1 ? '60℃':'140℉'}}</em>
                  </span>
                  <!-- <div class="progress">
                  <svg xmlns="http://www.w3.org/200/svg" :height="260/37.5 + 'rem'" :width="280/37.5 + 'rem'">
                    <circle ref="temperature" class="circle" :cx="144/37.5+'rem'" :cy="127/37.5+'rem'" :r="122.6/37.5+'rem'" fill="none" stroke="#4EA3FE" stroke-width=".373333rem" :stroke-dasharray="hStrokeDasharray" stroke-linecap="round"/>
                  </svg>
                </div> -->
                  <div class="progress">
                    <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px">
                      <circle class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round" />
                    </svg>
                    <svg xmlns="http://www.w3.org/200/svg" height="260px" width="280px" class="radius_bar">
                      <circle ref="countdown" class="circle" cx="143px" cy="126px" :r="radius+'px'" fill="none" stroke="#4EA3FE" stroke-width="13px" :stroke-dasharray="hStrokeDasharray" stroke-linecap="round" />
                    </svg>
                  </div>
                </div>
              </div>
              <div class="set_bottom">

                <!-- <div class="describe">
                  <span class="red" v-if="!hStartActive">{{$t('temperaturePage.tempertatureIsNotActivated')}}</span>
                  <span v-if="hStartActive">{{$t('temperaturePage.tempertatureRunning')}}</span>
                </div> -->
              </div>

              <div class="themeButton bom_position" v-if="!hStartActive">
                <span @click="buttonOperation(3)">{{$t('commonPage.run')}}</span>
              </div>
              <div class="themeButton_red bom_position" v-if="hStartActive">
                <span @click="buttonOperation(4)">{{$t('commonPage.end')}}</span>
              </div>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </div>
    <!-- 弹窗 -->
    <!-- 输入告警温度值 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='tAlarmValueOpreation'">
      <div slot="pormptColumn" class="common_alert currentalert">
        <div class="alert_title">
          {{$t('messageBox.temperatureValue')}}
        </div>
        <div class="alert_container">
          <div class="alert_temperature">
            <div class="center_column">
              <input type="number" @focus="focus" ref="tAlarmValueTemp" :class="tAlarmValueFlag?'':'error'" @input="regexpnum(1)">
            </div>
            <div class="right_column">
              <em>{{TemperatureUnit === 1 ? '℃':'℉'}}</em>
            </div>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(1)" :style="{'background':disabledeter?'':'grey'}">
            {{$t('timerPage.save')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 输入告警湿度值 -->
    <messageBox setHeight="180" setType="pormpt" v-show="messageBoxType==='hAlarmValueOpreation'">
      <div slot="pormptColumn" class="common_alert currentalert">
        <div class="alert_title">
          {{$t('messageBox.temperatureValue')}}
        </div>
        <div class="alert_container">
          <div class="alert_temperature">
            <div class="center_column">
              <input type="number" @focus="focus" ref="hAlarmValueTemp" :class="hAlarmValueFlag?'':'error'" @input="regexpnum(2)">
            </div>
            <div class="right_column">
              <em>{{TemperatureUnit === 1 ? '℃':'℉'}}</em>
            </div>
          </div>
        </div>
        <div class="alert_operation">
          <div class="determine">
            <span @click="alertChoose(2)" :style="{'background':disabledeter?'':'grey'}">
             {{$t('timerPage.save')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 结束温度告警 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='tEndAlarmOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
          {{$t('messageBox.quitTargetTemperature')}}
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(3)">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>

    <!-- 结束湿度告警 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='hEndAlarmOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">
          {{$t('messageBox.quitTargetTemperature')}}
        </div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">
              {{$t('messageBox.cancel')}}
            </span>
          </div>
          <div class="determine">
            <span @click="alertChoose(4)">
              {{$t('messageBox.confirm')}}
            </span>
          </div>
        </div>
      </div>
    </messageBox>
    <!-- 温度已启动设置警告温度 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='sTemperatureRunningOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.sTemperatureRunningOpreation')}}</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)">{{$t('messageBox.confirm')}}</div>
        </div>
      </div>
    </messageBox>
    <!-- 湿度已启动设置警告温度 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='humidityRunningOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.humidityRunningOpreation')}}</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)">{{$t('messageBox.confirm')}}</div>
        </div>
      </div>
    </messageBox>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { } from 'mint-ui'
import headTop from '../../components/header/header'
import messageBox from '../../components/common/messageBox'
import toast from '../../components/common/toast'
import { temperatureAndHumidityDataResponse, alarmTemperatureValueResponse } from '../../service/getData'
import { temperatureAndHumidityDataRequest, alarmTemperatureValueRequest, powerSwitchStatusRequest } from '../../service/method'
import { getStore } from '../../config/utils'
export default {
  data () {
    return {
      fullHeight: null, // 屏幕高度
      tabActive: 'tab-container1', // 导航栏切换
      radius: 119, // svg进度条半径
      temperatureValue: 0, // 温度值
      // tStartActive: false, // 温度启动结束状态
      tAlarmValue: this.$store.state.TemperatureUnit === 1 ? 26 : 79, // 温度告警值
      tAlarmValueTemp: 0, // 输入框温度告警值
      tAlarmValueFlag: true, // 温度值验证
      humidityValue: 0, // 湿度值
      // hStartActive: false, // 湿度启动结束状态
      hAlarmValue: this.$store.state.TemperatureUnit === 1 ? 26 : 79, // 湿度告警值
      hAlarmValueTemp: 0, // 输入框湿度告警值
      hAlarmValueFlag: true, // 湿度值验证
      timer: null, // 定时器
      inputlength: null,
      hotCodeOrder: true // 默认显示制
    }
  },
  created () {
    // 屏幕高度
    let h =
      window.innerHeight ||
      document.documentElement.clientHeight ||
      document.body.clientHeight
    this.fullHeight = h
  },

  components: {
    headTop,
    messageBox,
    toast
  },

  computed: {
    ...mapState(['ToastType', 'messageBoxType', 'currentDevice',
      'disabledeter', 'temperatureOFF', 'TemperatureUnit', 'tStartActive', 'hStartActive']),
    // 原度数为280
    tStrokeDasharray () {
      let percentage
      if (this.TemperatureUnit === 1) {
        percentage = (parseInt(this.temperatureValue) + 10) / 70
        void (this.temperatureValue > 60 ? percentage = 1 : percentage)
        void (this.temperatureValue < -10 ? percentage = 0 : percentage)
      } else {
        percentage = (parseInt(this.temperatureValue) - 14) / 126
        void (this.temperatureValue > 140 ? percentage = 1 : percentage)
        void (this.temperatureValue < 14 ? percentage = 0 : percentage)
      }
      return (
        Math.floor(2 * Math.PI * this.radius) *
        percentage * 280 / 360 +
        ',10000'
      )
    },
    hStrokeDasharray () {
      let percentage
      if (this.TemperatureUnit === 1) {
        percentage = (parseInt(this.humidityValue) + 10) / 70
        void (this.humidityValue > 60 ? percentage = 1 : percentage)
        void (this.humidityValue < -10 ? percentage = 0 : percentage)
      } else {
        percentage = (parseInt(this.humidityValue) - 14) / 126
        void (this.humidityValue > 140 ? percentage = 1 : percentage)
        void (this.humidityValue < 14 ? percentage = 0 : percentage)
      }
      return (
        Math.floor(2 * Math.PI * this.radius) *
        percentage * 280 / 360 +
        ',10000'
      )
    }
  },

  mounted () {
    console.log(this.hAlarmValue)
    // 模拟后台响应数据
    if (this.$root.ISDEBUG) {
      setTimeout(() => {
        // console.log(this.currentDevice.state)
        // window.temperatureAndHumidityDataResponse(this.currentDevice.mac, '{"temperature":{"currentValue":0,"hotAlarmValue":0,"hotAlarmSwitch":false,"codeAlarmValue":0,"codeAlarmSwitch":true}}')
        // window.alarmHumidityValueResponse(this.currentDevice.mac, false)
        // window.alarmTemperatureValueResponse(this.currentDevice, true, 1, false)
        // console.log(this.hStartActive)
      }, 2000)
    }
    // 温湿度页面数据
    temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    // 温湿度页面数据返回
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'humidity')
    // 设置告警温度值响应
    alarmTemperatureValueResponse(this.NAVIGATOR, this, 'humidity')
    // 从本地读取页面数据
    let temperatureData = JSON.parse(getStore('temperatureData'))
    if (temperatureData) {
      // this.hStartActive = temperatureData.temperature.codeAlarmSwitch
      // this.tStartActive = temperatureData.temperature.hotAlarmSwitch
      if (this.TemperatureUnit === 1) {
        this.hAlarmValue = parseInt(temperatureData.temperature.codeAlarmValue)
        this.tAlarmValue = parseInt(temperatureData.temperature.hotAlarmValue)
        this.temperatureValue = parseFloat((temperatureData.temperature.currentValue).toFixed(1))
        this.humidityValue = parseFloat((temperatureData.temperature.currentValue).toFixed(1))
      } else if (this.TemperatureUnit === 2) {
        // this.hAlarmValue = Math.round(temperatureData.temperature.codeAlarmValue * 1.8 + 32)
        this.hAlarmValue = Math.round(temperatureData.temperature.codeAlarmValue * 1.8 + 32)
        this.tAlarmValue = Math.round(temperatureData.temperature.hotAlarmValue * 1.8 + 32)
        this.temperatureValue = parseFloat((temperatureData.temperature.currentValue * 1.8 + 32).toFixed(1))
        this.humidityValue = parseFloat((temperatureData.temperature.currentValue * 1.8 + 32).toFixed(1))
      }
    }
    // if (this.TemperatureUnit === 1) {
    //   this.tAlarmValue = 26 // 温度值
    //   this.hAlarmValue = 26 // 湿度值
    // } else {
    //   this.tAlarmValue = 79 // 温度值
    //   this.hAlarmValue = 79 // 湿度值
    // }
    if (this.hStartActive && !this.tStartActive) {
      // self.hotCodeOrder = true
      this.tabActive = 'tab-container2'
    } else {
      // self.hotCodeOrder = true
      this.tabActive = 'tab-container1'
    }
    // // 设置告警湿度值响应
    // alarmHumidityValueResponse(this.NAVIGATOR, this)
  },

  methods: {
    ...mapMutations(['MESSAGEBOX_SWITCH']),

    navSwicth (num) {
      this.tabActive = 'tab-container' + num
    },

    // 设置告警温湿度值
    setValue (num) {
      if (num === 1) {
        this.$refs.tAlarmValueTemp.value = ''
        if (this.tAlarmValue) {
          this.$store.state.disabledeter = true
        }
        if (!this.tStartActive) {
          this.tAlarmValueFlag = true
          this.tAlarmValueTemp = this.tAlarmValue
          this.MESSAGEBOX_SWITCH({ type: 'tAlarmValueOpreation' })
          this.$store.state.disablemask = true
        }
      } else {
        this.$refs.hAlarmValueTemp.value = ''
        if (!this.hStartActive) {
          this.hAlarmValueFlag = true
          this.hAlarmValueTemp = this.hAlarmValue
          this.MESSAGEBOX_SWITCH({ type: 'hAlarmValueOpreation' })
          this.$store.state.disablemask = true
        }
        if (this.hAlarmValue) {
          this.$store.state.disabledeter = true
        }
      }
    },

    focus () {
      this.$refs.divHeight.style.height = this.fullHeight
    },

    // 启动、结束
    buttonOperation (num) {
      if (this.currentDevice.state) {
        let tvalue
        switch (num) {
          case 1:
            // if (this.tAlarmValue === '') {
            //   this.$store.state.ToastType = 'setTemperature'
            //   return false
            // }
            // 设置告警温度值请求
            if (this.TemperatureUnit === 1) {
              tvalue = this.tAlarmValue
              if (parseInt(tvalue) > 60 || parseInt(tvalue) < -10) {
                this.$store.state.ToastType = 'tAlarmValueTip'
                return false
              }
            } else {
              if (parseInt(this.tAlarmValue) > 140 || parseInt(this.tAlarmValue) < 14) {
                this.$store.state.ToastType = 'tAlarmValueTip2'
                return false
              }
              tvalue = (this.tAlarmValue - 32) / 1.8
              console.log(this.tAlarmValue)
            }
            if (this.hStartActive) {
              this.$store.state.ToastType = 'hotRunning'
            } else {
              alarmTemperatureValueRequest(this.NAVIGATOR, this, this.currentDevice.mac, !this.tStartActive, Number(tvalue), 1)
              // window.alarmTemperatureValueResponse(this.currentDevice, true, 1, true)
              console.log(Number(tvalue))
            }
            if (this.$root.ISDEBUG) {
              this.$store.state.tStartActive = true
            }
            break
          case 2:
            this.MESSAGEBOX_SWITCH({ type: 'tEndAlarmOpreation' })
            this.$store.state.disablemask = true
            break
          case 3:
            // if (this.hAlarmValue === '') {
            //   this.$store.state.ToastType = 'setTemperature'
            //   return false
            // }
            if (this.TemperatureUnit === 1) {
              tvalue = this.hAlarmValue
              if (parseInt(this.hAlarmValue) > 60 || parseInt(this.hAlarmValue) < -10) {
                this.$store.state.ToastType = 'tAlarmValueTip1'
                return false
              }
            } else {
              if (parseInt(this.hAlarmValue) > 140 || parseInt(this.hAlarmValue) < 14) {
                this.$store.state.ToastType = 'tAlarmValueTip2'
                return false
              }
              tvalue = (this.hAlarmValue - 32) / 1.8
            }
            if (this.tStartActive) {
              this.$store.state.ToastType = 'codeRunning'
            } else {
              // 设置告警湿度值请求
              alarmTemperatureValueRequest(this.NAVIGATOR, this, this.currentDevice.mac, !this.hStartActive, Number(tvalue), 2)
              console.log(Number(tvalue))
              if (this.$root.ISDEBUG) {
                this.$store.state.hStartActive = true
              }
            }
            break
          case 4:
            this.MESSAGEBOX_SWITCH({ type: 'hEndAlarmOpreation' })
            this.$store.state.disablemask = true
            break
        }
      } else {
        this.MESSAGEBOX_SWITCH({ type: 'Undevices' })
      }
    },

    // 弹窗: 取消、温度告警保存、湿度告警保存、结束温度告警、结束湿度告警
    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
          this.$store.state.disablemask = false
          break
        case 1:
          this.tAlarmValueTemp = this.$refs.tAlarmValueTemp.value
          if (this.TemperatureUnit === 1) {
            if ((this.tAlarmValueTemp < -10 || this.tAlarmValueTemp > 60) || !this.tAlarmValueTemp) {
              this.tAlarmValueFlag = false
              this.$store.state.ToastType = 'tAlarmValueTip'
              return false
            } else {
              this.tAlarmValue = this.tAlarmValueTemp
              this.tAlarmValueFlag = true
              this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
              this.$store.state.disablemask = false
            }
          } else {
            if ((this.tAlarmValueTemp < 14 || this.tAlarmValueTemp > 140) || !this.tAlarmValueTemp) {
              this.tAlarmValueFlag = false
              this.$store.state.ToastType = 'tAlarmValueTip2'
              return false
            } else {
              this.tAlarmValue = this.tAlarmValueTemp
              this.tAlarmValueFlag = true
              this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
              this.$store.state.disablemask = false
            }
          }
          break
        case 2:
          this.hAlarmValueTemp = this.$refs.hAlarmValueTemp.value
          if (this.TemperatureUnit === 1) {
            if ((this.hAlarmValueTemp < -10 || this.hAlarmValueTemp > 60) || /^[0][0-9]{1,}$/.test(this.hAlarmValueTemp) || !this.hAlarmValueTemp) {
              this.hAlarmValueFlag = false
              this.$store.state.ToastType = 'tAlarmValueTip'
              return false
            } else {
              this.hAlarmValue = this.hAlarmValueTemp
              this.hAlarmValueFlag = true
              this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
              this.$store.state.disablemask = false
            }
          } else {
            if ((this.hAlarmValueTemp < 14 || this.hAlarmValueTemp > 140) || /^[0][0-9]{1,}$/.test(this.hAlarmValueTemp) || !this.hAlarmValueTemp) {
              this.hAlarmValueFlag = false
              this.$store.state.ToastType = 'tAlarmValueTip2'
              return false
            } else {
              this.hAlarmValue = this.hAlarmValueTemp
              this.hAlarmValueFlag = true
              this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
              this.$store.state.disablemask = false
            }
          }
          break
        case 3:
          // this.tStartActive = false
          let tvalue
          if (this.TemperatureUnit === 1) {
            tvalue = this.tAlarmValue
          } else {
            tvalue = (this.tAlarmValue - 32) / 1.8
          }
          alarmTemperatureValueRequest(this.NAVIGATOR, this, this.currentDevice.mac, !this.tStartActive, Number(tvalue), 1)
          if (this.$root.ISDEBUG) {
            this.$store.state.tStartActive = false
          }
          console.log(!this.tStartActive, parseInt(tvalue))
          this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
          this.$store.state.disablemask = false
          break
        case 4:
          // 设置告警湿度值请求
          if (this.TemperatureUnit === 1) {
            tvalue = this.hAlarmValue
          } else {
            tvalue = (this.hAlarmValue - 32) / 1.8
          }
          alarmTemperatureValueRequest(this.NAVIGATOR, this, this.currentDevice.mac, !this.hStartActive, Number(tvalue), 2)
          this.MESSAGEBOX_SWITCH({ type: this.messageBoxType })
          this.$store.state.disablemask = false
          break
      }
    },
    regexpnum (num) { // val-->输入框获取的值 num-->1温度2适度
      let val
      switch (num) {
        case 1 :
          val = this.$refs.tAlarmValueTemp.value
          if (/^[0][1-9]{1,}$/.test(val)) {
            this.$refs.tAlarmValueTemp.value = val.slice(1, val.length)
          }
          console.log(val, typeof val)
          if (this.TemperatureUnit === 1) {
            if (parseInt(val) >= 0 && val.length > 2) {
              if (/\./g.test(val) && val.length < 4) {
                let len = val.length
                console.log(len)
                this.tAlarmValueTemp = val.slice(0, 1)
                this.$refs.tAlarmValueTemp.value = this.tAlarmValueTemp
              } else {
                this.tAlarmValueTemp = val.slice(0, 2)
                this.$refs.tAlarmValueTemp.value = this.tAlarmValueTemp
              }
            } else if (parseInt(val) < 0 && val.length > 3) {
              if (/\./g.test(val) && val.length <= 4) {
                let len = val.length
                console.log(len)
                this.tAlarmValueTemp = val.slice(0, 2)
                this.$refs.tAlarmValueTemp.value = this.tAlarmValueTemp
              } else {
                this.tAlarmValueTemp = val.slice(0, 3)
                this.$refs.tAlarmValueTemp.value = this.tAlarmValueTemp
              }
            } else {
              this.tAlarmValueTemp = val
            }
            if (this.tAlarmValueTemp > 60 || this.tAlarmValueTemp < -10) { // || (val < 50 || val > 100)
              this.$store.state.disabledeter = false
              if (val) {
                this.tAlarmValueFlag = false
              } else {
                this.tAlarmValueFlag = true
              }
            } else {
              this.$store.state.disabledeter = true
              this.tAlarmValueFlag = true
            }
          } else {
            if (val.length > 3) {
              if (/\./g.test(val) && val.length <= 4) {
                let len = val.length
                console.log(len)
                this.tAlarmValueTemp = val.slice(0, 2)
                this.$refs.tAlarmValueTemp.value = this.tAlarmValueTemp
              } else {
                this.tAlarmValueTemp = val.slice(0, 3)
                this.$refs.tAlarmValueTemp.value = this.tAlarmValueTemp
              }
            } else {
              this.tAlarmValueTemp = val
            }
            if (this.tAlarmValueTemp > 140 || this.tAlarmValueTemp < 14) { // || (val < 50 || val > 100)
              this.$store.state.disabledeter = false
              if (val) {
                this.tAlarmValueFlag = false
              } else {
                this.tAlarmValueFlag = true
              }
            } else {
              this.$store.state.disabledeter = true
              this.tAlarmValueFlag = true
            }
          }
          break
        case 2 :
          val = this.$refs.hAlarmValueTemp.value
          if (/^[0][1-9]{1,}$/.test(val)) {
            this.$refs.hAlarmValueTemp.value = val.slice(1, val.length)
          }
          if (this.TemperatureUnit === 1) {
            if (parseInt(val) >= 0 && val.length > 2) {
              if (/\./g.test(val) && val.length < 4) {
                let len = val.length
                console.log(len)
                this.hAlarmValueTemp = val.slice(0, 1)
                this.$refs.hAlarmValueTemp.value = this.hAlarmValueTemp
              } else {
                this.hAlarmValueTemp = val.slice(0, 2)
                this.$refs.hAlarmValueTemp.value = this.hAlarmValueTemp
              }
            } else if (parseInt(val) < 0 && val.length > 3) {
              if (/\./g.test(val) && val.length <= 4) {
                let len = val.length
                console.log(len)
                this.hAlarmValueTemp = val.slice(0, 2)
                this.$refs.hAlarmValueTemp.value = this.hAlarmValueTemp
              } else {
                this.hAlarmValueTemp = val.slice(0, 3)
                this.$refs.hAlarmValueTemp.value = this.hAlarmValueTemp
              }
            } else {
              this.hAlarmValueTemp = val
            }
            if (this.hAlarmValueTemp > 60 || this.hAlarmValueTemp < -10) {
              this.$store.state.disabledeter = false
              if (val) {
                this.hAlarmValueFlag = false
              } else {
                this.hAlarmValueFlag = true
              }
            } else {
              this.$store.state.disabledeter = true
              this.hAlarmValueFlag = true
            }
          } else {
            if (val.length > 3) {
              if (/\./g.test(val) && val.length <= 4) {
                let len = val.length
                console.log(len)
                this.hAlarmValueTemp = val.slice(0, 2)
                this.$refs.hAlarmValueTemp.value = this.hAlarmValueTemp
              } else {
                this.hAlarmValueTemp = val.slice(0, 3)
                this.$refs.hAlarmValueTemp.value = this.hAlarmValueTemp
              }
            } else {
              this.hAlarmValueTemp = val
            }
            if (this.hAlarmValueTemp > 140 || this.hAlarmValueTemp < 14) {
              this.$store.state.disabledeter = false
              if (val) {
                this.hAlarmValueFlag = false
              } else {
                this.hAlarmValueFlag = true
              }
            } else {
              this.$store.state.disabledeter = true
              this.hAlarmValueFlag = true
            }
          }
          break
      }
    }
  },

  watch: {
    temperatureValue (currentValue, oldValue) {
      // console.log(123456)
    },
    tStartActive (currentValue, oldValue) {
      if (currentValue) {
        clearInterval(this.timer)
        this.timer = setInterval(() => {
          // 温湿度页面数据
          temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
        }, 60000)
      } else {
        clearInterval(this.timer)
      }
    },
    hStartActive (currentValue, oldValue) {
      if (currentValue) {
        clearInterval(this.timer)
        this.timer = setInterval(() => {
          // 温湿度页面数据
          temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
        }, 60000)
      } else {
        clearInterval(this.timer)
      }
    }
  },
  destroyed () {
    clearInterval(this.timer)
    powerSwitchStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac)
    // temperatureAndHumidityDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
  }
}
</script>

