              margin: 0 auto;
              position: relative;
              // background: url(../../image/humidity/circle.png) 15px 10px no-repeat;
              // background-size: 250px auto;
              span {
                display: block;
                position: relative;
                z-index: 1;
              }
              span:nth-child(1) {
                .wh(100%, 25%);
