                line-height: 2.666667rem;
              }
              span:nth-child(2) {
                .wh(100%, 35%);
                strong {
                  font-size: 1.653333rem;
                }
                em {
                  font-size: 0.96rem;
                }
              }
              span:nth-child(3) {
                .wh(100%, 10%);
                line-height: 0.8rem;
                color: #999999;
                font-size: 17px;
                font-weight: 600;
              }
              span:nth-child(4) {
                .wh(100%, 10%);
                font-size: 0.4rem;
                color: #999999;
                line-height: 1.013333rem;
              }
              span:nth-child(5) {
                .wh(100%, 20%);
                position: relative;
                padding-top: 15px;
                em:nth-child(1), em:nth-child(3) {
                  font-size: 10px;
                  color: #585858;
                  font-weight: 600;
                }
                em:nth-child(2) {
                  .wh-l(auto, 0.8rem);
                  font-size: 0.32rem;
                  // background: url(../../image/humidity/Smallbox.png) 0 0 no-repeat;
                  // background-size: 2.72rem .8rem;
                  background: @themeColor;
                  border-radius: 5px;
                  color: #def0ff;
                  padding: 8px;
                  // position: absolute;
                  // left: 50%;
                  // top: .586667rem;
                  // margin-left: -1.36rem;
                }
              }
              .progress {
                .fs;
                .radius_bar {
                  position: absolute;
                  left: 0;
                  top: 0;
                }
                .circle {
                  transform-origin: center;
                  transform: rotate(-230deg);
                  transition: stroke-dasharray 0.3s ease-in;
                }
              }
            }
          }
          .set_bottom {
            .wh(100%, 40%);
            position: relative;
            .describe {
              .wh-l(260px, 0.8rem);
              font-size: 0.4rem;
              position: absolute;
              left: 50%;
              margin-left: -130px;
              bottom: 2.4rem;
              .red {
                color: #e7527b;
              }
            }
          }
        }
      }
    }
  }
  .alert_temperature {
    .wh(100%, 100%);
    margin: 0 auto;
    font-size: 22px;
    color: #5c5c5c;
    font-weight: bold;
    line-height: 80px;
    .left_column {
      .wh(32%, 100%);
      float: left;
      em {
        font-size: 16px;
      }
