    span {
      .wh(16px, 16px);
      background: url("../../../image/mainSet/yuandefault.png");
      background-size: cover;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      right: 36px;
      margin-top: -8px;
    }
    .clickActive {
      background: url("../../../image/mainSet/yuanclick.png");
      background-size: cover;
    }
  }
}

</style>



