<script>
// import Bscroll from 'better-scroll'
// import scroll from '../components/BScroll'
import { mapState, mapMutations } from 'vuex'
import { isLoginRequest, deviceListRequest, logoutUserRequest, wifiPowerSwitchRequest, unbundlingDeviceRequest, systemLanguageRequest, disableGoBackRequest } from '../service/method'
import { isLoginResponse, deviceListResponse, logoutUserResponse, unbundlingDeviceResponse, wifiPowerSwitchResponse, setSystemLanguageResponse, systemLanguageResponse, temperatureAndHumidityDataResponse, networkStatusResponse } from '../service/getData.js'
import { } from 'mint-ui'
import messageBox from '../components/common/messageBox'
import actionSheet from '../components/common/actionSheet'
import Slider from '../components/Slider'
import toast from '../components/common/toast'

export default {
  data () {
    return {
      scroll: null, // 普通模式滚动
      foot_active: 1, // 底部导航
      currentItem: null, // 当前选择项
      delIcon: require('../image/home/del.png'),
      password: '', // 下拉
      topTip: 1, // 1、下拉刷新   2、释放立即刷新
      deviceList: [], // 设备列表
      pullRefreshObj: {
        pulldown: true, // 下拉刷新
        finishPullDown: true, // 下拉刷新完成
        pullup: false, // 上拉加载
        finishPullUp: true, // 上拉加载完成
        noMoreData: false // 没有更多数据
      },
      listenScroll: true // 滚动监听
    }
  },

  mounted () {
    // 网络状态返回
    networkStatusResponse(this.NAVIGATOR, this)

    // 查询系统语言返回
    systemLanguageResponse(this.NAVIGATOR, this)

    // 获取设备列表返回
    deviceListResponse(this.NAVIGATOR, this, 'home')

    // 验证用户是否登录返回
    isLoginResponse(this.NAVIGATOR, this)

    // 插座电源开关状态返回
    wifiPowerSwitchResponse(this.NAVIGATOR, this, 'home')

    // 解绑设备返回
    unbundlingDeviceResponse(this.NAVIGATOR, this)

    // 设置系统语言返回
    setSystemLanguageResponse(this.NAVIGATOR, this)

    // 注销用户返回
    logoutUserResponse(this.NAVIGATOR, this)

    // 温湿度页面数据
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'home')

    // 首次启动
    if (this.firstRunning) {
      this._initInterface()
      this.$store.state.firstRunning = false
    }

    if (this.$store.state.LoginState) {
      // 获取设备列表
      deviceListRequest(this.NAVIGATOR, this)
    }

    if (this.$root.ISDEBUG) {
      console.log(this.currentRouter)
      // this.MESSAGEBOX_SWITCH({type: 'inputPasswordOpreation'})
      // 登录状态
      window.isLoginResponse(true)
      // console.log(this.$store.state.LoginState)
      if (this.$store.state.LoginState) {
        window.deviceListResponse('[{"name":"卧室空调的插座00","state":true,"switch":true,"mac":"00:00:00:00:00","wifiInfomation":{"ssid":"PHICOMM_startai","strength":0}},{"name":"卧室空调的插座01","state":true,"switch":true,"mac":"00:00:00:00:01","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-95}},{"name":"卧室空调的插座02","state":false,"switch":true,"mac":"00:00:00:00:02","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-70}},{"name":"卧室空调的插座03","state":false,"switch":true,"mac":"00:00:00:00:03","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-72}},{"name":"卧室空调的插座04","state":true,"switch":true,"mac":"00:00:00:00:04","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-100}}]')
        console.log(this.deviceList)
      }

      // setTimeout(() => {
        // window.wifiPowerSwitchResponse('00:00:00:00:02', false)
        // 登出
        // window.logoutUserResponse(true)
        // 网络
        // window.networkStatusResponse('', false, '')
        // 解绑
        // window.unbundlingDeviceResponse(true, this.currentDevice.mac)
      // }, 3000)
    }

    // this.$nextTick(() => {
    //   this.scroll = new Bscroll(this.$refs.warpper, {
    //     click: true,
    //     bounceTime: 700
    //   })
    // })
  },

  components: {
    messageBox,
    actionSheet,
    Slider,
    toast
  },

  computed: {
    ...mapState([
      'messageBox', 'messageBoxType', 'actionSheet', 'actionSheetType', 'userInfo', 'currentDevice', 'currentRouter', 'LoginState', 'ToastType', 'firstRunning'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH', 'TABLE_SWITCH'
    ]),

    _initInterface () {
      // 查询系统语言
      systemLanguageRequest(this.NAVIGATOR, this)

      // 检验是否登录
      isLoginRequest(this.NAVIGATOR, this)
    },

    listNav (num) {
      // this.$router.push({path: '/home/shop'})
      // 禁用原生返回
      disableGoBackRequest(this.NAVIGATOR, this, false)
    },

    // 下拉刷新
    refreshData () {
      console.log('refreshData')
      // 获取设备列表
      deviceListRequest(this.NAVIGATOR, this)

      if (this.$root.ISDEBUG) {
        setTimeout(() => {
          window.deviceListResponse('[{"name":"卧室空调的插座00","state":true,"switch":true,"mac":"00:00:00:00:00","wifiInfomation":{"ssid":"PHICOMM_startai","strength":0}}]')
        }, 3000)
      }
    },

    // 上拉加载
    loadData () {
      // console.log('loadData')
      // setTimeout(() => {
      //   console.log('finish')
      //   this.pullRefreshObj.finishPullUp = true
      //   this.pullRefreshObj.noMoreData = true
      // }, 10000)
    },

    // 初始化平移位置
    scrollHandle () {
      let listItems = document.querySelectorAll('.list-item')

      for (let i = 0; i < listItems.length; i++) {
        listItems[i].dataset.type = 0
      }
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          // 解绑设备返回
          unbundlingDeviceRequest(this.NAVIGATOR, this, this.currentItem.mac)
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
      }
    },

    sheetItem (num) {
      switch (num) {
        case 0:
          break
        case 1:
          this.$router.push({name: 'adddirver'})
          this.$store.state.findPage = false
          break
        case 2:
          this.$router.push({name: 'choose'})
          this.$store.state.findPage = true
          break
      }
      this.TABLE_SWITCH({type: this.actionSheetType})
    },

    add () {
      // console.log(this.$store.state.LoginState)
      if (this.$store.state.LoginState) {
        this.TABLE_SWITCH({type: 'homeSheet', flag: true})
      } else {
        this.$router.push({path: '/login'})
      }
    },

    goDevice (item) {
      console.log(item)
      this.$router.push({path: '/main'})
      this.$store.state.currentDevice = item
    },

    deleteDevice (item) {
      console.log('deleteDevice')
      this.currentItem = item
      this.MESSAGEBOX_SWITCH({type: 'deleteEquipmentOpreation'})
    },

    // 注销
    logout () {
      logoutUserRequest(this.NAVIGATOR, this)
    },

    chooseNav (num) {
      this.foot_active = num
      switch (num) {
        case 1:
          break
        case 2:
          break
        case 3:
          break
      }
    },

    // WIFI信号强度
    wifiStrength (item) {
      let array = [-25, -50, -75, -100]
      if (item !== undefined && item.wifiInfomation !== undefined) {
        for (let i = 0; i < array.length; i++) {
          if (item.wifiInfomation.strength >= array[i] && item.wifiInfomation.strength <= 0) {
            return i + 1
          }
        }
        return 0
      }
    },

    // 插座开关切换
    socketSwitch (item) {
      // 插座电源开关控制
      wifiPowerSwitchRequest(this.NAVIGATOR, this, item.mac, !item.switch)

      if (this.$root.ISDEBUG) {
        item.switch = !item.switch
      }
    }
  },

  watch: {
    currentRouter: function (newValue) {
      if (newValue === '/home') {
        // 绑定设备后跳转主页请求数据
        if (this.$route.params.binding) {
          // 获取设备列表
          deviceListRequest(this.NAVIGATOR, this)
        }
      }
    }
  }
}
</script>

