                .wh-f(38%, 100%);
                text-align: left;
                font-size: 14px;
              }
              .column-describe{
                .wh-f(38%, 100%);
                text-align: right;
                font-size: 13px;
                color: #c5c5c5;
                .text-over;
              }
              .column-after{
                .wh-f(10%, 100%);
                position: relative;
                img{
                  width: 7px;
                  .f-center;
                }
              }
            }
          }
        }
      }
    }

    @userColumn: 110px;
    @userColumnList: 50px;
    .user{
      .fs;
      text-align: center;
      color: #2c3e50;
      .main-content{
        .wh(100%, 100%);
        background: #fff;
        .user-msg{
          .wh(100%, 225px);
          color: #fff;
          background: url(../image/user/bg.png) 0 0 no-repeat;
          background-size: cover;
          .item:nth-child(1){
            font-size: 18px;
            height: 40px;
            line-height: 56px;
          }
          .item:nth-child(2){
            .wh(61px, 61px);
            margin: 0 auto;
            border-radius: 50%;
            background: #fff;
            margin-top: 25px;
            position: relative;
            span{
              .wh(56px, 56px);
              border-radius: 50%;
              background: @themeColor;
              .f-center;
              overflow: hidden;
              top: 49%;
              img{
                height: 100%;
              }
            }
          }
          .item:nth-child(3){
            height: 18px;
            font-size: 16px;
            margin-top: 12px;
          }
          .item:nth-child(4){
            height: 20px;
            font-size: 12px;
            .zoom-font(0.8);
            letter-spacing: 2px;
            margin-right: -96px; 
            color: #DBEDFF;
          }
          .item:nth-child(5){
             font-size: 12px;
             margin-top: 8px;
            span{
              border-radius: 2px;
              margin: 0 auto;
              text-align: left;
              background: #FFF;
              color: @themeColor;
              position: relative;
              padding: 4px 20px 4px 8px;
              img{
                height: 12px;
                position: absolute;
                top: 6px;
                right: 5px;
              }
            }
          }
        }
        .user-setup{
          width: 100%;
          position: relative;
          margin-top: 50px;
          li{
            .wh-l(100%, @userColumnList);
            background: #FFF;
            position: relative;
            a{
              display: block;
              .wh(100%, 100%);
              .setup-text{
                .wh-f(90%, 100%);
                text-align: left;
                font-size: 16px;
                strong{
                  .wh-f(15%, 100%);
                  display: block;
                  position: relative;
                  img{
                    width: 24px; /* no */
                    .f-center;
                  }
                }
                span{
                  .wh-f(70%, 100%);
                  font-size: 14px;
                  display: block;
                  padding-left: 12px;
                }
              }
              .setup-after{
                .wh-f(10%, 100%);
                position: relative;
                img{
                  width: 7px;
                  .f-center;
                }
              }
            }
          }
        }
        .login_out{
          .wh(100%, 50px);
          line-height: 50px;
          margin-top: 20px; // 10px 会挡住顶部一像素边框
          font-size: 14px;
          position: relative;
          a{
            .wh(100%, 50px);
            display: block;
          }
        }
      }
    }    
  }
  .footer{
    .wh(100%, 50px);
    position: absolute;
    background: #fff;
    left: 0;
    bottom: 0;
    text-align: center;
    font-size: 13px;
    .foot-left, .foot-center, .foot-right{
      float: left;
      a{
        display: block;
        .wh(100%, 100%);
        .foot-col-icon{
          .wh(100%, 60%);
          padding-top: 8px;
          img{
            height: 20px;
          }
        }
        .foot-col-text{
          .wh(100%, 40%);
          line-height: 14px;
          font-size: 12px;
        }
        &:active {
          background: transparent;
        }
      }
    }
    .foot-left{
      .wh(100%/3, 50px);
    }
    .foot-center{
      .wh(100%/3, 50px);
      img{
        height: 22px !important; /* no */
        margin-top: -2px;
      }
    }
    .foot-right{
      .wh(100%/3, 50px);      
    }

    .foot-active{
      color: @themeColor;
    }
  }
  .home_sheet{
    .wh(100%, 100%);
    li{
      .wh-l(100%, 50px);
      position: relative;
      a{
        .wh(100%, 100%);
        font-size: 14px;
        display: block;
      }
    }
  }
}

</style>



